import Vue from 'vue';
// import vuescroll from 'vuescroll';
import vuescroll from 'vuescroll/dist/vuescroll-native';
import 'vuescroll/dist/vuescroll.css';
Vue.use(vuescroll, {
  ops: {
    scrollPanel:{
      scrollingX: true,
      scrollingY: true,
    },
    bar: {
      background: '#B7BECE',//#ccc
      keepShow:false,
      size: '4px'
    },
    rail:{
      background:'#f1f1f1',//
      opacity: 1,
      size: '4px'
    }
  },
  name: 'vueScroll' // vueScroll
});


