<!--  -->
<template>
  <div class="container">
    <Row type="flex" align="middle">
      <Col span="24">
        <div
          class="tool-item-container"
          v-for="item in tools"
          :key="item.action"
          @click="handleClick(item)"
        >
          <!-- <Tooltip placement="bottom" :content="item.title">
            <i class="iconfont" :class="[item.className,item.isSelect?'tool-item-select':'']"></i>
          </Tooltip>-->
          <i
            class="iconfont"
            :title="item.title"
            :class="[item.className,item.isSelect?'tool-item-select':'']"
            style="font-size:20px;font-size:0.2rem;"
          ></i>
          <!-- <span :class="[item.isSelect?'tool-item-select':'']">{{item.title}}</span> -->
        </div>
        <!---->
        <div
          class="tool-item-container"
          v-for="item in editTools"
          :key="item.action"
          @click="handleClick(item)"
        >
          <!-- <Tooltip placement="bottom" :content="item.title">
            <i class="se-font" :class="[item.className,item.isSelect?'tool-item-select':'']"></i>
          </Tooltip>-->
          <i
            class="se-font iconfont"
            :title="item.title"
            :class="[item.className,item.isSelect?'tool-item-select':'']"
            style="font-size:20px;"
          ></i>
        </div>
      </Col>
    </Row>
    <Dialog :title="title" :visible="visible" @cus_cancel="handleClose" @cus_ok="handleOk">
      <Row style="padding:6px ; font-size:14px;">
        <Col span="18" style="color:red; ">{{msg}}</Col>
      </Row>
    </Dialog>
  </div>
</template>

<script>
import { errorMsg, infoMsg } from "../utils/message";
import { actionType } from "../const/enum";
import { mapState } from "vuex";
import Dialog from "./Dialog";
export default {
  name: "MapTool",
  components: {
    Dialog
  },
  data() {
    //
    return {
      visible: false,
      title: this.$t("dialog.confirm") || "",
      msg: this.$t("dialog.deleteFeaturesMsg"),
      tools: [
        {
          action: actionType.PAN,
          isSelect: true,
          className: "iconpingyi",
          title: this.$t("mapTool.pan") || ""
        },
        {
          action: actionType.SELECT_BY_POINT,
          isSelect: false,
          className: "icondianxuan-",
          title: this.$t("mapTool.select") || ""
        },
        {
          action: actionType.SELECT_BY_RECT,
          isSelect: false,
          className: "iconkuangxuan-",
          title: ""
        },
        {
          action: actionType.SELECT_BY_CIRCLE,
          isSelect: false,
          className: "iconquanxuan-",
          title: ""
        },
        {
          action: actionType.SELECT_BY_POLYGON,
          isSelect: false,
          className: "iconduobianxingxuan-",
          title: ""
        },
        {
          action: actionType.IDENTIFY_EXT,
          isSelect: false,
          className: "iconshibie-1",
          title: this.$t("mapTool.identify") || ""
        },
        {
          action: actionType.VIEW_NODE,
          isSelect: false,
          className: "iconchakanzuobiao",
          title: ""
        },
        {
          action: actionType.MEASURE_DISTANCE,
          isSelect: false,
          className: "iconceju-",
          title: this.$t("mapTool.distance") || ""
        },
        {
          action: actionType.MEASURE_AREA,
          isSelect: false,
          className: "iconcemian-",
          title: this.$t("mapTool.area") || ""
        },
        {
          action: actionType.CLEAR,
          isSelect: false,
          className: "iconqingchu-",
          title: this.$t("mapTool.clear") || ""
        }
      ],
      editTools: [],
      mTooltip: null
    };
  },
  // data
  computed: {
    ...mapState(["map"])
  },
  mounted() {
    this.$nextTick(() => {
      this.setDefaultState();
    });
  },
  //
  methods: {
    /***
     * @description 
     */
    handleClose() {
      this.visible = false;
    },
    /***
     * @description 
     */
    handleOk() {
      let editManager = this.map.editManager();
      let features = editManager && editManager.getCurrentEditFeatures();
      if (features && features.length > 0) {
        this.visible = true;
        let bRet = this.map.deleteFeatures(features);
        if (!bRet) {
          errorMsg(this.$t("message.fail"));
        }
      } else {
        errorMsg(this.$t("message.noEditFeature"));
      }
      this.visible = false;
    },
    /****
     * @description 
     */
    initToolState() {
      this.tools.forEach(item => {
        item.isSelect = false;
      });
      this.editTools &&
        this.editTools.forEach(item => {
          item.isSelect = false;
        });
    },
    /****
     * @description 
     */
    setToolState(action, isSelect) {
      this.tools.forEach(item => {
        if (item.action === action) {
          item.isSelect = isSelect;
        } else {
          item.isSelect = false;
        }
      });
      this.editTools &&
        this.editTools.forEach(item => {
          if (item.action === action) {
            item.isSelect = isSelect;
          } else {
            item.isSelect = false;
          }
        });
    },
    setDefaultState() {
      this.tools.forEach((item, index) => {
        if (index == 0) {
          item.isSelect = true;
        } else {
          item.isSelect = false;
        }
      });
      this.editTools &&
        this.editTools.forEach(item => {
          item.isSelect = false;
        });
    },
    /****
     * @description 
     * @param{Object}
     */
    handleClick(item) {
      let seManager =
        this.map && this.map.seManager ? this.map.seManager : null;
      if (!seManager) return false;
      let bSetSuccess = false;
      switch (item.action) {
        case actionType.PAN: {
          seManager.setAction(L.QGIS.SeAction.PAN);
          item.isSelect = true;
          bSetSuccess = true;
          break;
        }
        case actionType.SELECT_BY_POINT: {
          seManager.setAction(L.QGIS.SeAction.SELECT_BY_POINT);
          item.isSelect = true;
          bSetSuccess = true;
          break;
        }
        case actionType.SELECT_BY_RECT: {
          seManager.setAction(L.QGIS.SeAction.SELECT_BY_RECT);
          item.isSelect = true;
          bSetSuccess = true;
          break;
        }
        case actionType.SELECT_BY_CIRCLE: {
          seManager.setAction(L.QGIS.SeAction.SELECT_BY_CIRCLE);
          item.isSelect = true;
          bSetSuccess = true;
          break;
        }
        case actionType.SELECT_BY_POLYGON: {
          seManager.setAction(L.QGIS.SeAction.SELECT_BY_POLYGON);
          item.isSelect = true;
          bSetSuccess = true;
          break;
        }
        case actionType.IDENTIFY_EXT: {
          seManager.setAction(L.QGIS.SeAction.IDENTIFY_EXT);
          item.isSelect = true;
          bSetSuccess = true;
          break;
        }
        case actionType.VIEW_NODE: {
          //
          seManager.setAction(L.QGIS.SeAction.VIEW_NODE);
          if (seManager.getAction() === L.QGIS.SeAction.VIEW_NODE) {
            item.isSelect = true;
            bSetSuccess = true;
          }
          break;
        }
        case actionType.MEASURE_DISTANCE: {
          seManager.clearMeasure();
          seManager.setAction(L.QGIS.SeAction.MEASURE_DISTANCE);
          item.isSelect = true;
          bSetSuccess = true;
          break;
        }
        case actionType.MEASURE_AREA: {
          seManager.clearMeasure();
          seManager.setAction(L.QGIS.SeAction.MEASURE_AREA);
          item.isSelect = true;
          bSetSuccess = true;
          break;
        }
        case actionType.CLEAR: {
          seManager.clearSelection();
          seManager.clearMeasure();
          seManager.setAction(L.QGIS.SeAction.PAN);
          this.setDefaultState();
          break;
        }
        //
        case actionType.CREATE_POINT: {
          seManager.setAction(L.QGIS.SeAction.CREATE_POINT);
          if (seManager.getAction() === L.QGIS.SeAction.CREATE_POINT) {
            item.isSelect = true;
            bSetSuccess = true;
          }
          break;
        }
        case actionType.CREATE_POLYLINE: {
          seManager.setAction(L.QGIS.SeAction.CREATE_POLYLINE);
          if (seManager.getAction() === L.QGIS.SeAction.CREATE_POLYLINE) {
            item.isSelect = true;
            bSetSuccess = true;
          }
          break;
        }
        case actionType.CREATE_POLYGON: {
          seManager.setAction(L.QGIS.SeAction.CREATE_POLYGON);
          if (seManager.getAction() === L.QGIS.SeAction.CREATE_POLYGON) {
            item.isSelect = true;
            bSetSuccess = true;
          }
          break;
        }
        case actionType.MOVE: {
          seManager.setAction(L.QGIS.SeAction.MOVE);
          if (seManager.getAction() === L.QGIS.SeAction.MOVE) {
            item.isSelect = true;
            bSetSuccess = true;
          }
          break;
        }
        case actionType.ROTATE: {
          seManager.setAction(L.QGIS.SeAction.ROTATE);
          if (seManager.getAction() === L.QGIS.SeAction.ROTATE) {
            item.isSelect = true;
            bSetSuccess = true;
          }
          break;
        }
        case actionType.ADD_NODE: {
          seManager.setAction(L.QGIS.SeAction.ADD_NODE);
          if (seManager.getAction() === L.QGIS.SeAction.ADD_NODE) {
            item.isSelect = true;
            bSetSuccess = true;
          }
          break;
        }
        case actionType.EDIT_NODE: {
          seManager.setAction(L.QGIS.SeAction.EDIT_NODE);
          if (seManager.getAction() === L.QGIS.SeAction.EDIT_NODE) {
            item.isSelect = true;
            bSetSuccess = true;
          }
          break;
        }
        case actionType.DELETE_NODE: {
          seManager.setAction(L.QGIS.SeAction.DELETE_NODE);
          if (seManager.getAction() === L.QGIS.SeAction.DELETE_NODE) {
            item.isSelect = true;
            bSetSuccess = true;
          }
          break;
        }
        case actionType.CLIP_LINE: {
          seManager.setAction(L.QGIS.SeAction.CLIP_LINE);
          if (seManager.getAction() === L.QGIS.SeAction.CLIP_LINE) {
            item.isSelect = true;
            bSetSuccess = true;
          }
          break;
        }
        case actionType.CLIP_POLYGON: {
          seManager.setAction(L.QGIS.SeAction.CLIP_POLYGON);
          if (seManager.getAction() === L.QGIS.SeAction.CLIP_POLYGON) {
            item.isSelect = true;
            bSetSuccess = true;
          }
          break;
        }
        case actionType.GRAP_POLYGON: {
          //
          let selections = this.map.getSelection();
          let selection = null;
          if (selections && selections.length > 0) {
            selection = selections[0];
          }
          if (
            selection &&
            selection.getCount() == 2 &&
            this.map.getEditLayer() &&
            selection.getLayerId() == this.map.getEditLayer().getID()
          ) {
            let editLayer = this.map.getEditLayer();
            let dt = editLayer.getDataset();
            let dtID = dt && dt.getID();
            let dataset = this.map.findDatasetById(dtID);
            if (dataset) {
              let feature1 = dataset.getFeatureById(selection.getIds()[0]);
              let feature2 = dataset.getFeatureById(selection.getIds()[1]);
              if (feature1 && feature2) {
                let mapzone = this.getMapZone(dataset.getProjInfo());
                feature2 = feature2.clone();
                let bRet = feature1.clone().eliminateGaps(feature2, mapzone);
                if (bRet) {
                  this.map.modifyFeatures([feature2.clone()]);
                } else {
                  errorMsg("!");
                }
              }
            }
          } else {
            errorMsg("!");
          }
          break;
        }
        case actionType.UNION_FEATURE: {
          //
          let selections = this.map.getSelection();
          let selection = null;
          if (selections && selections.length > 0) {
            selection = selections[0];
          }
          if (
            selection &&
            selection.getCount() >= 2 &&
            this.map.getEditLayer() &&
            selection.getLayerId() == this.map.getEditLayer().getID()
          ) {
            let editLayer = this.map.getEditLayer();
            let dt = editLayer.getDataset();
            let dtID = dt && dt.getID();
            let dataset = this.map.findDatasetById(dtID);
            if (dataset) {
              let features = dataset.getFeaturesByIds(selection.getIds());
              let bRet = this.map.combineFeatures(dtID, features);
              if (!bRet) {
                errorMsg("!");
              }
            }
          } else {
            errorMsg("!");
          }
          break;
        }
        case actionType.DELETE: {
          let editManager = this.map.editManager();
          let features = editManager && editManager.getCurrentEditFeatures();
          if (features && features.length > 0) {
            this.visible = true;
          } else {
            errorMsg(this.$t("message.noEditFeature"));
          }
          break;
        }
        case actionType.MODIFYATTR: {
          //
          let editManager = this.map.editManager();
          let features = editManager && editManager.getCurrentEditFeatures();
          if (features && features.length == 0) {
            errorMsg(this.$t("message.noEditFeature"));
          } else if (features && features.length > 1) {
            //
            let editLayer = this.map.getEditLayer();
            if (editLayer) {
              let data = {
                dataset_id: editLayer.getDataset().getID(),
                name: editLayer.getName(),
                type: 3,
                features: [...features],
                layer_id: editLayer.getID()
              };
              this.map.fire("edit_feature_attr", { data });
            }
          } else if (features && features.length == 1) {
            let editLayer = this.map.getEditLayer();
            if (editLayer) {
              let data = {
                dataset_id: editLayer.getDataset().getID(),
                name: editLayer.getName(),
                type: 2,
                feature: features[0].clone(),
                layer_id: editLayer.getID()
              };
              this.map.fire("edit_feature_attr", { data });
            }
          } else {
            errorMsg(this.$t("message.noEditFeature"));
          }
          break;
        }
        case actionType.FINISHEDIT: {
          //
          let editManager = this.map.editManager();
          editManager && editManager.finishEdit();
          seManager.setAction(L.QGIS.SeAction.PAN);
          this.setDefaultState();
          this.map.fire("edit-state", { isUndo: false, isRedo: false });
          this.editTools = [];
          this.map.fire("edit-finish");
          infoMsg("");
          break;
        }
      }
      if (bSetSuccess) {
        this.setToolState(item.action, true);
      }
    },
    getMapZone(projInfo, tolerance) {
      tolerance = tolerance || 4;
      const center = this.map.getCenter();
      const p1 = this.map.latLngToLayerPoint(center);
      const p2 = new L.Point(p1.x, p1.y + tolerance);
      let pp1 = this.map.layerPointToLatLng(p1);
      let pp2 = this.map.layerPointToLatLng(p2);
      if (projInfo) {
        pp1 = projInfo.project(pp1);
        pp2 = projInfo.project(pp2);
      } else {
        pp1 = new L.Point(pp1.lng, pp1.lat);
        pp2 = new L.Point(pp2.lng, pp2.lat);
      }
      const mapZone = Math.sqrt(
        Math.pow(pp1.x - pp2.x, 2) + Math.pow(pp1.y - pp2.y, 2)
      );
      return mapZone;
    },
    /***
     * @description 
     */
    handleStartEditLayer(e) {
      let data = e.data;
      this.editTools = [];
      if (data) {
        switch (data.type) {
          case "point":
          case L.QGIS.DatasetType.POINT:
          case L.QGIS.CloudDatasetType.POINT: {
            this.editTools.push(
              {
                action: actionType.CREATE_POINT,
                isSelect: false,
                className: "icon-drawPoint",
                title: this.$t("common.placeholder.drawPoint") || ""
              },
              {
                action: actionType.MOVE,
                isSelect: false,
                className: "icon-move",
                title: this.$t("common.placeholder.moveFeature") || ""
              },
              {
                action: actionType.UNION_FEATURE,
                isSelect: false,
                className: "iconhebingicon",
                title: ""
              },
              {
                action: actionType.DELETE,
                isSelect: false,
                className: "icon-deleteGraphic",
                title: this.$t("common.placeholder.deleteFeature") || ""
              },
              {
                action: actionType.MODIFYATTR,
                isSelect: false,
                className: "icon-editInfo",
                title: this.$t("common.placeholder.modifyAttr") || ""
              },
              {
                action: actionType.FINISHEDIT,
                isSelect: false,
                className: "icon-finishEdit",
                title: this.$t("common.placeholder.finishEdit") || ""
              }
            );
            break;
          }
          case "line":
          case L.QGIS.DatasetType.LINE:
          case L.QGIS.CloudDatasetType.LINE: {
            this.editTools.push(
              {
                action: actionType.CREATE_POLYLINE,
                isSelect: false,
                className: "icon-drawLine",
                title: this.$t("common.placeholder.drawLine") || ""
              },
              {
                action: actionType.MOVE,
                isSelect: false,
                className: "icon-move",
                title: this.$t("common.placeholder.moveFeature") || ""
              },
              {
                action: actionType.ROTATE,
                isSelect: false,
                className: "icon-rotate",
                title: this.$t("common.placeholder.rotateFeature") || ""
              },
              {
                action: actionType.ADD_NODE,
                isSelect: false,
                className: "icon-addGraphic",
                title: this.$t("common.placeholder.addNode") || ""
              },
              {
                action: actionType.EDIT_NODE,
                isSelect: false,
                className: "icon-editGraphic",
                title: this.$t("common.placeholder.editNode") || ""
              },
              {
                action: actionType.DELETE_NODE,
                isSelect: false,
                className: "iconshanchujiedian",
                title: ""
              },
              {
                action: actionType.CLIP_LINE,
                isSelect: false,
                className: "clipLine",
                title: this.$t("common.placeholder.clipLine") || ""
              },
              {
                action: actionType.UNION_FEATURE,
                isSelect: false,
                className: "iconhebingicon",
                title: ""
              },
              {
                action: actionType.DELETE,
                isSelect: false,
                className: "icon-deleteGraphic",
                title: this.$t("common.placeholder.deleteFeature") || ""
              },
              {
                action: actionType.MODIFYATTR,
                isSelect: false,
                className: "icon-editInfo",
                title: this.$t("common.placeholder.modifyAttr") || ""
              },
              {
                action: actionType.FINISHEDIT,
                isSelect: false,
                className: "icon-finishEdit",
                title: this.$t("common.placeholder.finishEdit") || ""
              }
            );
            break;
          }
          case "polygon":
          case L.QGIS.DatasetType.POLYGON:
          case L.QGIS.CloudDatasetType.POLYGON: {
            this.editTools.push(
              {
                action: actionType.CREATE_POLYGON,
                isSelect: false,
                className: "icon-drawPolygon",
                title: this.$t("common.placeholder.drawPolygon") || ""
              },
              {
                action: actionType.MOVE,
                isSelect: false,
                className: "icon-move",
                title: this.$t("common.placeholder.moveFeature") || ""
              },
              {
                action: actionType.ROTATE,
                isSelect: false,
                className: "icon-rotate",
                title: this.$t("common.placeholder.rotateFeature") || ""
              },
              {
                action: actionType.ADD_NODE,
                isSelect: false,
                className: "icon-addGraphic",
                title: this.$t("common.placeholder.addNode") || ""
              },
              {
                action: actionType.EDIT_NODE,
                isSelect: false,
                className: "icon-editGraphic",
                title: this.$t("common.placeholder.editNode") || ""
              },
              {
                action: actionType.DELETE_NODE,
                isSelect: false,
                className: "iconshanchujiedian",
                title: ""
              },
              {
                action: actionType.CLIP_POLYGON,
                isSelect: false,
                className: "clipPolygon",
                title: this.$t("common.placeholder.clipPolygon") || ""
              },
              {
                action: actionType.GRAP_POLYGON,
                isSelect: false,
                className: "iconxiaochujianju",
                title: ""
              },
              {
                action: actionType.UNION_FEATURE,
                isSelect: false,
                className: "iconhebingicon",
                title: ""
              },
              {
                action: actionType.DELETE,
                isSelect: false,
                className: "icon-deleteGraphic",
                title: this.$t("common.placeholder.deleteFeature") || ""
              },
              {
                action: actionType.MODIFYATTR,
                isSelect: false,
                className: "icon-editInfo",
                title: this.$t("common.placeholder.modifyAttr") || ""
              },
              {
                action: actionType.FINISHEDIT,
                isSelect: false,
                className: "icon-finishEdit",
                title: this.$t("common.placeholder.finishEdit") || ""
              }
            );
            break;
          }
        }
      }
      this.map.setAction(L.QGIS.SeAction.PAN);
      this.setDefaultState();
    },
    /***
     * @description 
     */
    handleEditFinsh(e) {
      let editManager = this.map.editManager();
      editManager && editManager.finishEdit();
      let seManager =
        this.map && this.map.seManager ? this.map.seManager : null;
      seManager && seManager.setAction(L.QGIS.SeAction.PAN);
      this.setDefaultState();
      this.editTools = [];
    },
    /****
     * @description 
     */
    handleAddFeatures(e) {
      //
      if (e.features && e.features.length == 1) {
        let editLayer = this.map.getEditLayer();
        if (editLayer) {
          let data = {
            dataset_id: editLayer.getDataset().getID(),
            name: editLayer.getName(),
            type: 1,
            feature: e.features[0]
          };
          this.map.fire("draw_end_new", { data });
        }
      }
    },
    /****
     * @description 
     */
    handleModifyFeatures(e) {
      if (e.features) {
        let bRet = this.map.modifyFeatures(e.features);
        if (!bRet) {
          errorMsg(this.$t("message.fail"));
        }
      }
    },
    /****
     * @description 
     */
    handleEditTip(e) {
      if (e.tip) {
        errorMsg(e.tip);
      }
    },
    handleViewNodeCoord(e) {
      console.log(e);
      let { data, latlng } = e;
      if (data && latlng) {
        let content = `<div class="edit-tooltip-containter">
                   <p>${data.pointIndex + 1}</p>
                   <p>X${data.snapPoint.x}</p>
                   <p>Y${data.snapPoint.y}</p>
                   </div>`;
        if (!this.mTooltip) {
          this.mTooltip = new L.Tooltip({ direction: "top" });
        }
        this.mTooltip.setContent(content);
        this.map.openTooltip(this.mTooltip, latlng);
      } else {
        if (this.mTooltip) {
          this.map.closeTooltip(this.mTooltip);
        }
      }
    }
  },
  watch: {
    map() {
      if (this.map) {
        this.map.off("edit-finish", this.handleEditFinsh);
        this.map.on("edit-finish", this.handleEditFinsh);
        this.map.off("start_edit", this.handleStartEditLayer);
        this.map.on("start_edit", this.handleStartEditLayer);
        this.map.off("add_features", this.handleAddFeatures);
        this.map.on("add_features", this.handleAddFeatures);
        this.map.off("modify_features", this.handleModifyFeatures);
        this.map.on("modify_features", this.handleModifyFeatures);
        this.map.off("view-node-coord", this.handleViewNodeCoord);
        this.map.on("view-node-coord", this.handleViewNodeCoord);
        // this.map.off("delete_features", this.handleDeleteFeatures);
        // this.map.on("delete_features", this.handleDeleteFeatures);
        if (this.map.editManager()) {
          this.map.editManager().off("editTip", this.handleEditTip);
          this.map.editManager().on("editTip", this.handleEditTip);
        }
      }
    }
  }
};
</script>
<style lang='less' scoped>
//@import url(); css
.container {
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 2px 4px 0px rgba(51, 80, 110, 0.2);
}
.tool-item-container {
  float: left;
  padding: 0px 4px;
  height: 36px;
  height: 0.36rem;
  display: flex;
  justify-content: center;
  align-items: middle;
  flex-direction: column;
  font-size: 12px;
  font-weight: 400;
  color: rgba(16, 36, 60, 1);
  text-align: center;
  cursor: pointer;
}
.tool-item-container i {
  color: rgba(16, 36, 60, 1);
  font-size: 16px;
  font-weight: 500;
}
.tool-item-container span {
  font-size: 14px;
  color: rgba(16, 36, 60, 0.6);
}

.tool-item-select {
  color: #43aaf5 !important;
}
.se-font {
  // padding: 0 4px;
  font-size: 20px !important;
  font-size: 0.2rem !important;
  // min-width: 20px;
  display: inline-block;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1;
  vertical-align: bottom;
}
</style>


