/***
 * @description 
 * @param {String} val 
 * @param {Number} len 
 * @returns {Boolean} true
 */
function isMoreThanLength(val, len) {
  if (val == null) {
    val = "";
  }
  val = val.toString().trim();
  len = len || 64;
  if (len <= 0) {
    len = 64;
  }
  if (val.length > len) {
    return true;
  } else {
    return false;
  }
}
