function isEmpty(obj) {
  if (obj === undefined || obj == null) {
    return true;
  }
  if (obj.trim() == "") {
    return true;
  } else {
    return false;
  }
}
/***
 * @description 
 * @param{String} val
 * @returns{Number|undefined} 
 */
function parseNum(val) {
  if (isValidDecimal(val) || isValidInteger(val)) {
    return Number(val);
  } else {
    return null;
  }
}
