/***
 * @description ID
 * @returns{Array} ID
 */
function getDatasets() {
  if (
    process &&
    process.env &&
    process.env.NODE_ENV &&
    process.env.NODE_ENV === "development"
  ) {
    return ["fa1ac125-eca5-457a-9a89-98d2f04efffa"];
  } else {
    let temps = localStorage.getItem("datasets");
    if (temps == null || temps == undefined) {
      return [];
    } else {
      return JSON.parse(temps);
    }
  }
}
function createFieldsFromFieldsInfo(fieldInfos) {
  let fields = [];
  fieldInfos &&
    fieldInfos.forEach((item) => {
      let tempType = "";
      switch (item.getType()) {
        case L.SE.DataType.DB_STRING:
        case dataType.STRING: {
          tempType = dataType.STRING;
          break;
        }
        case L.SE.DataType.DB_INTEGER:
        case dataType.BIGINT:
        case dataType.INT: {
          tempType = dataType.INT;
          break;
        }
        case dataType.FLOAT: {
          tempType = dataType.FLOAT;
          break;
        }
        case L.SE.DataType.DB_DOUBLE:
        case dataType.DOUBLE: {
          tempType = dataType.DOUBLE;
          break;
        }
        case L.SE.DataType.DB_DATETIME:
        case dataType.DATETIME: {
          tempType = dataType.DATETIME;
          break;
        }
        case L.SE.DataType.DB_DATE:
        case dataType.DATE: {
          tempType = dataType.DATE;
          break;
        }
        case L.SE.DataType.DB_TIME:
        case dataType.TIME: {
          tempType = dataType.TIME;
          break;
        }
        case L.SE.DataType.DB_BLOB:
        case dataType.BLOB: {
          tempType = dataType.BLOB;
          break;
        }
        case dataType.BOOL: {
          tempType = dataType.BOOL;
          break;
        }
      }
      if (tempType) {
        fields.push({ name: item.getName(), type: tempType });
      }
    });
  return fields;
}
/****
 * @description SeLayer
 * @param {SeLayer} layerSeLayer
 * @param {Boolean} 
 * @returns {Object} 
 */
function createLayerInfo(layer, bEdit) {
  if (!layer || layer.dataset == null) {
    return null;
  }
  let dataset = layer.dataset;
  let dtType = "";
  switch (dataset.type) {
    case 1:
    case datasetType.POINT: {
      dtType = datasetType.POINT;
      break;
    }
    case 2:
    case datasetType.LINE: {
      dtType = datasetType.LINE;
      break;
    }
    case 3:
    case datasetType.POLYGON: {
      dtType = datasetType.POLYGON;
      break;
    }
  }
  if (!dtType) {
    //console.log("");
    return null;
  }
  let fieldInfos = dataset.fieldInfos;
  let fields = [];
  fieldInfos &&
    fieldInfos.forEach((item) => {
      if (!item.isSysField()) {
        let tempType = "";
        switch (item.getType()) {
          case L.QGIS.DataType.DB_STRING:
          case dataType.STRING: {
            tempType = dataType.STRING;
            break;
          }
          case L.QGIS.DataType.DB_INTEGER:
          case dataType.BIGINT:
          case dataType.INT: {
            tempType = dataType.INT;
            break;
          }
          case dataType.FLOAT: {
            tempType = dataType.FLOAT;
            break;
          }
          case L.QGIS.DataType.DB_DOUBLE:
          case dataType.DOUBLE: {
            tempType = dataType.DOUBLE;
            break;
          }
          case L.QGIS.DataType.DB_DATETIME:
          case dataType.DATETIME: {
            tempType = dataType.DATETIME;
            break;
          }
          case L.QGIS.DataType.DB_DATE:
          case dataType.DATE: {
            tempType = dataType.DATE;
            break;
          }
          case L.QGIS.DataType.DB_TIME:
          case dataType.TIME: {
            tempType = dataType.TIME;
            break;
          }
          case L.QGIS.DataType.DB_BLOB:
          case dataType.BLOB: {
            tempType = dataType.BLOB;
            break;
          }
          case dataType.BOOL: {
            tempType = dataType.BOOL;
            break;
          }
        }
        if (tempType) {
          fields.push({ name: item.getName(), type: tempType });
        }
      }
    });
  let renderStyle = layer.renderStyle ? layer.renderStyle : null;
  let renderItem = renderStyle && renderStyle.getDefaultRenderItem();
  let symbol = renderItem && renderItem.getSymbol();
  let symbolColor = symbol && symbol.getColor().hexValue();
  let lineColor = null;
  if (dtType == datasetType.POLYGON) {
    lineColor = symbol.getOutlineSymbol().getColor().hexValue();
  }
  let renderTextStyle = layer.renderTextStyle ? layer.renderTextStyle : null;
  let field =
    renderTextStyle && renderTextStyle.getUsedFields()
      ? renderTextStyle.getUsedFields()[0]
      : "";
  let textRenderItem =
    renderTextStyle && renderTextStyle.getRenderItem()
      ? renderTextStyle.getRenderItem()
      : null;
  let textSymbol =
    textRenderItem && textRenderItem ? textRenderItem.getTextSymbol() : null;
  let label = {
    enable: renderTextStyle ? renderTextStyle.isEnable() : false,
    field: field,
    size: textSymbol ? textSymbol.getFontSize() : 12,
    color: textSymbol ? textSymbol.getColor().hexValue : "#000000",
  };
  let filter = null;
  if (layer.filter && layer.filter.length > 0) {
    if (layer.filter[0] === "all" || layer.filter[0] === "any") {
      filter = { op: layer.filter[0], list: [] };
      for (let i = 1; i < layer.filter.length; i++) {
        if (layer.filter[i].length == 3) {
          filter.list.push({
            name: layer.filter[i][1],
            operator: layer.filter[i][0],
            value: layer.filter[i][2],
          });
        }
      }
    }
  }
  if (!filter) {
    filter = {
      op: "all", //allany
      list: [], //{name:,operator:,value:}
    };
  }
  let obj = {
    num: 0,
    id: layer.id, //ID
    visible: layer.visible,
    type: dtType, //1,2,3
    name: layer.name,
    dataset_id: dataset.id, //ID
    editable: bEdit === undefined ? false : bEdit, //
    fields: fields, //,{name:,type:}
    symbol: {
      color: symbolColor, //
    },
    label: label,
    filter: filter,
    isSetting: false, //
    opAction: 0, //0,1,2
  };
  if (lineColor) {
    obj.symbol.lineColor = lineColor;
  }
  //filter: {
  //   op: "", //andany
  //   list: [] //{name:,operator:,value:}
  // }
  return obj;
}
/****
 * @description 
 * @param{Number} min
 * @param{Number} max
 * @returns{Number}  [min,max)
 */
function randomInt(min, max) {
  var range = max - min;
  var rand = Math.random();
  var num = min + Math.floor(rand * range);
  return num;
}
/****
 * @description 
 * @returns{String} 
 *
 */
function randomColor16() {
  var r = Math.floor(Math.random() * 256);
  var g = Math.floor(Math.random() * 256);
  var b = Math.floor(Math.random() * 256);
  var color = "#" + r.toString(16) + g.toString(16) + b.toString(16);
  return color;
}

/****
 * @description 
 * @param {Array} list 
 * @returns {String} 
 */
function randomSymbolColor(list) {
  list = list || [];
  if (list && list.length >= COLORS.length) {
    //
    while (true) {
      let color = randomColor16();
      if (list.indexOf(color) < 0) {
        return color;
      }
    }
  } else {
    let len = COLORS.length;
    while (true) {
      let index = randomInt(0, len);
      if (list.indexOf(COLORS[index]) < 0) {
        return COLORS[index];
      }
    }
  }
}
/***
 * @description 
 * @param{Object} data 
 */
function deepCopy(data) {
  return data ? JSON.parse(JSON.stringify(data)) : {};
}
/***
 * @description ID
 * @param {Array} arrID
 * @param {Array} colors
 * @returns {Array} [{id:dtID,color:L.RGBAColor}]
 */
function makeAddDatasets(arr, colors) {
  let list = [];
  arr &&
    arr.forEach((item) => {
      let hexColor = randomSymbolColor(colors);
      colors.push(hexColor);
      let rgb = tinycolor(hexColor).toRgb();
      let color = new L.SymbolLib.RGBAColor(rgb.r, rgb.g, rgb.b, 255);
      list.push({ id: item, color: color });
    });
  return list;
}
