/****
 * @description value
 * @param{String} value 
 * @returns{String}  label
 */
export function findLabelByDataType(value) {
    let label = "";//stringintfloatdoubleblobdatetimedatetime
    let len = dataTypeList.length;
    for (let i = 0; i < len; i++) {
        if (dataTypeList[i].value == value) {
            label = dataTypeList[i].label;
            break;
        }
    }
    return label;
}
/****
 * @description value
 * @param{String} value 
 * @returns{String}  label
 */
export function findLabelByDatasetType(value) {
    let label = "";
    if (value == null || value == undefined) return '';
    value = value == 1 ? "point" : value == 2 ? "line" : value == 3 ? "polygon" : "";
    let temp = value.toLowerCase();
    let values = Object.values(datasetType)
    if (values && values.indexOf(temp) >= 0) {
        label = il8n.t('label.datasetType.' + temp)
    }
    else {
        label = temp;
    }
    return label;
}
