<!--  -->
<template>
  <Modal
    v-model="visible"
    class-name="vertical-center-modal"
    :closable="false"
    width="540"
    :mask-closable="false"
  >
    <Row slot="header" type="flex" class="modal-dialog-item modal-dialog-header" align="middle">
      <i-col span="12" style="text-align:left;">
        <span>{{$t("editDataset.title")||""}}</span>
      </i-col>
      <i-col span="12" style="text-align:right;cursor: pointer;">
        <i class="iconfont iconguanbi-" @click="closeDialog"></i>
      </i-col>
    </Row>
    <div class="cus-vec-edit2">
      <Form :label-width="80" label-position="left" ref="formInline" :model="formInline">
        <FormItem :label="$t('common.form.name')||''" prop="name">
          <Input v-model="formInline.name" disabled size="large"></Input>
        </FormItem>
        <FormItem :label="$t('common.form.crs')||''" prop="crs">
          <Select v-model="formInline.crs" filterable disabled size="large">
            <Option
              v-for="(item,index) in crsList"
              :value="item.auth_name+':'+item.srid"
              :key="index"
            >{{ formatCrs(item)}}</Option>
          </Select>
        </FormItem>
        <FormItem :label="$t('common.form.type')||''" prop="type">
          <RadioGroup v-model="formInline.type">
            <Radio :disabled="formInline.type==1?false:true" :label="1"></Radio>
            <Radio :disabled="formInline.type==2?false:true" :label="2"></Radio>
            <Radio :disabled="formInline.type==3?false:true" :label="3"></Radio>
          </RadioGroup>
        </FormItem>
      </Form>
      <Form :label-width="80" label-position="left" ref="formInline2" :model="formInline2">
        <FormItem :label="$t('common.form.fieldSet')||''" prop="fieldName">
          <Row type="flex" align="middle" justify="space-between">
            <Col span="9">
              <Input
                size="large"
                :placeholder="$t('common.placeholder.fieldName')||''"
                v-model="formInline2.fieldName"
              ></Input>
            </Col>
            <Col span="9">
              <Select v-model="formInline2.dataType" size="large">
                <Option
                  v-for="item in dataTypeList"
                  :value="item.value"
                  :key="item.value"
                >{{ item.label }}</Option>
              </Select>
            </Col>
            <Col span="4" style="text-align:right;">
              <Button
                type="primary"
                style="background:#44AAF6;font-size:12px;font-size:0.12rem;"
                @click="addField"
              >{{$t('common.button.add')}}</Button>
            </Col>
          </Row>
        </FormItem>
      </Form>
    </div>
    <Table :columns="columns" :data="fields" :row-class-name="rowClassName" height="160">
      <template slot-scope="{ row }" slot="name">
        <div class="label-ellipsis" :title="row.name">{{ row.name }}</div>
      </template>
      <template slot-scope="{ row }" slot="type">{{ findLabelByDataType(row.type) }}</template>
      <template slot-scope="{ row, index }" slot="action">
        <i
          class="iconfont iconshanchu1 modal-dialog-content-text"
          style="cursor:pointer;"
          @click="deletefield(index)"
        >{{$t('common.button.delete')||''}}</i>
      </template>
    </Table>
    <Row slot="footer" type="flex" align="middle">
      <Col span="24" style="text-align:right;">
        <Button
          class="modal-dialog-footer modal-dialog-footer-cancle"
          @click="closeDialog"
        >{{$t('common.button.cancel')||''}}</Button>
        <Button
          type="primary"
          class="modal-dialog-footer modal-dialog-footer-ok"
          @click="handleOk"
        >{{$t('common.button.confirm')||''}}</Button>
      </Col>
    </Row>
    <CusDialog
      :title="title2"
      :width="width"
      :visible="visible2"
      @cus_cancel="handleClose"
      @cus_ok="handleOk2"
    >
      <Row style="padding:6px ; font-size:14px;">
        <Col span="24" style="color:red; ">{{msg}}</Col>
      </Row>
    </CusDialog>
  </Modal>
</template>

<script>
import util from "../utils/util";
import { mapState } from "vuex";
import { dataTypeList, findLabelByDataType, LK_EVENT } from "../const/enum";
import { successMsg, errorMsg, infoMsg } from "../utils/message";
import Dialog from "./Dialog";
import CusDialog from "./base/CusDialog";
export default {
  components: {
    Dialog,
    CusDialog
  },
  data() {
    //
    return {
      sysFields: [], //
      isConfirm: false, //
      currentIndex: -1, //
      /***
       * @description 
       */
      title2: this.$t("dialog.confirm") || "",
      msg: "?",
      width: 300,
      visible2: false,
      dataset: null,
      crsList: [], //
      findLabelByDataType,
      visible: false, //
      formInline: {
        name: "", //
        type: 1, //    point    line    polygon
        crs: "EPSG:4326"
      },
      formInline2: {
        fieldName: "", //
        dataType: 1043 //
      },
      fields: [],
      sourcefields: [], //
      dataTypeList,
      columns: [
        {
          title: this.$t("common.table.fieldName") || "", //"",
          key: "name",
          slot: "name"
        },
        {
          title: this.$t("common.table.type") || "",
          key: "type",
          slot: "type"
        },
        {
          title: this.$t("common.table.op") || "",
          slot: "action",
          // width: 150,
          align: "center"
        }
      ]
    };
  },
  computed: {
    ...mapState(["map"])
  },
  //
  methods: {
    /***
     * @description 
     */
    handleClose() {
      this.visible2 = false;
    },
    /***
     * @description 
     */
    handleOk2() {
      if (this.isConfirm) {
        let fields = this.getChangeFields();
        if (!fields) {
          this.visible2 = false;
          return;
        }
        let options = {
          params: fields,
          id: this.dataset.getID(),
          success: result => {
            //
            let fieldsInfo = [];
            this.fields &&
              this.fields.forEach(item => {
                fieldsInfo.push(new L.SE.SeFieldInfo(item));
              });
            let bRet = this.map.updateDatasetFields(
              this.dataset.getID(),
              fieldsInfo
            );
            if (!bRet) {
              errorMsg(this.$t("message.updateFieldError"));
            } else {
              this.$bus.emit(LK_EVENT.EDIT_DATASET, {
                id: this.dataset.getID(),
                fields: util.createFieldsFromFieldsInfo(fieldsInfo)
              });
            }
            this.closeDialog();
            this.visible2 = false;
          },
          error: msg => {
            errorMsg(msg);
            this.visible2 = false;
          }
        };
        this.$http.updateDatasetFields(options);
      } else {
        //
        let index = this.currentIndex;
        if (this.fields && index >= 0 && index < this.fields.length) {
          this.fields.splice(index, 1);
        }
        this.visible2 = false;
      }
    },
    /****
     * @description 
     */
    getCrsList() {
      let options = {
        success: result => {
          if (result) {
            this.crsList = result;
          } else {
            this.crsList = [];
          }
        }
      };
      this.$http.getSysCrs(options);
    },
    formatCrs(item) {
      return item ? item.name + "#" + item.auth_name + ":" + item.srid : "";
    },
    showDialog() {
      this.clear();
      this.getCrsList();
      let editLayer = this.map.getEditLayer();
      if (editLayer && editLayer.dataset) {
        this.dataset = this.map.findDatasetById(editLayer.dataset.getID());
      }
      if (this.dataset) {
        this.formInline.name = this.dataset.getName();
        this.formInline.type = this.dataset.getType();
        this.formInline.crs = this.dataset.getProjInfo().getCode();
        let fieldsInfo = this.dataset.getFieldInfos();
        fieldsInfo &&
          fieldsInfo.forEach(item => {
            if (item.isSysField()) {
              this.sysFields.push(item.options);
            } else {
              this.fields.push(item.options);
            }
          });
        this.sourcefields.push(...this.fields);
      }
      this.visible = true;
    },
    /***
     * 
     */
    rowClassName(row, index) {
      if (index % 2 == 0) {
        return "table-even";
      } else {
        return "table-odd";
      }
    },
    /***
     * 
     */
    closeDialog() {
      this.clear();
      this.visible = false;
    },

    /***
     * 
     */
    addField() {
      //var reg2 = /(^_([a-zA-Z0-9]_?)*$)|(^[a-zA-Z](_?[a-zA-Z0-9])*_?$)/;
      let fieldName =
        this.formInline2.fieldName == null ||
        this.formInline2.fieldName == undefined
          ? ""
          : this.formInline2.fieldName.trim();
      if (fieldName.length <= 0) {
        errorMsg(this.$t("message.fieldNullError"));
        return false;
      }
      if (util.isMoreThanLength(fieldName)) {
        errorMsg(this.$t("message.stringLength64Error"));
        return false;
      }
      this.fields = this.fields || [];
      const bool = this.fields.some(
        item => item.name.toLowerCase() == fieldName.toLowerCase()
      );
      if (bool) {
        errorMsg(this.$t("message.fieldExistError"));
        return false;
      }
      //
      let field = this.sourcefields.find(
        item => item.name.toLowerCase() == fieldName.toLowerCase()
      );
      //
      if (field && field.type != this.formInline2.dataType) {
        errorMsg(this.$t("message.fieldTypeError"));
        return false;
      }
      this.formInline2.fieldName = "";
      let obj = {
        name: fieldName,
        type: this.formInline2.dataType,
        isNull: true,
        index: "none"
      };
      if (this.formInline2.dataType === "string") {
        obj.length = 255;
      }
      this.fields.push(obj);
    },
    /***
     * 
     * @param {Number} index:
     */
    deletefield(index) {
      this.currentIndex = index;
      this.visible2 = true;
      this.msg = "";
      this.width = 300;
      this.isConfirm = false;
    },
    /***
     * 
     */
    clear() {
      this.sysFields = [];
      this.isConfirm = false;
      this.currentIndex = -1;
      this.visible2 = false;
      this.dataset = null;
      this.formInline = {
        name: "", //
        type: 1, //    point    line    polygon
        crs: "EPSG:4326"
      };
      // this.formInline.name="";
      // this.formInline.type="point";
      // this.formInline.crs="EPSG:4326";
      //   name: "", //
      //   type: "point", //    point    line    polygon
      //   crs:"EPSG:4326"
      // };
      this.formInline2 = {
        fieldName: "", //
        dataType: 1043 //
      };
      this.fields = [];
      this.crsList = [];
      this.sourcefields = [];
    },
    /****
     * @description 
     * @returns {Object} :{add:[],delete:[]}
     */
    getChangeFields() {
      let len = this.fields ? this.fields.length : 0;
      let count = this.sourcefields ? this.sourcefields.length : 0;
      let addArr = []; //
      let delArr = []; //
      //
      for (let i = 0; i < len; i++) {
        let bExist = false;
        for (let j = 0; j < count; j++) {
          if (
            this.fields[i].name.toLowerCase() ==
            this.sourcefields[j].name.toLowerCase()
          ) {
            bExist = true;
            break;
          }
        }
        if (!bExist) {
          let obj = { ...this.fields[i] };
          delete obj["isNull"];
          obj["null"] = "null";
          addArr.push(obj);
        }
      }
      //
      for (let i = 0; i < count; i++) {
        let bExist = false;
        for (let j = 0; j < len; j++) {
          if (
            this.fields[j].name.toLowerCase() ==
            this.sourcefields[i].name.toLowerCase()
          ) {
            bExist = true;
            break;
          }
        }
        if (!bExist) {
          delArr.push(this.sourcefields[i]);
        }
      }
      let obj = null;
      if (addArr && addArr.length > 0) {
        obj = obj || {};
        obj.add = addArr;
      }
      if (delArr && delArr.length > 0) {
        obj = obj || {};
        obj.delete = delArr;
      }
      return obj;
    },
    /***
     * 
     */
    handleOk() {
      let result = this.getChangeFields();
      if (result) {
        this.visible2 = true;
        this.msg = "";
        this.width = 300;
        this.isConfirm = true;
      } else {
        infoMsg(".");
      }
      // let fieldsInfo = [];
      // this.fields &&
      //   this.fields.forEach(item => {
      //     fieldsInfo.push(new L.SE.SeFieldInfo(item));
      //   });
      // let bRet = this.map.updateDatasetFields(this.dataset.getID(), fieldsInfo);
      // if (!bRet) {
      //   errorMsg(this.$t("message.updateFieldError"));
      // } else {
      //   this.$bus.emit(LK_EVENT.EDIT_DATASET, {
      //     id: this.dataset.getID(),
      //     fields: util.createFieldsFromFieldsInfo(fieldsInfo)
      //   });
      // }
      // this.closeDialog();
    }
  }
};
</script>
<style lang='less' scope>
//@import url(); css
.vertical-center-modal {
  //   display: flex;
  //   align-items: center;
  //   justify-content: center;
  .ivu-modal {
    top: 20%;
    .ivu-modal-content {
      border-radius: 4px;
      box-shadow: 0px 0px 20px 0px rgba(51, 80, 110, 0.3);
      // width: 540px;
      background: rgba(242, 246, 249, 1);
    }
  }
}
</style>


