<!--  -->
<template>
  <Modal
    v-model="visible"
    class-name="vertical-center-modal"
    :footer-hide="true"
    :closable="false"
    :mask-closable="false"
    width="800"
  >
    <Row slot="header" type="flex" class="modal-dialog-item modal-dialog-header" align="middle">
      <i-col span="12" style="text-align:left;">
        <span>{{$t("addDataset.title")||""}}</span>
      </i-col>
      <i-col span="12" style="text-align:right;cursor: pointer;">
        <i class="iconfont iconguanbi-" @click="closeDialog"></i>
      </i-col>
    </Row>
    <Row type="flex" align="middle" justify="space-between" class="cus-vec-edit-btn">
      <Col span="16">
        <Input
          v-model="key"
          :placeholder="$t('common.placeholder.key')||''"
          style="width:70%;"
        />
        <Button
          type="primary"
          icon="ios-search"
          style="margin-left: 10px;"
          class="modal-dialog-footer-ok"
          @click="handleSearch"
        >{{$t("common.button.search")||""}}</Button>
      </Col>
      <Col span="4" style="text-align:right;">
        <Button
          @click="handleOpen"
          type="primary"
          class="modal-dialog-footer modal-dialog-footer-ok"
        >{{$t("common.button.add")||""}}</Button>
      </Col>
    </Row>
    <Table
      @on-selection-change="handleSelectChange"
      ref="selection"
      :columns="columns"
      :data="dtList"
      :row-class-name="rowClassName"
      height="240"
      style="margin-top:16px;"
    >
      <template slot-scope="{ row }" slot="name">
        <div class="label-ellipsis" :title="row.name">{{row.name}}</div>
      </template>
      <template slot-scope="{ row }" slot="type" class="label-ellipsis">{{row.type }}</template>
      <template slot-scope="{ row }" slot="crs" class="label-ellipsis">
        <div class="label-ellipsis" :title="row.srid">{{row.srid}}</div>
      </template>
      <template slot-scope="{ row }" slot="update_time">
        <div class="label-ellipsis" :title="row.update_time">{{row.update_time}}</div>
      </template>
    </Table>
    <Row type="flex" align="middle" justify="space-between" style="margin-top:16px;">
      <Col span="4">
        <!-- <Button
          @click="handleSelectAll(true)"
          type="primary"
          class="modal-dialog-footer-ok"
        >{{$t('common.button.select')||''}}</Button>-->
        <span>{{$t('common.text.selected',{num:selected})}}</span>
      </Col>
      <Col span="20" style="text-align:right;">
        <Page
          :total="total"
          :page-size="pageSize"
          :current.sync="current"
          @on-change="handleChange"
          show-elevator
        />
      </Col>
    </Row>
  </Modal>
</template>

<script>
import { successMsg, errorMsg } from "../utils/message";
import { findLabelByDatasetType, LK_EVENT, datasetType } from "../const/enum";
import util from "../utils/util";
import { mapState } from "vuex";
import tinycolor from "tinycolor2";
export default {
  computed: {
    ...mapState(["map"])
  },
  data() {
    //
    return {
      key: "", //
      pageSize: 5, //
      total: 0,
      current: 1, //
      visible: false,
      selected: 0,
      /***
       * @param{Object} {data_id: element.data_id,name: element.dataset_name,type: element.dataset_type,crs: element.crs.crsid}
       */
      dtList: [],
      columns: [
        {
          type: "selection",
          width: 60,
          align: "center"
        },
        {
          title: this.$t("common.table.name") || "", //"",
          key: "name",
          slot: "name"
        },
        {
          title: this.$t("common.table.crs") || "",
          key: "srid",
          slot: "crs"
        },
        {
          title: this.$t("common.table.type") || "",
          key: "type",
          slot: "type"
        },
        {
          title: "",
          key: "update_time",
          slot: "update_time"
        }
      ]
    };
  },
  //
  methods: {
    showDialog() {
      this.clear();
      this.visible = true;
      this.handleSearch();
    },
    /***
     * 
     */
    clear() {
      this.key = "";
      this.total = 0;
      this.current = 1;
      this.selected = 0;
      this.dtList = [];
    },
    closeDialog() {
      this.visible = false;
    },
    /***
     * @description 
     */
    rowClassName(row, index) {
      if (index % 2 === 0) {
        return "table-even";
      } else {
        return "table-odd";
      }
    },
    /***
     * @description 
     */
    handleSelectAll(status) {
      this.$refs.selection.selectAll(status);
    },
    /****
     * @description 
     */
    handleSelectChange() {
      if (this.$refs.selection && this.$refs.selection.getSelection()) {
        this.selected = this.$refs.selection.getSelection().length;
      } else {
        this.selected = 0;
      }
    },
    /***
     * @description 
     */
    handleSearch() {
      this.current = 1;
      this.queryDatasets(
        this.key,
        this.current,
        this.pageSize,
        this.searchSucces
      );
    },
    /***
     * @description 
     */
    searchSucces(result) {
      this.dtList = [];
      this.total = result ? result.total : 0;
      let items = result.rows ? result.rows : [];
      items.forEach(element => {
        this.dtList.push({
          data_id: element.id,
          name: element.name,
          type: findLabelByDatasetType(element.type),
          srid: `EPSG:${element.srid}`,
          update_time: element.create_time
        });
      });
    },
    /***
     * @description 
     */
    handleOpen() {
      if (this.selected == 0) {
        errorMsg(this.$t("message.selectNullError"));
        return false;
      }
      if (!this.map || !this.map.seManager) {
        return false;
      }
      let seManager = this.map.seManager;
      let selList = this.$refs.selection.getSelection();
      let filterList = [];
      selList.forEach(item => {
        let temp = seManager.getLayersByDtId(item.data_id);
        if (!temp || temp.length == 0) {
          filterList.push(item.data_id);
        }
      });
      if (filterList.length === 0) {
        errorMsg(this.$t("message.addDatasetNull"));
        return false;
      }
      let list = util.makeAddDatasets(filterList, this.$symbolColors);
      if (list && list.length > 0) {
        this.map.addCloudLayersExt(list, {
          success: () => {
            let result = [];
            filterList.forEach(item => {
              let temp = seManager.getLayersByDtId(item);
              if (temp && temp.length > 0) {
                let editLayer = this.map.getEditLayer();
                let bEdit = false;
                if (editLayer && editLayer.getID() === temp[0].getID()) {
                  bEdit = true;
                }
                let obj = util.createLayerInfo(temp[0], bEdit);
                obj && result.push(obj);
              }
            });
            this.$bus.emit(LK_EVENT.ADD_DATASETS, result);
            this.visible = false;
          },
          error: err => {
            errorMsg(err || this.$t("message.sdkInnerError"));
          }
        });
      }
    },
    /***
     * @description 
     */
    handleChange() {
      this.queryDatasets(
        this.key,
        this.current,
        this.pageSize,
        this.searchSucces
      );
    },
    /****
     * @description 
     * @param{String} key
     * @param{Number} current
     * @param{Number} pageSize
     * @param{Function} success
     */
    queryDatasets(key, current, pageSize, success) {
      key = key == undefined || key == null ? "" : key.trim();
      // if (!key) {
      //   errorMsg(this.$t("message.keyNullError"));
      //   return false;
      // }
      this.selected = 0;
      current = current <= 0 ? 1 : current;
      pageSize = pageSize <= 0 ? this.pageSize : pageSize;
      let params = {
        key,
        page_size: pageSize,
        page: current
      };
      let options = {
        params,
        success
      };
      this.$http.getDatasetList(options);
    },
  }
};
</script>
<style lang='less' scoped>
</style>


