import Vue from 'vue'
import Vuex from 'vuex'
import { getToken, getHost, getDatasetId } from '../utils/storage'
const PB_MAP_DEFAULT = "(4326)";
L.QGIS.MouseStyle.baseImageUrl = "static/lib/images/cursor/";
/***
 * @description token
 */
const access_token = getToken();
/***
 * @description URL
 */
const host = getHost();
/***
 * @description ,ID
 */
const dataset = getDatasetId();
/**
* @fileOverview Store
* @author LJ
* @version 0.1
*/
Vue.use(Vuex);
const store = new Vuex.Store({
    state: {
        /***
         * @description 
         */
        map: null,
        /***
         * @description IP
         */
        city: null,
        /****
         * @description token
         */
        access_token,
        /***
         * @description 
         */
        host,
        /***
         * @description 
         */
        userName: '',
        /****
         * @
         */
        sysUser: '',
        /****
         * @description ,ID
         */
        datasets: dataset && [dataset],
        curPbMap: PB_MAP_DEFAULT, //(4326)
        gray: 0 //0;1
    },
    mutations: {
        /***
         * @description Map
         */
        setMap(state, value) {
            state.map = value;
        },
        /****
         * @description 
         */
        setCity(state, value) {
            state.city = value;
        },
        setApiConfig(state, conf) {
            // state.access_token = conf.token;
            // state.host = conf.host;
            // state.userName = conf.user_name;
            // state.sysUser = conf.sys;
            state.host = conf.host;
            state.access_token = conf.access_token;
        },
        setCurPbMap(state, val) {
            val = val || PB_MAP_DEFAULT;
            state.curPbMap = val;
        },
        setGray(state, val) {
            state.gray = val;
        }
    }
})
export default store;


