<!--  -->
<template>
  <div class="simgle-attr-containter">
    <Drawer
      v-model="visible"
      :width="contentWidth"
      :closable="false"
      :mask-closable="false"
      :mask="isNew"
    >
      <!--header-->
      <Row solt="header" type="flex" class="row-item simgle-attr-header" align="middle">
        <i-col span="20" style="text-align:left;">
          <span>{{datasetName||''}}</span>
        </i-col>
        <i-col span="4" style="text-align:right;cursor: pointer;">
          <i class="iconfont iconguanbi-" @click.stop="closeDialog"></i>
        </i-col>
      </Row>
      <div class="simgle-attr-content">
        <!---->
        <div v-if="type==3">
          <Row type="flex" class="simgle-attr-content-row-item" align="middle">
            <Col span="8" style="line-height: 1;">
              <div
                style="overflow:hidden;text-overflow:ellipsis;white-space:nowrap;max-width:110px;"
              ></div>
            </Col>
            <Col span="16" style="background:#d7e7f2;line-height: 1;">
              <Select v-model="curField">
                <Option v-for="t in fields" :value="t.name" :key="t.name">{{ t.name }}</Option>
              </Select>
            </Col>
          </Row>
          <Row type="flex" class="simgle-attr-content-row-item" align="middle">
            <Col span="8" style="line-height: 1;">
              <div
                style="overflow:hidden;text-overflow:ellipsis;white-space:nowrap;max-width:110px;"
              >
                <span
                  v-show="!isNull(curField)"
                  style="color:#ed4014;padding:0 4px;display: inline-block;vertical-align: bottom;line-height: 1;"
                >*</span>
                
              </div>
            </Col>
            <Col span="16" style="background:#d7e7f2;line-height: 1;">
              <Select
                v-model="curValue"
                v-show="isBoolType(findFieldByName(curField))"
                :placeholder="getMsg(findFieldByName(curField))"
              >
                <Option
                  v-for="t in getBoolList(findFieldByName(curField))"
                  :value="t.value"
                  :key="t.value"
                >{{ t.label }}</Option>
              </Select>
              <Input
                v-model="curValue"
                v-show=" !isBoolType(findFieldByName(curField))"
                :placeholder="getMsg(findFieldByName(curField))"
              />
            </Col>
          </Row>
        </div>
        <!---->
        <Row
          v-else
          type="flex"
          class="simgle-attr-content-row-item"
          align="middle"
          v-for="(item,index) in values"
          :key="index"
        >
          <Col span="8" style="line-height: 1;">
            <Tooltip :content="item.name" placement="bottom" max-width="110">
              <div
                style="overflow:hidden;text-overflow:ellipsis;white-space:nowrap;max-width:110px;"
              >
                <span
                  v-show="!item.isNull"
                  style="color:#ed4014;padding:0 4px;display: inline-block;vertical-align: bottom;line-height: 1;"
                >*</span>
                {{item.name}}
              </div>
            </Tooltip>
          </Col>
          <Col span="16" style="background:#d7e7f2;line-height: 1;">
            <Select
              v-model="item.value"
              v-show="(isEdit||isNew) && isBoolType(item)"
              :placeholder="getMsg(item)"
            >
              <Option v-for="t in getBoolList(item)" :value="t.value" :key="t.value">{{ t.label }}</Option>
            </Select>
            <Input
              v-model="item.value"
              v-show="(isEdit||isNew) && !isBoolType(item)"
              :placeholder="getMsg(item)"
            />
            <Tooltip placement="bottom" max-width="238" v-show="isNormal">
              <p
                style="padding:0px 10px; height:36px; display: table-cell;vertical-align:middle;overflow:hidden;text-overflow:ellipsis;white-space:nowrap;max-width:238px;"
              >{{item.value}}</p>
              <div slot="content">{{item.value}}</div>
            </Tooltip>
          </Col>
        </Row>
        <Row type="flex" align="middle" class="row-item" v-show="isNew">
          <Col span="16" offset="8" style="text-align:center;">
            <Button
              @click.stop="handleSaveFeature"
              type="primary"
              style="background:#44AAF6;font-size:14px; width:100px;"
            >{{$t("common.button.save")||""}}</Button>
          </Col>
        </Row>
        <Row type="flex" align="middle" class="row-item" v-show="isEdit">
          <Col span="16" offset="8" style="text-align:center;">
            <Button
              @click.stop="modifyAttribute"
              type="primary"
              style="background:#44AAF6;font-size:14px; width:100px;"
            >{{$t("common.button.modify")||""}}</Button>
          </Col>
        </Row>
        <!---->
        <Row type="flex" align="middle" class="row-item" v-show="isBatchEdit">
          <Col span="16" offset="8" style="text-align:center;">
            <Button
              @click.stop="batchModifyAttribute"
              type="primary"
              style="background:#44AAF6;font-size:14px; width:100px;"
            >{{$t("common.button.batchModify")||""}}</Button>
          </Col>
        </Row>
      </div>
    </Drawer>
  </div>
</template>
<script>
import { mapState } from "vuex";
import { dataType, boolList, NULL } from "../const/enum";
import util from "../utils/util";
import { errorMsg, successMsg } from "../utils/message";
export default {
  data() {
    return {
      layerId: null, //ID
      width: 0,
      /****
       * @param{String} datasetName 
       */
      datasetName: "",
      /****
       * @param{Number} 0,1,2,3
       */
      type: 0,
      /****
       * @param{Boolean} visible 
       */
      visible: false,
      datasetId: null,
      values: [],
      feature: null,
      //
      features: [],
      fields: [],
      curField: "", //
      curValue: "" //
    };
  },
  computed: {
    contentWidth() {
      if (this.width > 1360) {
        return 400;
      } else {
        return 400;
      }
    },
    isBatchEdit() {
      if (this.type == 3) {
        return true;
      }
      return false;
    },
    isEdit() {
      if (this.type == 2) {
        return true;
      }
      return false;
    },
    isNew() {
      if (this.type == 1) {
        return true;
      }
      return false;
    },
    isNormal() {
      if (this.type == 0) {
        return true;
      }
      return false;
    },
    ...mapState(["map"])
  },
  watch: {
    map() {
      this.map && this.map.off("delete-layer", this.handleDeleteLayer);
      this.map && this.map.on("delete-layer", this.handleDeleteLayer);
    },
    curField(newVal) {
      let field = this.findFieldByName(newVal);
      if (this.isBoolType(field)) {
        let list = this.getBoolList(field);
        this.curValue = list && list.length > 0 ? list[0].value : "NULL";
      } else {
        this.curValue = "";
      }
    }
  },
  methods: {
    /***
     * @description Bool
     */
    isBoolType(item) {
      if (item && item.type == dataType.BOOL) {
        return true;
      }
      return false;
    },
    getBoolList(item) {
      if (item && item.type == dataType.BOOL) {
        if (item.isNull) {
          return [NULL, ...boolList];
        } else {
          return [...boolList];
        }
      }
      return [];
    },
    /***
     *
     */
    handleFocus(event) {
      event.target.select();
      console.log(event);
    },
    /****
     * @description 
     */
    handleDeleteLayer(e) {
      if (this.visible && this.layerId !== null && this.layerId === e.layerId) {
        this.closeDialog();
      }
    },
    getMsg(item) {
      let msg = "";
      if (item) {
        switch (item.type) {
          case L.QGIS.DataType.DB_STRING:
          case dataType.STRING: {
            msg = this.$t("common.placeholder.stringType") || "";
            break;
          }
          case L.QGIS.DataType.DB_BIGINT:
          case L.QGIS.DataType.DB_INTEGER:
          case dataType.INT: {
            msg = this.$t("common.placeholder.intType") || "";
            break;
          }
          case dataType.FLOAT:
          case L.QGIS.DataType.DB_DOUBLE:
          case dataType.DOUBLE: {
            msg = this.$t("common.placeholder.numberType") || "";
            break;
          }
          case L.QGIS.DataType.DB_DATETIME:
          case dataType.DATETIME: {
            msg =
              this.$t("common.placeholder.datetimeType") || "";
            break;
          }
          case L.QGIS.DataType.DB_DATE:
          case dataType.DATE: {
            msg = this.$t("common.placeholder.dateType") || "";
            break;
          }
          case L.QGIS.DataType.DB_TIME:
          case dataType.TIME: {
            msg = this.$t("common.placeholder.timeType") || "";
            break;
          }
          case dataType.BOOL: {
            msg = this.$t("common.placeholder.boolType") || "";
            break;
          }
          default: {
            msg = this.$t("common.placeholder.unknowType") || "";
            break;
          }
        }
      } else {
        msg = this.$t("common.placeholder.unknowType") || "";
      }
      return msg;
    },
    isNull(name) {
      let field = this.findFieldByName(name);
      return field && field.isNull ? true : false;
    },
    /***
     * @description 
     */
    findFieldByName(name) {
      return this.fields && this.fields.find(item => item.name == name);
    },
    /****
     * @description 
     */
    showDialog(data) {
      if (data) {
        this.visible = true;
        this.layerId = data.layer_id;
        this.type = data.type;
        this.datasetName = data.name;
        this.datasetId = data.dataset_id;
        let dt = this.map.findDatasetById(data.dataset_id);
        if (this.type == 3) {
          this.features = data.features;
          let list = dt ? dt.getFieldInfos() : [];
          list.forEach((item, index) => {
            if (index == 0) {
              this.curField = item.getName();
            }
            this.fields.push({
              name: item.getName(),
              type: item.getType(),
              isNull: item.isNull()
            });
          });
        } else {
          this.feature = data.feature;
          if (!this.feature) return flase;
          let fields = dt ? dt.getFieldInfos() : [];
          let detailFunc = properties => {
            this.values = [];
            let temp = properties;
            if (temp && fields && fields.length > 0) {
              let count = fields.length;
              for (let key in temp) {
                let bExist = false;
                let field = null;
                for (let i = 0; i < count; i++) {
                  if (fields[i].getName() === key) {
                    bExist = true;
                    field = fields[i];
                    break;
                  }
                }
                if (bExist) {
                  let tValue = null;
                  if (field.getType() == dataType.BOOL) {
                    tValue = temp[key] == null ? "NULL" : temp[key].toString();
                  } else {
                    tValue =
                      this.type == 0 && temp[key] == null ? "NULL" : temp[key];
                  }
                  this.values.push({
                    name: key,
                    value: tValue,
                    type: field.getType(),
                    isNull: field.isNull()
                  });
                }
              }
            }
          };
          let properties = data.feature ? data.feature.getProperties() : null;
          if (this.type == 1) {
            //
            detailFunc(properties);
          } else {
            detailFunc(properties);
          }
        }
      } else {
        this.closeDialog();
      }
    },
    /****
     * @description 
     */
    closeDialog() {
      this.layerId = null;
      this.visible = false;
      this.datasetName = "";
      this.type = 0;
      this.datasetId = null;
      this.values = [];
      this.feature = null;
      this.features = [];
      this.fields = [];
      this.curField = ""; //
      this.curValue = null; //
    },
    /***
     * @description 
     */
    batchModifyAttribute() {
      if (!this.features || this.features.length == 0) {
        this.closeDialog();
        return;
      }
      let field = this.findFieldByName(this.curField);
      if (!field) {
        return;
      }
      let len = this.fields ? this.fields.length : 0;
      if (len == 0) {
        this.closeDialog();
        successMsg(this.$t("message.success"));
        return;
      }
      let temp = {
        ...field,
        value: this.curValue
      };
      let obj = null;
      let bValid = false;
      let value = null;
      if (temp.value == null || temp.value.toString().trim() == "") {
        if (!temp.isNull) {
          errorMsg("[" + temp.name + "]" + this.$t("message.dataNullError"));
          return false;
        } else {
          value = null;
          bValid = true;
        }
      } else {
        switch (temp.type) {
          case L.QGIS.DataType.DB_INTEGER:
          case L.QGIS.DataType.DB_INT:
          case L.QGIS.DataType.DB_BIGINT: {
            bValid = util.isValidInteger(temp.value);
            if (bValid) {
              value = util.parseNum(temp.value);
            }
            break;
          }
          case L.QGIS.DataType.DB_FLOAT:
          case L.QGIS.DataType.DB_DOUBLE: {
            bValid = util.isValidDecimal(temp.value);
            if (bValid) {
              value = util.parseNum(temp.value);
            }
            break;
          }
          case L.QGIS.DataType.DB_DATETIME: {
            bValid = util.isValidDate(temp.value);
            if (bValid) {
              value = temp.value;
            }
            break;
          }
          case L.QGIS.DataType.DB_DATE: {
            bValid = util.isDatePart(temp.value);
            if (bValid) {
              value = temp.value;
            }
            break;
          }
          case L.QGIS.DataType.DB_TIME: {
            bValid = util.isTimePart(temp.value);
            if (bValid) {
              value = temp.value;
            }
            break;
          }
          case L.QGIS.DataType.DB_STRING: {
            bValid = true;
            value = temp.value;
            // if (bValid) {
            //   value = temp.value.trim();
            // }
            break;
          }
          case dataType.BOOL: {
            //bool
            if (temp.isNull) {
              if (temp.value.toString().trim() == "NULL") {
                value = null;
              } else if (temp.value.toString().trim() == "true") {
                value = "true";
              } else if (temp.value.toString().trim() == "false") {
                value = "false";
              } else {
                value = null;
              }
              bValid = true;
            } else {
              if (temp.value.toString().trim() == "true") {
                value = "true";
                bValid = true;
              } else if (temp.value.toString().trim() == "false") {
                value = "false";
                bValid = true;
              }
            }
            break;
          }
        }
      }
      if (!bValid) {
        errorMsg("[" + temp.name + "]" + this.$t("message.dataNotValidError"));
        return false;
      }
      if (!obj) {
        obj = {};
      }
      obj[temp.name] = value;
      if (obj && this.features && this.datasetId) {
        let ids = [];
        this.features.forEach(item => {
          ids.push(item.getID());
        });
        let bRet = this.map.modifyFeaturesAttribute(this.datasetId, ids, obj);
        if (!bRet) {
          errorMsg(this.$t("message.modifyError"));
        } else {
          successMsg(this.$t("message.success"));
          this.map.refresh();
          this.closeDialog();
        }
      } else {
        errorMsg(this.$t("message.modifyError"));
      }
    },
    /****
     * @description 
     */
    modifyAttribute() {
      let len = this.values ? this.values.length : 0;
      if (len == 0) {
        this.closeDialog();
        successMsg(this.$t("message.success"));
        return;
      }
      let obj = null;
      for (let i = 0; i < len; i++) {
        let temp = this.values[i];
        let bValid = false;
        let value = null;
        if (temp.value == null || temp.value.toString().trim() == "") {
          if (!temp.isNull) {
            errorMsg("[" + temp.name + "]" + this.$t("message.dataNullError"));
            return false;
          } else {
            if (!obj) {
              obj = {};
            }
            obj[temp.name] = null;
            continue;
          }
        }
        switch (temp.type) {
          case L.QGIS.DataType.DB_INTEGER:
          case L.QGIS.DataType.DB_INT:
          case L.QGIS.DataType.DB_BIGINT: {
            bValid = util.isValidInteger(temp.value);
            if (bValid) {
              value = util.parseNum(temp.value);
            }
            break;
          }
          case L.QGIS.DataType.DB_FLOAT:
          case L.QGIS.DataType.DB_DOUBLE: {
            bValid = util.isValidDecimal(temp.value);
            if (bValid) {
              value = util.parseNum(temp.value);
            }
            break;
          }
          case L.QGIS.DataType.DB_DATETIME: {
            bValid = util.isValidDate(temp.value);
            if (bValid) {
              value = temp.value;
            }
            break;
          }
          case L.QGIS.DataType.DB_DATE: {
            bValid = util.isDatePart(temp.value);
            if (bValid) {
              value = temp.value;
            }
            break;
          }
          case L.QGIS.DataType.DB_TIME: {
            bValid = util.isTimePart(temp.value);
            if (bValid) {
              value = temp.value;
            }
            break;
          }
          case L.QGIS.DataType.DB_STRING: {
            bValid = true;
            value = temp.value;
            // if (bValid) {
            //   value = temp.value.trim();
            // }
            break;
          }
          case dataType.BOOL: {
            //bool
            if (temp.isNull) {
              if (temp.value.toString().trim() == "NULL") {
                value = null;
              } else if (temp.value.toString().trim() == "true") {
                value = "true";
              } else if (temp.value.toString().trim() == "false") {
                value = "false";
              } else {
                value = null;
              }
              bValid = true;
            } else {
              if (temp.value.toString().trim() == "true") {
                value = "true";
                bValid = true;
              } else if (temp.value.toString().trim() == "false") {
                value = "false";
                bValid = true;
              }
            }
            break;
          }
        }
        if (!bValid) {
          errorMsg(
            "[" + temp.name + "]" + this.$t("message.dataNotValidError")
          );
          return false;
        }
        if (!temp.isNull) {
          if (!obj) {
            obj = {};
          }
          obj[temp.name] = value;
        } else {
          if (bValid) {
            if (!obj) {
              obj = {};
            }
            obj[temp.name] = value;
          }
        }
      }
      if (obj && this.feature && this.datasetId) {
        let bRet = this.map.modifyFeaturesAttribute(
          this.datasetId,
          [this.feature.getID()],
          obj
        );
        if (!bRet) {
          errorMsg(this.$t("message.modifyError"));
        } else {
          successMsg(this.$t("message.success"));
          this.map.refresh();
          this.closeDialog();
        }
      } else {
        errorMsg(this.$t("message.modifyError"));
      }
    },
    /****
     * @description 
     */
    handleSaveFeature() {
      let len = this.values ? this.values.length : 0;
      let obj = null;
      if (len == 0 && this.feature) {
        this.feature.setProperties({});
        let bRet = this.map.addFeatures([this.feature]);
        if (!bRet) {
          errorMsg(this.$t("message.fail"));
        } else {
          successMsg(this.$t("message.success"));
          this.map.refresh();
          this.closeDialog();
        }
        return;
      }
      let bHasNotNullField = false;
      for (let i = 0; i < len; i++) {
        let temp = this.values[i];
        let bValid = false;
        let value = null;
        if (temp.value == null || temp.value.toString().trim() == "") {
          if (!temp.isNull) {
            errorMsg("[" + temp.name + "]" + this.$t("message.dataNullError"));
            return false;
          } else {
            if (!obj) {
              obj = {};
            }
            obj[temp.name] = null;
            continue;
          }
        }
        switch (temp.type) {
          case L.QGIS.DataType.DB_INTEGER:
          case L.QGIS.DataType.DB_INT:
          case L.QGIS.DataType.DB_BIGINT: {
            bValid = util.isValidInteger(temp.value);
            if (bValid) {
              value = util.parseNum(temp.value);
            }
            break;
          }
          case L.QGIS.DataType.DB_FLOAT:
          case L.QGIS.DataType.DB_DOUBLE: {
            bValid = util.isValidDecimal(temp.value);
            if (bValid) {
              value = util.parseNum(temp.value);
            }
            break;
          }
          case L.QGIS.DataType.DB_DATETIME: {
            bValid = util.isValidDate(temp.value);
            if (bValid) {
              value = temp.value;
            }
            break;
          }
          case L.QGIS.DataType.DB_DATE: {
            bValid = util.isDatePart(temp.value);
            if (bValid) {
              value = temp.value;
            }
            break;
          }
          case L.QGIS.DataType.DB_TIME: {
            bValid = util.isTimePart(temp.value);
            if (bValid) {
              value = temp.value;
            }
            break;
          }
          case L.QGIS.DataType.DB_STRING: {
            bValid = true;
            value = temp.value;
            // bValid = util.isValidString(temp.value);
            // if (bValid) {
            //   value = temp.value.trim();
            // }
            break;
          }
          case dataType.BOOL: {
            //bool
            if (temp.isNull) {
              if (temp.value.toString().trim() == "NULL") {
                value = null;
              } else if (temp.value.toString().trim() == "true") {
                value = "true";
              } else if (temp.value.toString().trim() == "false") {
                value = "false";
              } else {
                value = null;
              }
              bValid = true;
            } else {
              if (temp.value.toString().trim() == "true") {
                value = "true";
                bValid = true;
              } else if (temp.value.toString().trim() == "false") {
                value = "false";
                bValid = true;
              }
            }
            break;
          }
        }
        if (!bValid) {
          errorMsg(
            "[" + temp.name + "]" + this.$t("message.dataNotValidError")
          );
          return false;
        }
        if (!temp.isNull) {
          if (!obj) {
            obj = {};
          }
          obj[temp.name] = value;
          if (!bHasNotNullField) {
            bHasNotNullField = true;
          }
        } else {
          if (bValid) {
            if (!obj) {
              obj = {};
            }
            obj[temp.name] = value;
          } else {
            if (!obj) {
              obj = {};
            }
            obj[temp.name] = null;
          }
        }
      }
      if (obj && this.feature) {
        this.feature.setProperties(obj);
        let bRet = this.map.addFeatures([this.feature]);
        if (!bRet) {
          errorMsg(this.$t("message.fail"));
        } else {
          successMsg(this.$t("message.success"));
          this.map.refresh();
          this.closeDialog();
        }
      } else {
        errorMsg(this.$t("message.fail"));
      }
    }
  },
  mounted() {
    this.width = `${document.documentElement.clientWidth}`;
    window.onresize = () => {
      this.width = `${document.documentElement.clientWidth}`;
    };
  }
};
</script>
<style lang="less" scope>
.row-item {
  padding: 0 16px;
}
.simgle-attr-containter {
  width: 100%;
  height: 100%;
  background: rgba(242, 246, 249, 1);
  box-shadow: 0px 0px 10px 0px rgba(51, 80, 110, 0.2);
}
.simgle-attr-header {
  height: 36px;
  font-size: 14px;
  font-family: SourceHanSansCN-Normal;
