<!--  -->
<template>
  <CusDialog
    @cus_cancel="handleClose"
    :visible="visible"
    :title="title"
    :width="1154"
    :isShowBottomBtn="false"
    class="cus-vec-edit"
  >
    <div class="c-f-containter2">
      <div class="left">
        <Table
          @on-row-click="handleRowClick"
          :columns="columns"
          :data="list"
          :row-class-name="rowClassName"
          height="344"
        >
          <template slot-scope="{ row }" slot="layerName">
            <div class="label-ellipsis c-f-row-item-label" :title="row.layerName">{{row.layerName}}</div>
          </template>
          <template slot-scope="{ row,index }" slot="remoteId">
            <div class="c-f-row-item">
              <span class="label-ellipsis" :title="row.remoteId">{{row.remoteId}}</span>
              <i
                v-if="row.isCheckedForServer"
                class="iconfont iconyixuan-"
                @click.stop="handleRemoteClick(row,index)"
              ></i>
              <i v-else class="iconfont iconkuang-" @click.stop="handleRemoteClick(row,index)"></i>
              <span></span>
            </div>
          </template>
          <template slot-scope="{ row,index }" slot="localId">
            <div class="c-f-row-item">
              <span :title="row.remoteId">{{row.localId}}</span>
              <i
                v-if="row.isCheckedForLocal"
                class="iconfont iconyixuan-"
                @click.stop="handleLocalClick(row,index)"
              ></i>
              <i v-else class="iconfont iconkuang-" @click.stop="handleLocalClick(row,index)"></i>
              <span></span>
            </div>
          </template>
          <template slot-scope="{ row }" slot="action">
            <div class="label-ellipsis c-f-row-item-label">{{row.action}}</div>
          </template>
        </Table>
      </div>
      <div class="right">
        <div id="tmap"></div>
        <div class="c-f-left-top">
          <img src="../assets/img/zoomin.svg" @click="zoomin" title="" />
          <img src="../assets/img/zoomout.svg" @click="zoomout" title="" />
        </div>
        <div class="c-f-left-top-attr">
          <span class="iconfont icon-sym" @click="showAttriDialog" title="">&#xe671;</span>
        </div>
        <div class="c-f-left-top-vi">
          <img src="../assets/img/layer_vi.svg" @click="handleChangeDraw" title="" />
        </div>
        <!---->
        <div class="c-f-left-bottom">
          <ul class="cp-f">
            <li>
              <div style="width:12px;height:2px;background:#FF0000;"></div>
              <span></span>
            </li>
            <li>
              <div style="width:12px;height:4px;background:#0018FF;"></div>
              <span></span>
            </li>
          </ul>
        </div>
        <AttributeCompare
          :dataset-name="curDtName"
          :attris="curAttris"
          v-if="bVisbileAttri"
          @attri-close="handleAttriClose"
          class="c-f-attr"
        ></AttributeCompare>
      </div>
    </div>

    <Row type="flex" align="middle" class="c-f-row-item-2">
      <Col span="16" offset="8" style="text-align:right;">
        <Button
          @click="handleSave"
          type="primary"
          style="background:#44AAF6;font-size:14px; width:100px;"
        >{{$t("common.button.save")||""}}</Button>
      </Col>
    </Row>
    <Dialog :title="title2" :visible="visible2" @cus_cancel="handleClose2" @cus_ok="handleSaveOk">
      <Row style="padding:6px ; font-size:14px;">
        <Col span="24" style="color:red; ">{{msg}}</Col>
      </Row>
    </Dialog>
  </CusDialog>
</template>

<script>
import CusDialog from "./base/CusDialog";
import AttributeCompare from "./base/AttributeCompare";
import { findPbMap } from "../const/enum";
import { mapState } from "vuex";
import { successMsg, errorMsg } from "../utils/message";
import util from "../utils/util";
import Dialog from "./Dialog";
export default {
  //import
  components: { CusDialog, AttributeCompare, Dialog },
  data() {
    //
    return {
      /***
       * @description 
       */
      title2: this.$t("dialog.confirm") || "",
      /***
       * @description 
       */
      visible2: false,
      /**
       * @description 
       */
      msg: this.$t("dialog.saveFeaturesMsg"),
      tMap: null,
      /***
       * @description 
       * @param{SeManager}
       */
      seManager: null,
      array: [],
      title: this.$t("dialog.compareFeature") || "",
      visible: false,
      columns: [
        {
          title: "",
          key: "layerName",
          slot: "layerName",
          width: 158
        },
        {
          title: "",
          key: "remoteId",
          slot: "remoteId",
          width: 150
        },
        {
          title: "",
          key: "localId",
          slot: "localId",
          width: 150
        },
        {
          title: "",
          key: "action",
          slot: "action",
          width: 100
        }
      ],
      bVisbileAttri: false,
      list: [
        // {
        //   id: 1, //
        //   dtId: 1, //ID
        //   layerName: "dadadadadaadadadad", //
        //   remoteId: 12121, //ID
        //   localId: 12, //ID
        //   isCheckedForServer: false, //
        //   isCheckedForLocal: false, //
        //   remoteFeature: {},
        //   localFeature: {},
        //   action:'', //add,modify,delete
        // }
      ], //
      remoteList: [], //
      localList: [], //
      curAttris: [
        // {
        //   name: "", //
        //   localValue: "", //
        //   remoteValue:"", //
        //   isSame: true //,true
        // }
      ], //
      curDtName: "", //,
      curDtId: null, //ID
      tempVecLayer: null //
    };
  },
  // data
  computed: {
    ...mapState(["access_token", "host"])
  },
  //
  methods: {
    /****
     * @description 
     */
    handleChangeDraw() {
      let features = this.tempVecLayer ? this.tempVecLayer.getFeatures() : [];
      if (features && features.length > 0) {
        features.reverse();
        this.tempVecLayer.setFeatures(features);
        this.tempVecLayer.draw();
      }
    },
    /***
     * @description 
     */
    handleClose2() {
      this.visible2 = false;
    },
    zoomin() {
      this.tMap && this.tMap.zoomIn();
    },
    zoomout() {
      this.tMap && this.tMap.zoomOut();
    },
    /****
     * @description 
     */
    handleSave() {
      let len = this.list ? this.list.length : 0;
      if (!len) return false;
      let bRet = this.isConflict();
      if (bRet) {
        errorMsg("");
        return false;
      }
      this.visible2 = true;
    },
    /****
     * @description 
     */
    handleSaveOk() {
      let features = this.combineConflictFeatures();
      this.$emit("compare-features", features);
      this.visible2 = false;
      this.clearData();
    },
    /****
     * @description 
     * @returns {Boolean} true
     */
    isConflict() {
      let temp =
        this.list &&
        this.list.find(
          item =>
            item.isCheckedForServer == false && item.isCheckedForLocal == false
        );
      return temp ? true : false;
    },
    /****
     * @description 
     */
    combineConflictFeatures() {
      let result = [];
      if (
        this.list &&
        this.list.length > 0 &&
        this.localList &&
        this.localList.length > 0
      ) {
        let len = this.localList.length;
        for (var i = 0; i < len; i++) {
          let temp = this.localList[i];
          let features = temp ? temp.features : null;
          let count = features ? features.length : 0;
          let combineFeatures = []; //
          for (var j = 0; j < count; j++) {
            let feature = features[j];
            if (feature) {
              let tempItem = this.list.find(
                item =>
                  item.dtId == temp.dataset_id && item.localId == feature.id
              ); //
              if (tempItem) {
                //,
                if (tempItem.isCheckedForLocal) {
                  if (
                    tempItem.remoteFeature &&
                    tempItem.remoteFeature.version == -1
                  ) {
                    combineFeatures.push({
                      ...tempItem.localFeature,
                      force: true,
                      version: 0
                    });
                  } else {
                    combineFeatures.push({
                      ...tempItem.localFeature,
                      version: tempItem.remoteFeature.version
                    });
                  }
                }
              } else {
                combineFeatures.push(feature);
              }
            }
          }
          if (combineFeatures.length > 0) {
            result.push({
              dataset_id: temp.dataset_id,
              features: combineFeatures
            });
          }
        }
      }
      return result;
    },
    /****
     * @description 
     */
    handleRowClick(currentRow, index) {
      this.bVisbileAttri = false;
      this.curDtName = currentRow.layerName;
      this.curDtId = currentRow.dtId;
      let { localFeature, remoteFeature } = currentRow;
      //
      this.curAttris = [];
      if (localFeature && remoteFeature) {
        //
        let localProperties = localFeature.properties;
        let remoteProperties = remoteFeature.properties;
        if (remoteFeature.version == -1) {
          //
          //
          if (localProperties) {
            for (var key in localProperties) {
              this.curAttris.push({
                name: key,
                localValue: (localProperties[key] == null
                  ? "NULL"
                  : localProperties[key]
                ).toString(),
                remoteValue: (localProperties[key] == null
                  ? "NULL"
                  : localProperties[key]
                ).toString(),
                isSame: true
              });
            }
          }
        } else {
          if (localProperties && remoteProperties) {
            for (var key in localProperties) {
              let value1 = (localProperties[key] == null
                ? "NULL"
                : localProperties[key]
              ).toString();
              let value2 = (remoteProperties[key] == null
                ? "NULL"
                : remoteProperties[key]
              ).toString();
              this.curAttris.push({
                name: key,
                localValue: value1,
                remoteValue: value2,
                isSame: value1 == value2 ? true : false
              });
            }
          }
        }
      }
      //
      this.clearTempVectorLayer();
      if (localFeature && remoteFeature) {
        let localGeoFeature = null;
        let remoteGeoFeature = null;
        let localNodeFeatures = null;
        let remoteNodeFeatures = null;
        let localColor = new L.SymbolLib.RGBAColor(255, 0, 0, 255);
        let remoteColor = new L.SymbolLib.RGBAColor(0, 0, 255, 255);
        if (remoteFeature.version == -1) {
          localGeoFeature = L.SE.SeFeature.createGeoJSONFeatureFromGeoJSON(
            localFeature
          );
          remoteGeoFeature = localGeoFeature.clone();
        } else {
          localGeoFeature = L.SE.SeFeature.createGeoJSONFeatureFromGeoJSON(
            localFeature
          );
          remoteGeoFeature = L.SE.SeFeature.createGeoJSONFeatureFromGeoJSON(
            remoteFeature
          );
        }
        if (remoteGeoFeature && localGeoFeature) {
          let tempList = util.makeAddDatasets(
            [currentRow.dtId],
            this.$symbolColors
          );
          this.tMap && this.tMap.removeAllVectorLayer();
          this.tMap &&
            this.tMap.addCloudLayersExt(tempList, {
              success: () => {
                let dt = this.tMap.findDatasetById(currentRow.dtId);
                //
                localGeoFeature.setProjInfo(dt.getProjInfo());
                localGeoFeature.setProj(dt.getProj());
                let localSymbol = this.getDefaultSymbol(
                  localGeoFeature.getType(),
                  true
                );
                localGeoFeature.setSymbol(localSymbol);
                //
                if (
                  localGeoFeature.getType() != L.SE.FeatureType.POINT &&
                  localGeoFeature.getType() != L.SE.FeatureType.MULTIPOINT
                ) {
                  localNodeFeatures = localGeoFeature.convertToPointFeatures();
                  localNodeFeatures &&
                    localNodeFeatures.forEach(item => {
                      let localSymbol1 = this.getDefaultSymbol2(
                        item.getType(),
                        localColor
                      );
                      item.setSymbol(localSymbol1);
                    });
                }

                //
                remoteGeoFeature.setProjInfo(dt.getProjInfo());
                remoteGeoFeature.setProj(dt.getProj());
                let remoteSymbol = this.getDefaultSymbol(
                  remoteGeoFeature.getType(),
                  false
                );
                remoteGeoFeature.setSymbol(remoteSymbol);
                //
                if (
                  remoteGeoFeature.getType() != L.SE.FeatureType.POINT &&
                  remoteGeoFeature.getType() != L.SE.FeatureType.MULTIPOINT
                ) {
                  remoteNodeFeatures = remoteGeoFeature.convertToPointFeatures();
                  remoteNodeFeatures &&
                    remoteNodeFeatures.forEach(item => {
                      let remoteSymbol1 = this.getDefaultSymbol2(
                        item.getType(),
                        remoteColor
                      );
                      item.setSymbol(remoteSymbol1);
                    });
                }

                this.tempVecLayer = new L.SE.SeTempLayer();
                this.tempVecLayer.setFeatures([
                  remoteGeoFeature,
                  ...remoteNodeFeatures,
                  localGeoFeature,
                  ...localNodeFeatures
                ]);
                if (this.tMap) {
                  this.tMap.addLayer(this.tempVecLayer);
                }
                this.tempVecLayer.draw();
                this.setMapBounds(
                  [remoteGeoFeature, localGeoFeature],
                  dt.getProj()
                );
              },
              error: err => {
                errorMsg(err || this.$t("message.sdkInnerError"));
              }
            });
        }
      }
    },
    /***
     * @description 
     */
    setMapBounds(features, proj) {
      let bounds = null;
      features &&
        features.forEach((item, index) => {
          if (index == 0) {
            bounds = item.getBounds();
          } else {
            bounds.add(item.getBounds());
          }
        });
      if (bounds && this.tMap && proj) {
        if (proj) {
          let p1 = L.SE.UtilTool.projTransform(
            L.point(bounds.getLeft(), bounds.getBottom()),
            proj,
            L.CRS.EPSG4326
          );
          let corner1 = L.latLng(p1.y, p1.x);
          let p2 = L.SE.UtilTool.projTransform(
            L.point(bounds.getRight(), bounds.getTop()),
            proj,
            L.CRS.EPSG4326
          );
          let corner2 = L.latLng(p2.y, p2.x);
          let locBounds = L.latLngBounds(corner1, corner2);
          this.tMap.fitBounds(locBounds, { maxZoom: 17 });
        } else {
          let corner1 = L.latLng(bounds.getBottom(), bounds.getLeft());
          let corner2 = L.latLng(bounds.getTop(), bounds.getRight());
          let locBounds = L.latLngBounds(corner1, corner2);
          this.tMap.fitBounds(locBounds, { maxZoom: 17 });
        }
      }
    },
    /****
     * @description 
     */
    openMap() {
      let temp = findPbMap();
      if (!this.tMap) {
        this.tMap = L.map("tmap", {
          crs: temp.crs.value,
          minZoom: 3,
          maxZoom: 22,
          zoom: 10,
          center: [31, 121],
          zoomControl: false,
          isCloudVer: true,
          attributionControl: false
        });
      } else {
        this.tMap.options.crs = temp.crs.value;
      }
      this.clearTileLayers();
      temp.layers.forEach(ele => {
        this.array.push(L.PBM.createTileLayer(ele, temp.name, { gray: 1 }));
      });
      this.addTileLayers();
      this.seManager = new L.SE.SeManager(this.tMap, {
        serviceUrl: this.host,
        token: this.access_token
      });
      this.tMap._onResize();
    },
    /***
     * @description 
     */
    clearTileLayers() {
      if (this.tMap) {
        this.array &&
          this.array.forEach(ele => {
            if (this.tMap.hasLayer(ele)) {
              this.tMap.removeLayer(ele);
            }
          });
      }
      this.array = [];
    },
    /***
     * @description 
     */
    addTileLayers() {
      if (this.tMap) {
        this.array &&
          this.array.forEach(ele => {
            if (!this.tMap.hasLayer(ele)) {
              this.tMap.addLayer(ele);
            }
          });
      }
    },
    /****
     * @description 
     */
    clearTempVectorLayer() {
      if (this.tMap && this.tempVecLayer) {
        if (this.tMap.hasLayer(this.tempVecLayer)) {
          this.tMap.removeLayer(this.tempVecLayer);
        }
      }
      this.tempVecLayer = null;
    },
    /****
     * @description 
     */
    handleLocalClick(row, index) {
      row.isCheckedForLocal = !row.isCheckedForLocal;
      if (row.isCheckedForLocal) {
        row.isCheckedForServer = false;
      }
      this.$set(this.list, index, row);
    },
    /****
     * @description 
     */
    handleRemoteClick(row, index) {
      row.isCheckedForServer = !row.isCheckedForServer;
      if (row.isCheckedForServer) {
        row.isCheckedForLocal = false;
      }
      this.$set(this.list, index, row);
    },
    /****
     * @description 
     * @param {Array} conflictDts  ,[{dataset_id:ID,features:[]}]
     * @param {Array} locaDts  [{dataset_id:ID,features:[]}]
     */
    showDialog(conflictDts, locaDts) {
      this.clearData();
      this.visible = true;
      this.openMap();
      this.remoteList = conflictDts;
      this.localList = locaDts;
      this.list = [];
      //
      let id = 0;
      this.remoteList &&
        this.remoteList.forEach(item => {
          if (item.features && item.features.length > 0) {
            let local =
              this.localList &&
              this.localList.find(va => item.dataset_id == va.dataset_id);
            if (local && local.features) {
              item.features.forEach(tFeature => {
                let localFeature = local.features.find(
                  tFeature2 => tFeature.id == tFeature2.id
                );
                if (localFeature) {
                  let tmpFeature = {
                    ...tFeature
                  };
                  if (tFeature.version == -1) {
                    //
                    tmpFeature.geometry = localFeature.geometry;
                    tmpFeature.properties = localFeature.properties;
                  } else {
                    if (tFeature.geometry) {
                      tmpFeature.geometry = JSON.parse(tFeature.geometry);
                    }
                    if (tFeature.properties) {
                      tmpFeature.properties = JSON.parse(tFeature.properties);
                    }
                  }
                  this.list.push({
                    id: ++id,
                    dtId: item.dataset_id, //ID
                    layerName: item.dataset_name, //
                    remoteId: localFeature.id, //ID
                    localId: localFeature.id, //ID
                    isCheckedForServer: false, //
                    isCheckedForLocal: false, //
                    remoteFeature: tmpFeature,
                    localFeature: localFeature,
                    action: this.getEidtAction(localFeature.action)
                  });
                }
              });
            }
          }
        });
    },
    getEidtAction(action) {
      return action == "add" ? "" : action == "modify" ? "" : "";
    },
    /***
     * @description 
     */
    clearData() {
      this.visible = false;
      this.bVisbileAttri = false;
      this.list = [];
      this.remoteList = []; //
      this.localList = []; //
      this.curAttris = []; //
      this.curDtName = ""; //,
      this.curDtId = null; //ID
      this.clearTempVectorLayer();
      this.seManager = null;
      this.tMap && this.tMap.removeAllVectorLayer();
    },
    /****
     * @description IDID
     * @param {Number} dtId ID
     * @param {Number} fId ID
     * @param {Boolean} bLocal true 
     */
    findFeatureById(dtId, fId, bLocal) {
      let list = null;
      if (bLocal) {
        list = this.this.localList;
      } else {
        list = this.remoteList;
      }
      let temp = list && list.find(item => item.dataset_id == dtId);
      let tFeature =
        temp && temp.features && temp.features.find(item => item.id == fId);
      return tFeature;
    },
    /****
     * @description 
     */
    showAttriDialog() {
      this.bVisbileAttri = true;
    },
    /****
     * @description 
     */
    handleAttriClose() {
      this.bVisbileAttri = false;
    },
    /***
     *@description 
     */
    handleClose() {
      this.clearData();
      this.visible = false;
      this.bVisbileAttri = false;
    },
    /***
     * @description 
     */
    rowClassName(row, index) {
      if (index % 2 === 0) {
        return "table-even";
      } else {
        return "table-odd";
      }
    },
    /**
     * 
     * @param featureType
     */
    getDefaultSymbol(featureType, bLocal) {
      let symbol = null;
      let color = bLocal
        ? new L.SymbolLib.RGBAColor(255, 0, 0, 255)
        : new L.SymbolLib.RGBAColor(0, 0, 255, 255);
      switch (featureType) {
        case L.SE.FeatureType.POINT:
        case L.SE.FeatureType.MULTIPOINT: {
          symbol = new L.SymbolLib.SimpleMarkerSymbol({
            color: color
          });
          break;
        }
        case L.SE.FeatureType.LINE: {
          symbol = new L.SymbolLib.SimpleLineSymbol({
            width: 2,
            color: color
          });
          break;
        }
        case L.SE.FeatureType.POLYGON:
        case L.SE.FeatureType.MULTIPOLYGON: {
          symbol = new L.SymbolLib.SimpleFillSymbol({
            color: new L.SymbolLib.RGBAColor(203, 220, 245, 255),
            hasOutline: true,
            outlineSymbol: new L.SymbolLib.SimpleLineSymbol({
              width: 2,
              color: color
            })
          });
          break;
        }
        default: {
          break;
        }
      }
      return symbol;
    },
    /**
     * 
     * @param featureType
     */
    getDefaultSymbol2(featureType, color) {
      let symbol = null;
      color = color || new L.SymbolLib.RGBAColor(255, 0, 0, 255);
      switch (featureType) {
        case L.SE.FeatureType.POINT:
        case L.SE.FeatureType.MULTIPOINT: {
          symbol = new L.SymbolLib.SimpleMarkerSymbol({
            color: color
          });
          break;
        }
        case L.SE.FeatureType.LINE: {
          symbol = new L.SymbolLib.SimpleLineSymbol({
            width: 2,
            color: color
          });
          break;
        }
        case L.SE.FeatureType.POLYGON:
        case L.SE.FeatureType.MULTIPOLYGON: {
          symbol = new L.SymbolLib.SimpleFillSymbol({
            color: new L.SymbolLib.RGBAColor(203, 220, 245, 255),
            hasOutline: true,
            outlineSymbol: new L.SymbolLib.SimpleLineSymbol({
              width: 2,
              color: color
            })
          });
          break;
        }
        default: {
          break;
        }
      }
      return symbol;
    }
  }
};
</script>
<style lang='less' scoped>
//@import url(); css
.c-f-row-item-2 {
  padding: 20px 0px 4px 0px;
}
.c-f-containter2 {
  display: flex;
  height: 344px;
}
.c-f-containter2 .left {
  flex: 1;
}
.c-f-containter2 .right {
  width: 524px;
  margin-left: 10px;
  position: relative;
  border: 1px solid #d3dceb;
  border-radius: 2px;
}
.c-f-left-top {
  width: 30px;
  height: 60px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 1px 2px 4px 0px rgba(0, 0, 0, 0.3);
  border-radius: 2px;
  position: absolute;
  left: 5px;
  top: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8px;
  justify-content: space-between;
}
.c-f-left-top > img {
  border: 1px dashed;
  width: 14px;
  height: 14px;
  cursor: pointer;
}
.c-f-left-top-attr {
  width: 30px;
  height: 30px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 1px 2px 4px 0px rgba(0, 0, 0, 0.3);
  border-radius: 2px;
  position: absolute;
  left: 5px;
  top: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.c-f-attr {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(242, 246, 249, 1);
