<!--  -->
<template>
  <div>
    <div id="map"></div>
    <SingleAttribute ref="singleAttribute"></SingleAttribute>
  </div>
</template>
<script>
import { mapState } from "vuex";
import SingleAttribute from "./SingleAttribute";
import util from "../utils/util";
import { successMsg, errorMsg } from "../utils/message";
import { LK_EVENT, findPbMap, BD09_PB_MAP } from "../const/enum";

export default {
  //import
  components: { SingleAttribute },
  data() {
    //
    return {
      tileLayer: null,
      /***
       * @description SDK
       * @param{Map}
       */
      map: null,
      /***
       * @description 
       * @param{SeManager}
       */
      seManager: null,
      array: []
    };
  },
  // data
  computed: {
    ...mapState([
      "access_token",
      "host",
      "datasets",
      "userName",
      "curPbMap",
      "gray"
    ])
  },
  //data
  watch: {
    curPbMap(newVal) {
      this.changeMap();
    }
  },
  //
  methods: {
    /***
     * @description
     */
    initMap() {
      // var corner1 = L.latLng(-90, -180),
      //   corner2 = L.latLng(90, 180),
      //   bounds = L.latLngBounds(corner1, corner2);
      let temp = findPbMap(this.curPbMap);
      if (!this.map) {
        this.map = L.map("map", {
          crs: temp.crs.value,
          minZoom: 3,
          maxZoom: 22,
          zoom: 10,
          center: [31, 121],
          zoomControl: false,
          isCloudVer: true,
          attributionControl: false
          //maxBounds: bounds
        });
      } else {
        this.map.options.crs = temp.crs.value;
      }
      this.clearTileLayers();
      temp.layers.forEach(ele => {
        this.array.push(L.PBM.createTileLayer(ele, temp.name));
      });
      this.addTileLayers();
      this.seManager = new L.QGIS.SeManager(this.map, {
        serviceUrl: this.host,
        token: this.access_token
      });
      let dtId = util.getQueryString("id");
      if (dtId /*this.datasets && this.datasets.length > 0*/) {
        let list = util.makeAddDatasets(
          [dtId] /*this.datasets*/,
          this.$symbolColors
        );
        if (list && list.length > 0) {
          this.map.addCloudLayersExt(list, {
            success: () => {
              let result = [];
              this.$bus.emit(LK_EVENT.ADD_DATASETS, result);
            },
            error: err => {
              errorMsg(err || this.$t("message.sdkInnerError"));
            }
          });
        }
      }

      this.$store.commit("setMap", this.map);
      this.$http.getCityByGaoDe(result => {
        // let nc = result && result.bounds ? result.bounds.oc : null;
        // if (nc) {
        //   this.map.flyTo([nc.lat, nc.lng], 10);
        // }
        this.$store.commit("setCity", result);
      });
      this.seManager.off("identify_end", this.identifyEnd);
      this.seManager.on("identify_end", this.identifyEnd);
      this.map.on("draw_end_new", e => {
        if (!e.data) {
          this.$refs.singleAttribute.closeDialog();
        } else {
          this.$refs.singleAttribute.showDialog(e.data);
        }
      });
      this.map.on("edit_feature_attr", e => {
        if (!e.data) {
          this.$refs.singleAttribute.closeDialog();
        } else {
          this.$refs.singleAttribute.showDialog(e.data);
        }
      });
      this.map.off("view-result", this.viewResult);
      this.map.on("view-result", this.viewResult);
    },
    viewResult(e) {
      this.$bus.emit(LK_EVENT.VIEW_STATICS, e.data);
    },
    /****
     * @description 
     */
    identifyEnd(e) {
      if (!e.data) {
        this.$refs.singleAttribute.closeDialog();
      } else {
        let editLayer = this.map.getEditLayer();
        let type = 0; //0,1,2
        let data = e.data;
        data.type = type;
        this.$refs.singleAttribute.showDialog(data);
      }
    },
    /***
     * @description 
     */
    clearTileLayers() {
      if (this.map) {
        this.array &&
          this.array.forEach(ele => {
            if (this.map.hasLayer(ele)) {
              this.map.removeLayer(ele);
            }
          });
      }
      this.array = [];
    },
    /***
     * @description 
     */
    addTileLayers() {
      if (this.map) {
        this.array &&
          this.array.forEach(ele => {
            if (!this.map.hasLayer(ele)) {
              this.map.addLayer(ele);
            }
          });
      }
    },
    /***
     * @description 
     */
    refresh() {
      this.map && this.map._onResize();
    },
    /***
     * @description 
     */
    changeTileMap(bVisible) {
      if (this.array) {
        this.array.forEach(item => {
          if (bVisible) {
            if (!this.map.hasLayer(item)) {
              this.map.addLayer(item);
            }
          } else {
            if (this.map.hasLayer(item)) {
              this.map.removeLayer(item);
            }
          }
        });
      }
      // if (this.tileLayer) {
      //   if (bVisible) {
      //     if (!this.map.hasLayer(this.tileLayer)) {
      //       this.map.addLayer(this.tileLayer);
      //     }
      //   } else {
      //     if (this.map.hasLayer(this.tileLayer)) {
      //       this.map.removeLayer(this.tileLayer);
      //     }
      //   }
      // }
    },
    /***
     * @description 
     */
    changeTileLayers(gray) {
      if (this.array) {
        this.array.forEach(item => {
          item.setGray(gray);
        });
      }
    },
    changeMap() {
      this.clearTileLayers();
      let temp = findPbMap(this.curPbMap);
      let oldCenter = this.map.getCenter();
      let oldZoom = this.map.getZoom();
      //console.log(this.map.getCenter());
      //this.map.getZoom();
      if (temp && this.map) {
        this.map.options.crs = temp.crs.value;
        if (BD09_PB_MAP.indexOf(temp.name) >= 0) {
          this.map.options.maxZoom = 18;
        } else {
          this.map.options.maxZoom = 22;
        }
        temp.layers.forEach(ele => {
          this.array.push(
            L.PBM.createTileLayer(ele, temp.name, { gray: this.gray })
          );
        });
        this.addTileLayers();
        // this.$http.getCityByGaoDe(result => {
        //   let oc = result && result.bounds ? result.bounds.oc : null;
        //   if (oc) {
        //     this.map.flyTo([oc.lat, oc.lng], 10);
        //   }
        //   this.$store.commit("setCity", result);
        // });
        //this.map.refresh();
        this.map.setView(oldCenter, oldZoom);
      }
    }
  },
  // - DOM
  mounted() {
    this.$nextTick(() => {
      //
      if (process.env.NODE_ENV == "development") {
        let config = {
          access_token: "pk.2a18c2a0ea1511eaa84bad503b828ee6",
          host: "http://127.0.0.1"
        };
        this.$store.commit("setApiConfig", config);
        this.initMap();
      } else {
        //
        let no_action = util.getQueryString("no_action");
        if (no_action != null) {
          //
          let bDev = process.env.NODE_ENV == "development";
          let config2 = {
            access_token: "tk.19fb054e94d0284966a416a852a6924a",
            host: bDev ? "http://127.0.0.1" : "http://127.0.0.1"
          };
          this.$store.commit("setApiConfig", config2);
          this.initMap();
        } else {
          let options = {
            success: result => {
              this.$store.commit("setApiConfig", result);
              this.initMap();
            }
          };
          this.$http.getApiConfig(options);
        }
      }
    });
  }
};
</script>
<style lang='less' scoped>
//@import url(); css
#map {
  width: 100%;
  height: 100%;
}
</style>


