// proj4.js  proj4leaflet.js x_0=1440 +y_0=255 +datum=WGS84 +units=m +no_defs', {
L.CRS.Baidu = new L.Proj.CRS('EPSG:3395', '+proj=merc +lon_0=0 +k=1 +x_0=1440 +y_0=255 +datum=WGS84 +units=m +no_defs', {
    resolutions: (() => {
        let level = 19 //19
        var res = [];
        res[0] = Math.pow(2, 18);//18
        for (var i = 1; i < level; i++) {
            res[i] = Math.pow(2, (18 - i)) //18
        }
        return res;
    })(),
    origin: [0, 0],
    bounds: L.bounds([20037508.342789244, 0], [0, 20037508.342789244])
});
