//         var tileSize = 256; //(256*256)
//         var initialResolution = 2 * Math.PI * 6378137 / tileSize ;//6378137
//         var res = [];
//         for (var i = 0; i < level; i++) {
//             res[i] =1/Math.pow(2, i) //zoom(0-21)
//         }
//         return res;
//     })(),
//     // transformation: new L.Transformation(1 / 40075016.68558, 0.5, -1 / 40075016.68558, 0.5),
//     bounds: L.bounds([-20037508.34279, -20037508.34279], [20037508.34279, 20037508.34279])
// });
L.tileLayer.baidu = function (option) {
    option = option || {};
    var layer;
    var subdomains = '0123456789';
    switch (option.layer) {
        //
        case "vec":
        default:
            //http://online{s}.map.bdimg.com/tile/?qt=tile&x={x}&y={y}&z={z}&styles=pl&udt=20150518
            //'http://online{s}.map.bdimg.com/tile/?qt=tile&x={x}&y={y}&z={z}&styles=pl&b=0&limit=60&scaler=1&udt=20170525'
            layer = L.tileLayer('http://online{s}.map.bdimg.com/onlinelabel/?qt=tile&x={x}&y={y}&z={z}&styles=' + (option.bigfont ? 'ph' : 'pl') + '&scaler=1&p=1', {
                name: option.name, subdomains: subdomains, tms: true, ...option
            });
            break;
        case "img_d":
            layer = L.tileLayer('http://shangetu{s}.map.bdimg.com/it/u=x={x};y={y};z={z};v=009;type=sate&fm=46', {
                name: option.name, subdomains: subdomains, tms: true, ...option
            });
            break;
        case "img_z":
            layer = L.tileLayer('http://online{s}.map.bdimg.com/tile/?qt=tile&x={x}&y={y}&z={z}&styles=' + (option.bigfont ? 'sh' : 'sl') + '&v=020', {
                name: option.name, subdomains: subdomains, tms: true, ...option
            });
            break;

        case "custom"://Custom 
            //dark,midnight,grayscale,hardedge,light,redalert,googlelite,grassgreen,pink,darkgreen,bluish
            option.customid = option.customid || 'midnight';
            layer = L.tileLayer('http://api{s}.map.bdimg.com/customimage/tile?&x={x}&y={y}&z={z}&scale=1&customid=' + option.customid, {
                name: option.name, subdomains: "012", tms: true, ...option
            });
            break;

        case "time"://
            var time = new Date().getTime();
            layer = L.tileLayer('http://its.map.baidu.com:8002/traffic/TrafficTileService?x={x}&y={y}&level={z}&time=' + time + '&label=web2D&v=017', {
                name: option.name, subdomains: subdomains, tms: true, ...option
            });
            break;

        //
        case "img":
            layer = L.layerGroup([
                L.tileLayer.baidu({ name: "", layer: 'img_d', bigfont: option.bigfont }),
                L.tileLayer.baidu({ name: "", layer: 'img_z', bigfont: option.bigfont })
            ]);
            break;
    }
    return layer;
};


