import axios from 'axios';
import { formatServiceUrl } from './config'
import { errorMsg, successMsg } from '../utils/message'
import { getToken } from '@/utils/storage';
import store from '@/store';
import util from '@/utils/util';
import il8n from '../i18n';
axios.defaults.headers = {
	'Content-Type': 'application/json; charset=utf-8'
};
let baseURL = util.getBaseUrl();
const token = getToken();
const SIGNIN = baseURL + '/auth/login'; //
/**
* @fileOverview 
* @author LJ
* @version 0.1
*/
/***
 * @description 
 * @param{Object} resp response
 * @param{Function} success 
 */
const handleSucess = (resp, success, error) => {
	switch (resp.status) {
		case 0://
			{
				success && success(resp.result);
				break;
			}
		default:
			{
				if (!error) {
					errorMsg(resp.message);
				}
				else {
					error(resp.message);
				}
			}
	}
}
/***
 * @description 
 * @param{Object} err 
 */
const handleError = (err, error) => {
	if (err && err.response) {
		switch (err.response.status) {
			case 400:
				err.message = ''
				break
			case 401:
				err.message = ''
				location.href = SIGNIN;
				break
			case 403:
				err.message = ''
				break
			case 404:
				err.message = ''
				break
			case 408:
				err.message = ''
				break
			case 500:
				err.message = ''
				break
			case 501:
				err.message = ''
				break
			case 502:
				err.message = ''
				break
			case 503:
				err.message = ''
				break
			case 504:
				err.message = ''
				break
			case 505:
				err.message = 'HTTP'
				break
			default:
				{
					if (!err.message) {
						err.message = il8n.t('message.unknowError');
					}
					break;
				}
		}
		if (!error) {
			errorMsg(err.message);
		} else {
			error(err.message);
		}
	}
	else if (err && err.message) {
		if (!error) {
			errorMsg(err.message);
		} else {
			error(err.message);
		}
	}
	else {
		if (!error) {
			errorMsg(il8n.t('message.unknowError'));
		} else {
			error(il8n.t('message.unknowError'));
		}
	}
}
/***
 * @description 
 * @param{String} status 
 * @param{Object|String} result 
 * @param{Function} success 
 */
const handleGaoDeService = (status, result, success) => {
	if (status === "complete") {
		success && success(result);
	}
	else if (status === "no_data")//
	{
		successMsg(il8n.t('message.queryNoData'));
		success && success(null);
	}
	else if (status === "error") {
		errorMsg(result);
	} else {
		errorMsg("undefined error");
	}
}
