export default {
	// /***
	//  * 
	//  * @param{Object} options:{params,success}
	//  */
	// test(options) {
	// 	if (!options) return false;
	// 	let url = 'http://127.0.0.1:8080/api/v1/test';
	// 	const { params, success } = options;
	// 	axios.get(url, { params }).then(response => {
	// 		handleSucess(response.data, success);
	// 	}).catch(err => {
	// 		handleError(err);
	// 	})
	// },
	/***
	 * @description SE API 
	 * @param {Object} options:{success}
	 */
	getApiConfig(options) {
		if (!options) return false;
		let url = baseURL + `/tokens/v1`;
		const { success } = options;
		let headers = {
			authorization: `Bearer ${token}`
		}
		axios.get(url, { headers: headers }).then(response => {
			handleSucess(response.data, success);
		}).catch(err => {
			handleError(err);
		})
	},
	/***
	 * @description [GET]
	 * @param{Object} options:{params,success}
	 */
	getDatasetList(options) {
		if (!options) return false;
		let url = formatServiceUrl(`datasets`, true);
		const { params, success } = options;
		axios.get(url, {
			params: params
		}).then(response => {
			handleSucess(response.data, success);
		}).catch(err => {
			handleError(err);
		})
	},
	/***
	 * @description [POST]
	 * @param{Object} options:{params,success}
	 */
	createDataset(options) {
		if (!options) return false;
		let user = store.state.userName;
		let url = formatServiceUrl(`datasets`, true);
		const { params, success } = options;
		axios.post(url, params).then(response => {
			handleSucess(response.data, success);
		}).catch(err => {
			handleError(err);
		})
	},
	/***
	 * @description [GET]
	 * @param{Object} options:{id:id,success}
	 * @example {id:ID}
	 */
	getDatasetMeta(options) {
		if (!options) return false;
		const { id, success } = options;
		let url = formatServiceUrl(`dataset/${id}`, true);
		axios.get(url).then(response => {
			handleSucess(response.data, success);
		}).catch(err => {
			handleError(err);
		})
	},
	/***
	 * @description [POST]
	 * @param{Object} options:{id:id,success,params}
	 */
	queryDatasetBySQL(options) {
		if (!options) return false;
		const { success, params } = options;
		let url = formatServiceUrl(`recordsets`, true);
		axios.post(url, params).then(response => {
			handleSucess(response.data, success);
		}).catch(err => {
			handleError(err);
		})
	},
	/****
	 * @description IP
	 * @param {Function} callback
	 */
	getCityByGaoDe(callback) {
		var citySearch = new AMap.CitySearch();
		citySearch.getLocalCity((status, result) => {
			handleGaoDeService(status, result, callback);
		});
	},
	/****
	 * @description 
	 * @param {Object} options:{city:,pageSize:,pageIndex:,keyword:String,success}
	 */
	searchPoiByGaoDe(options) {
		const { city, pageSize, pageIndex, keyword, success } = options;
		let placeSearch = new AMap.PlaceSearch({
			city: city,
			pageSize: pageSize,
			pageIndex: pageIndex,
			citylimit: true
		});
		//
		placeSearch.search(keyword, (status, result) => {
			handleGaoDeService(status, result, success);
		});
	},
	/***
	 * @description [POST]
	 * @param{Object} options:{params,success}
	 */
	saveAllDatasetFeatures(options) {
		if (!options) return false;
		let url = formatServiceUrl("features", true);
		const { params, success } = options;
		axios.post(url, params).then(response => {
			handleSucess(response.data, success);
		}).catch(err => {
			handleError(err);
		})
	},

	/****
	 * @description [POST]
	 * @param{Object} options:{params,success}
	 */
	queryKeyVal(options) {
		// if (!options) return false;
		// const { params, id, success } = options;
		// let url = formatServiceUrl(`dataset/${id}/distinct`, true);
		// axios.post(url, params, { headers: { 'Content-Type': 'text/plain' } }).then(response => {
		// 	handleSucess(response.data, success);
		// }).catch(err => {
		// 	handleError(err);
		// })
		this.queryDatasetBySQL(options);
	},
	/****
	 * @description 
	 * @param{Object} options:{params,success}
	 */
	queryAttributesByFilters(options) {
		if (!options) return false;
		const { params, success } = options;
		let url = formatServiceUrl(`dataset/query`, true);
		axios.post(url, params, { headers: { 'Content-Type': 'text/plain' } }).then(response => {
			handleSucess(response.data, success);
		}).catch(err => {
			handleError(err);
		})
	},
	/****
	 * @description [GET]
	 * @param {Object} options:{success}
	 */
	getSysCrs(options) {
		if (!options) return false;
		const { success, params } = options;
		let url = formatServiceUrl(`srs`, true);
		axios.get(url, { params }).then(response => {
			handleSucess(response.data, success);
		}).catch(err => {
			handleError(err);
		})
	},
	/***
	 * @description [POST]
	 * @param{Object} options:{params,success,id}
	 */
	addFields(options) {
		if (!options) return false;
		const { params, id } = options;
		let url = formatServiceUrl(`dataset/${id}/field`, true);
		return axios.post(url, params);
	},
	/***
	 * @description [delete]
	 * @param{Object} options:{success,id,name}
	 */
	deleteField(options) {
		if (!options) return false;
		const { id, name, success } = options;
		let url = formatServiceUrl(`dataset/${id}/field/${name}`, true);
		return axios.delete(url);
	},
	/***
	 * @description 
	 * @param {Object} options:{params,success,id,error}
	 */
	updateDatasetFields(options) {
		if (!options) return false;
		let { params, success, id, error } = options;
		let addFields = params.add;
		let deleteFields = params.delete;
		let array = [];
		if (addFields && addFields.length > 0) {
			array.push(this.addFields({ params: { field_list: addFields }, id: id }));
		}
		if (deleteFields && deleteFields.length > 0) {
			let len = deleteFields.length;
			for (let i = 0; i < len; i++) {
				array.push(this.deleteField({ id: id, name: deleteFields[i].name }));
			}
		}
		axios.all(array).then(axios.spread((...result) => {
			handleSucess(result[0].data, success, error);
		})).catch(err => {
			handleError(err, error);
		});
	}
}


