<!--  -->
<template>
  <div class="exp-containter cus-vec-edit">
    <div class="exp-f-header">
      <p></p>
      <Select v-model="op" style="width:124px; text-align:left;">
        <Option
          v-for="item in logicalOperatorList"
          :value="item.value"
          :key="item.value"
        >{{ item.label }}</Option>
      </Select>
    </div>
    <ul>
      <li v-for="(item,index) in filters" :key="index">
        <i class="iconfont iconyichu- i-delete" @click="handleDelete(index)"></i>
        <!---->
        <Select
          v-model="item.name"
          class="exp-f-item"
          style="width:70px;text-align:left;"
          :title="item.name"
          @on-change="handleChangeFieldName(index)"
        >
          <Option v-for="item2 in selfFields" :value="item2.name" :key="item2.name">{{ item2.name }}</Option>
        </Select>
        <!---->
        <Select
          v-model="item.operator"
          style="width:54px;text-align:left;"
          class="exp-f-item"
          :title="item.operator"
          @on-change="handleChangeOp(index)"
        >
          <Option
            v-for="item2 in getOpList(item.name)"
            :value="item2.value"
            :key="item2.value"
          >{{ item2.label }}</Option>
        </Select>
        <!---->
        <Select
          @on-change="handleChangeValue(index)"
          v-if="isBoolType(item.name) && item.operator!='IsNull'"
          v-model="item.value"
          :title="item.value"
          class="exp-f-item"
          style="width:102px;text-align:left;"
          :placeholder="getMsg(item)"
        >
          <Option v-for="t in bools" :value="t.value" :key="t.value">{{ t.label }}</Option>
        </Select>
        <Input
          v-else
          class="exp-f-item"
          style="width:102px;"
          :title="item.value"
          v-model="item.value"
          :placeholder="getMsg(item)"
          @on-change="handleChangeValue(index)"
          :disabled="item.operator=='IsNull'"
        />
        <i
          v-if="item.operator=='IsNull'"
          style="padding-left:10px;cursor: not-allowed; color:#ccc;"
          class="iconfont icontubiaon- i-tubiaon"
        ></i>
        <Poptip :transfer="true" placement="right" width="320" v-model="item.visible" v-else>
          <i
            style="padding-left:10px;"
            class="iconfont icontubiaon- i-tubiaon i-attr"
            @click="chooseValue(item,index)"
          ></i>

          <div slot="content" style="height: 410px; overflow: hidden">
            <div
              style="padding: 10px;display: flex;flex-direction: row;justify-content: space-between;align-items: center;height: 50px"
            >
              <div
                style="display: flex;flex-direction: row;align-items: center;overflow: auto;width: 260px"
              >
                <div v-for="(item,index) in checkedList" :key="index" class="tag-layout">
                  <span>{{item.value}}</span>
                  <i class="iconfont iconguanbi- i-close" @click="deleteCheckedVal(index)"></i>
                </div>
              </div>
              <p
                style="color: #44AAF6;margin-left: 5px;width: 30px;cursor: pointer;"
                @click="confirmValue"
              ></p>
            </div>
            <Input
              style="margin: 0 10px;width: 280px "
              :placeholder="$t('common.button.search')"
              v-model="key"
              prefix="ios-search"
              :search="true"
              @on-search="handleSearch"
            />
            <div
              style="margin: 10px;width: 280px;border: 1px solid #D3DCEB;height: 300px;overflow: hidden"
            >
              <div style="height: 250px;overflow-y: auto;overflow-x: hidden">
                <div
                  class="sub-val-layout"
                  v-for="(item,index) in valueList"
                  :key="index"
                  :class="item.isChecked?'sub-val-checked':''"
                  @click="chooseItemVal(item)"
                >
                  <span>{{item.value}}</span>
                  <i v-if="item.isChecked" class="iconfont iconduihao- i-choose"></i>
                </div>
              </div>
              <Page
                style="margin-top: 10px;text-align: center;"
                simple
                :total="total"
                :current.sync="current"
                class-name="bottom-page"
                @on-change="handlePageChange"
              />
            </div>
          </div>
        </Poptip>
      </li>
    </ul>
    <div class="exp-f-btn-add">
      <i class="iconfont icontianjia- exp-f-i-add" @click="handleAdd"></i>
      <span @click="handleAdd"></span>
    </div>
  </div>
</template>

<script>
import { successMsg, errorMsg } from "../../utils/message";
import {
  opList,
  logicalOperatorList,
  datasetType,
  dataType,
  boolList,
  NULL
} from "../../const/enum";
const props = {
  fields: {
    type: Array,
    default: () => [] //{name:,type:}
  },
  dtid: {
    type: [String, Number],
    default: null
  }
};
export default {
  props,
  data() {
    //
    return {
      bools: [...boolList],
      logicalOperatorList,
      opList,
      opList2: [opList[0], opList[1], opList[8]], //Bool
      op: "all",
      filters: [], //
      selfFields: this.fields,
      data_id: this.dtid, // id
      current: 1, // 
      key: "", // 
      total: 0, // 
      valueList: [], // 
      checkedList: [], // value
      curFilter: null // filter
    };
  },
  // data
  computed: {},
  //data
  watch: {
    op(newVal) {
      this.dispatchFilters();
    },
    filters: {
      handler(newVal) {
        this.dispatchFilters();
      },
      deeep: true
    }
  },
  //
  methods: {
    /***
     * @description 
     */
    dispatchFilters() {
      this.$emit("filter-setting", { op: this.op, list: this.filters });
    },
    /***
     * @description Bool
     */
    isBoolType(fieldName) {
      let field = this.findFieldType(fieldName);
      if (field && field.type == dataType.BOOL) {
        return true;
      }
      return false;
    },
    /****
     * @description 
     */
    getOpList(fieldName) {
      let bRet = this.isBoolType(fieldName);
      if (bRet) {
        return this.opList2;
      } else {
        return this.opList;
      }
    },
    /***
     * @description 
     */
    findFieldType(name) {
      let fields = this.selfFields ? this.selfFields : [];
      let temp = fields && fields.find(item => item.name == name);
      return temp;
    },
    getMsg(item) {
      let field = this.findFieldType(item.name);
      let msg = "";
      if (field) {
        switch (field.type) {
          case L.QGIS.DataType.DB_STRING:
          case dataType.STRING: {
            msg = this.$t("common.placeholder.stringType") || "";
            break;
          }
          case L.QGIS.DataType.DB_BIGINT:
          case L.QGIS.DataType.DB_INTEGER:
          case dataType.INT: {
            msg = this.$t("common.placeholder.intType") || "";
            break;
          }
          case dataType.FLOAT:
          case L.QGIS.DataType.DB_DOUBLE:
          case dataType.DOUBLE: {
            msg = this.$t("common.placeholder.numberType") || "";
            break;
          }
          case L.QGIS.DataType.DB_DATETIME:
          case dataType.DATETIME:
          case L.QGIS.DataType.DB_DATE:
          case dataType.DATE:
          case L.QGIS.DataType.DB_TIME:
          case dataType.TIME: {
            msg = this.$t("common.placeholder.dateType") || "";
            break;
          }
          case dataType.BOOL: {
            msg = this.$t("common.placeholder.boolType") || "";
            break;
          }
          default: {
            msg = this.$t("common.placeholder.unknowType") || "";
            break;
          }
        }
      } else {
        msg = this.$t("common.placeholder.unknowType") || "";
      }
      return msg;
    },
    /****
     * @description 
     */
    handleAdd() {
      let fields = this.selfFields ? this.selfFields : [];
      if (!fields || fields.length === 0) {
        errorMsg(",");
        return false;
      }
      //name:,operator:,value:
      let name = fields && fields.length > 0 ? fields[0].name : "";
      let field = this.findFieldType(name);
      let value = "";
      if (field.type == dataType.BOOL) {
        value = "false";
      }
      let temp = {
        name: name,
        operator: "==",
        value: value,
        visible: false,
        checkedList: value ? [{ value: value }] : []
      };
      this.filters.push(temp);
    },
    /****
     * @description 
     * @param{Number} index
     */
    handleDelete(index) {
      if (this.filters && index >= 0 && index < this.filters.length) {
        this.filters.splice(index, 1);
      }
    },
    /****
     * @description 
     */
    handleChangeFieldName(index) {
      if (this.filters && index >= 0 && index < this.filters.length) {
        let filter = this.filters[index];
        let field = this.findFieldType(filter.name);
        let newFilter = { ...filter };
        newFilter.checkedList = [];
        newFilter.operator = "==";
        if (field && field.type == dataType.BOOL) {
          newFilter.value = "false";
          newFilter.checkedList = [{ value: "false" }];
        } else {
          newFilter.value = "";
        }
        this.filters.splice(index, 1, newFilter);
      }
    },
    /**
     * @description 
     */
    handleChangeOp(index, bValue) {
      if (this.filters && index >= 0 && index < this.filters.length) {
        let filter = this.filters[index];
        let newFilter = { ...filter };
        newFilter.checkedList = [];
        if (filter.operator == "IsNull") {
          newFilter.checkedList = [];
          newFilter.value = "";
        } else if (filter.operator == "in" || filter.operator == "!in") {
          if (filter.value && filter.value.trim && filter.value.trim() !== "") {
            let arr = filter.value.split(",");
            if (arr) {
              Array.prototype.forEach.call(arr, ele => {
                if (ele.trim && ele.trim() !== "") {
                  newFilter.checkedList.push({
                    value: ele.trim()
                  });
                }
              });
            }
          } else {
            newFilter.value = "";
          }
        } else {
          if (filter.value && filter.value.trim && filter.value.trim() !== "") {
            newFilter.checkedList.push({
              value: filter.value.trim()
            });
          } else {
            newFilter.checkedList = [];
            newFilter.value = "";
          }
        }
        if (!bValue) {
          let field = this.findFieldType(filter.name);
          if (
            field &&
            field.type == dataType.BOOL &&
            filter.operator != "IsNull"
          ) {
            newFilter.value = "false";
            newFilter.checkedList = [{ value: "false" }];
          }
        }
        newFilter.visible = false;
        this.filters.splice(index, 1, newFilter);
      }
    },
    /**
     * @description 
     */
    handleChangeValue(index) {
      this.handleChangeOp(index, true);
    },
    /****
     * @description poptip
     */
    clearPoptipData() {
      this.current = 1; // 
      this.key = ""; // 
      this.total = 0; // 
      this.valueList = []; // 
      this.checkedList = []; // value
      this.curFilter = null; // filter
    },
    /**
     * @description 
     */
    chooseValue(filter, index) {
      this.clearPoptipData();
      this.curFilter = filter;
      this.checkedList = [];
      this.curFilter.checkedList.forEach(item => {
        this.checkedList.push(item);
      });
      this.queryKeyVal(filter);
    },
    /**
     * @description 
     */
    queryKeyVal(filter) {
      let list1 = [];
      list1.push(filter.name);
      // let params = {
      //   page_num: this.current,
      //   page_count: 10,
      //   key: this.key,
      //   field_list: list1
      // };
      // let options = {
      //   params,
      //   id: this.data_id,
      //   success: this.handleKeyVal
      // };
      let params = {
        id: this.data_id,
        query: {
          aggregation: "distinct",
          filter: this.key ? ["==", filter.name, this.key] : null,
          fields: list1
        },
        page: this.current,
        page_size: 10
      };
      let options = {
        params,
        success: this.handleKeyVal
      };
      this.$http.queryKeyVal(options);
    },
    /**
     * @description 
     */
    handleKeyVal(data) {
      // if (data && data.items) {
      //   let that = this;
      //   that.valueList.length = 0;
      //   Array.prototype.forEach.call(data.items, item => {
      //     let enable = false;
      //     if (
      //       Array.prototype.some.call(this.checkedList, f => {
      //         return f.value === item.value;
      //       })
      //     ) {
      //       enable = true;
      //     }
      //     if (item.value && item.value !== null && item.value !== "") {
      //       that.valueList.push({
      //         value: item.value,
      //         isChecked: enable
      //       });
      //     }
      //   });
      //   this.total = data.total_count;
      // }
      // console.log(data);
      if (data && data.rows) {
        let that = this;
        that.valueList.length = 0;
        Array.prototype.forEach.call(data.rows, item => {
          let enable = false;
          let value = "";
          if (Object.values(item).length > 0) {
            value = Object.values(item)[0];
            if (
              Array.prototype.some.call(this.checkedList, f => {
                return f.value === value;
              })
            ) {
              enable = true;
            }

            if (value !== null && value !== "") {
              that.valueList.push({
                value: value,
                isChecked: enable
              });
            }
          }
        });
        this.total = data.total;
      }
    },
    /**
     * @description value
     */
    deleteCheckedVal(index) {
      if (!this.checkedList || this.checkedList.length == 0) return false;
      this.checkedList.splice(index, 1);
      this.resetChecked();
      // 
      Array.prototype.forEach.call(this.checkedList, item => {
        Array.prototype.some.call(this.valueList, ele => {
          let enable = false;
          if (item.value === ele.value) {
            ele.isChecked = true;
            enable = true;
          }
          return enable;
        });
      });
    },
    /**
     * @description 
     */
    resetChecked() {
      Array.prototype.forEach.call(this.valueList, item => {
        item.isChecked = false;
      });
    },
    /**
     * @description 
     */
    confirmValue() {
      this.curFilter.visible = false;
      this.curFilter.checkedList = this.checkedList;
      this.curFilter.value = this.calcval();
    },
    calcval() {
      let val = "";
      if (this.curFilter && this.curFilter.checkedList) {
        for (let i = 0; i < this.curFilter.checkedList.length; i++) {
          let item = this.curFilter.checkedList[i];
          if (i === this.curFilter.checkedList.length - 1) {
            val = val + item.value;
          } else {
            val = val + item.value + ",";
          }
        }
      }
      return val;
    },
    handlePageChange(size) {
      this.current = size;
      this.queryKeyVal(this.curFilter);
    },
    handleSearch() {
      this.queryKeyVal(this.curFilter);
    },
    chooseItemVal(item) {
      if (!this.curFilter) return false;
      let op = this.curFilter.operator;
      if (op === "in" || op === "!in") {
        item.isChecked = !item.isChecked;
        if (item.isChecked) {
          // 
          this.checkedList.push(item);
        } else {
          // 
          let index = -1;
          for (let i = 0; i < this.checkedList.length; i++) {
            let h = this.checkedList[i];
            if (item.value === h.value) {
              index = i;
              break;
            }
          }
          if (index >= 0) {
            this.checkedList.splice(index, 1);
          }
        }
      } else {
        // 
        this.resetChecked();
        item.isChecked = !item.isChecked;
        this.checkedList.length = 0;
        if (item.isChecked) {
          this.checkedList.push(item);
        }
      }
    }
  }
};
</script>
<style lang='less' scoped>
//@import url(); css
.exp-containter {
  font-size: 14px;
  font-size: 0.14rem;
  font-family: Source Han Sans SC;
  font-weight: 400;
  color: rgba(211, 220, 235, 1);
}
.exp-f-header {
  padding: 10px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.exp-f-btn-add {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 14px;
  font-size: 0.14rem;
  color: #43aaf5;
  margin-left: 10px;
  padding: 20px 0px;
}
.exp-f-btn-add > span {
  line-height: 1;
  margin-left: 4px;
  font-size: 14px;
  font-size: 14px;
  font-size: 0.14rem;
  color: #43aaf5;
}
.exp-f-i-add {
  font-size: 16px;
  font-size: 0.16rem;
  color: #43aaf5;
}
.exp-containter > ul {
  margin: 0px;
  padding: 0px;
}
.exp-containter > ul > li {
  display: flex;
  padding: 0 10px;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 10px;
}
.exp-containter > ul > li:last-child {
  padding-bottom: 0px;
}
.exp-f-item {
  padding-left: 6px;
}
.exp-containter > ul > li > i:first-child:hover {
  cursor: pointer;
  color: #fb5252;
}
.i-attr:hover {
  cursor: pointer;
  color: #44aaf6;
}
.tag-layout {
  background-color: #f2f6f9;
  padding: 3px;
  margin-right: 5px;
  border-radius: 4px;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.sub-val-layout {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 280px;
  padding: 0 10px 0 10px;
  height: 30px;
  align-items: center;
  cursor: pointer;
}

.sub-val-layout:hover {
  // background-color: #D1EAFD;
  background-color: #d3dceb;
}
.sub-val-checked {
  background-color: #d1eafd;
}

.i-choose {
  font-size: 13px;
  font-size: 0.13rem;
  color: #44aaf5;
}
.i-close {
  font-size: 8px;
  font-size: 0.08rem;
  color: #b4c0d3;
  margin-left: 3px;
}
</style>


