<!--  -->
<template>
  <Modal
    v-model="visible"
    class-name="vertical-center-modal"
    :closable="false"
    width="540"
    :mask-closable="false"
  >
    <Row slot="header" type="flex" class="modal-dialog-item modal-dialog-header" align="middle">
      <i-col span="12" style="text-align:left;">
        <span>{{$t("createDataset.title")||""}}</span>
      </i-col>
      <i-col span="12" style="text-align:right;cursor: pointer;">
        <i class="iconfont iconguanbi-" @click="closeDialog"></i>
      </i-col>
    </Row>
    <Form :label-width="80" label-position="left" ref="formInline" :model="formInline">
      <FormItem :label="$t('common.form.name')||''" prop="name">
        <Input v-model="formInline.name"></Input>
      </FormItem>
      <FormItem :label="$t('common.form.crs')||''" prop="crs">
        <Select v-model="formInline.crs" filterable>
          <Option
            v-for="(item,index) in crsList"
            :value="item.srid"
            :key="index"
          >{{ formatCrs(item)}}</Option>
        </Select>
      </FormItem>
      <FormItem :label="$t('common.form.type')||''" prop="type">
        <RadioGroup v-model="formInline.type">
          <Radio label="point">{{$t('label.datasetType.point')||''}}</Radio>
          <Radio label="line">{{$t('label.datasetType.line')||''}}</Radio>
          <Radio label="polygon">{{$t('label.datasetType.polygon')||''}}</Radio>
        </RadioGroup>
      </FormItem>
    </Form>
    <Form :label-width="80" label-position="left" ref="formInline2" :model="formInline2">
      <FormItem :label="$t('common.form.fieldSet')||''" prop="fieldName">
        <Row type="flex" align="middle" justify="space-between">
          <Col span="9">
            <Input
              :placeholder="$t('common.placeholder.fieldName')||''"
              v-model="formInline2.fieldName"
            ></Input>
          </Col>
          <Col span="9">
            <Select v-model="formInline2.dataType">
              <Option
                v-for="item in dataTypeList"
                :value="item.value"
                :key="item.value"
              >{{ item.label }}</Option>
            </Select>
          </Col>
          <Col span="4" style="text-align:right;">
            <Button
              type="primary"
              style="background:#44AAF6;font-size:12px;font-size:0.12rem;"
              @click="addField"
            >{{$t('common.button.add')}}</Button>
          </Col>
        </Row>
      </FormItem>
    </Form>
    <Table :columns="columns" :data="fields" :row-class-name="rowClassName" height="160">
      <template slot-scope="{ row }" slot="name">
        <div class="label-ellipsis" :title="row.name">{{ row.name }}</div>
      </template>
      <template slot-scope="{ row }" slot="type">{{ findLabelByDataType(row.type) }}</template>
      <template slot-scope="{ row, index }" slot="action">
        <i
          class="iconfont iconshanchu1 modal-dialog-content-text"
          style="cursor:pointer;"
          @click="deletefield(index)"
        >{{$t('common.button.delete')||''}}</i>
      </template>
    </Table>
    <Row slot="footer" type="flex" align="middle">
      <Col span="24" style="text-align:right;">
        <Button
          class="modal-dialog-footer modal-dialog-footer-cancle"
          @click="closeDialog"
        >{{$t('common.button.cancel')||''}}</Button>
        <Button
          type="primary"
          class="modal-dialog-footer modal-dialog-footer-ok"
          @click="handleOk"
        >{{$t('common.button.confirm')||''}}</Button>
      </Col>
    </Row>
  </Modal>
</template>

<script>
import util from "../utils/util";
import { mapState } from "vuex";
import { dataTypeList, findLabelByDataType, LK_EVENT } from "../const/enum";
import { successMsg, errorMsg } from "../utils/message";
export default {
  data() {
    //
    return {
      crsList: [], //
      findLabelByDataType,
      visible: false, //
      formInline: {
        name: "", //
        type: "point", //    point    line    polygon
        crs: 4326
      },
      formInline2: {
        fieldName: "", //
        dataType: 1043 //,
      },
      fields: [],
      dataTypeList,
      columns: [
        {
          title: this.$t("common.table.fieldName") || "", //"",
          key: "name",
          slot: "name"
        },
        {
          title: this.$t("common.table.type") || "",
          key: "type",
          slot: "type"
        },
        {
          title: this.$t("common.table.op") || "",
          slot: "action",
          // width: 150,
          align: "center"
        }
      ]
    };
  },
  computed: {
    ...mapState(["map"])
  },
  //
  methods: {
    /****
     * @description 
     */
    getCrsList() {
      let options = {
        success: result => {
          result = result && result.rows ? result.rows : [];
          this.crsList = result;
        },
        params: {
          page_size: 60000
        }
      };
      this.$http.getSysCrs(options);
    },
    formatCrs(item) {
      return item ? `${item.auth_name}:${item.srid}` : "";
    },
    showDialog() {
      this.clear();
      this.getCrsList();
      this.visible = true;
    },
    /***
     * 
     */
    rowClassName(row, index) {
      if (index % 2 == 0) {
        return "table-even";
      } else {
        return "table-odd";
      }
    },
    /***
     * 
     */
    closeDialog() {
      this.clear();
      this.visible = false;
    },

    /***
     * 
     */
    addField() {
      //var reg2 = /(^_([a-zA-Z0-9]_?)*$)|(^[a-zA-Z](_?[a-zA-Z0-9])*_?$)/;
      let fieldName =
        this.formInline2.fieldName == null ||
        this.formInline2.fieldName == undefined
          ? ""
          : this.formInline2.fieldName.trim();
      if (fieldName.length <= 0) {
        errorMsg(this.$t("message.fieldNullError"));
        return false;
      }
      if (util.isMoreThanLength(fieldName)) {
        errorMsg(this.$t("message.stringLength64Error"));
        return false;
      }
      this.fields = this.fields || [];
      const bool = this.fields.some(
        item => item.name.toLowerCase() == fieldName.toLowerCase()
      );
      if (bool) {
        errorMsg(this.$t("message.fieldExistError"));
        return false;
      }
      this.formInline2.fieldName = "";
      let obj = {
        name: fieldName,
        type: this.formInline2.dataType
      };
      if (this.formInline2.dataType === 1043) {
        obj.width = 255;
      }
      this.fields.push(obj);
    },
    /***
     * 
     * @param {Number} index:
     */
    deletefield(index) {
      if (this.fields && index >= 0 && index < this.fields.length) {
        this.fields.splice(index, 1);
      }
    },
    /***
     * 
     */
    clear() {
      this.formInline = {
        name: "", //
        type: "point", //    point    line    polygon
        crs: 4326
      };
      // this.formInline.name="";
      // this.formInline.type="point";
      // this.formInline.crs="EPSG:4326";
      //   name: "", //
      //   type: "point", //    point    line    polygon
      //   crs:"EPSG:4326"
      // };
      this.formInline2 = {
        fieldName: "", //
        dataType: 1043 //
      };
      this.fields = [];
      this.crsList = [];
    },
    /***
     * 
     */
    handleOk() {
      let name =
        this.formInline.name == null || this.formInline.name == undefined
          ? ""
          : this.formInline.name.trim();
      if (name.length <= 0) {
        errorMsg(this.$t("message.nameError"));
        return false;
      }
      if (util.isMoreThanLength(name)) {
        errorMsg(this.$t("message.stringLength64Error"));
        return false;
      }
      this.fields = this.fields || [];
      if (this.fields.length == 0) {
        errorMsg(this.$t("message.fieldNumError"));
        return false;
      }
      let dtType = null;
      dtType =
        this.formInline.type == "point"
          ? 1
          : this.formInline.type == "line"
          ? 2
          : this.formInline.type == "polygon"
          ? 3
          : null;
      if (!dtType) {
        return false;
      }
      let params = {
        name: name,
        type: dtType,
        srid: this.formInline.crs,
        fields: this.fields
      };
      let options = {
        params,
        success: result => {
          let id = result ? result : null;
          if (id) {
            let arr = [id];
            let list = util.makeAddDatasets(arr, this.$symbolColors);
            if (list && list.length > 0 && this.map) {
              this.map.addCloudLayersExt(list, {
                success: () => {
                  let data = [];
                  let seManager = this.map.seManager;
                  arr.forEach(item => {
                    let temp = seManager.getLayersByDtId(item);
                    if (temp && temp.length > 0) {
                      let editLayer = this.map.getEditLayer();
                      let bEdit = false;
                      if (editLayer && editLayer.getID() === temp[0].getID()) {
                        bEdit = true;
                      }
                      let obj = util.createLayerInfo(temp[0]);
                      obj && data.push(obj);
                    }
                  });
                  this.$bus.emit(LK_EVENT.ADD_DATASETS, data);
                },
                error: err => {
                  errorMsg(err || this.$t("message.sdkInnerError"));
                }
              });
            }
            this.closeDialog();
          }
        }
      };
      this.$http.createDataset(options);
    }
  }
};
</script>
<style lang='less' scope>
//@import url(); css
.vertical-center-modal {
  //   display: flex;
  //   align-items: center;
  //   justify-content: center;
  .ivu-modal {
    top: 20%;
    .ivu-modal-content {
      border-radius: 4px;
      box-shadow: 0px 0px 20px 0px rgba(51, 80, 110, 0.3);
      // width: 540px;
      background: rgba(242, 246, 249, 1);
    }
  }
}
</style>


