<!---->
<template>
  <Row class="pannel" type="flex" align="middle">
    <i-col span="12" style="text-align: left;">
      <!-- <Tooltip placement="bottom" :content="$t('leftTool.open')||''" >
        <i class="iconfont icontianjiatuceng-" @click.stop="handleAddDataset" style="font-size:20px;font-size:0.2rem;"></i>
      </Tooltip>
      <Tooltip placement="bottom" :content="$t('leftTool.create')||''">
        <i class="iconfont iconxuanzeshujuji-" @click.stop="handleCreate" style="margin-left:10px;margin-left:0.1rem;"></i>
      </Tooltip>-->
      <i
        class="iconfont icontianjiatuceng-"
        @click="handleAddDataset"
        :title="$t('leftTool.open')||''"
      ></i>
      <i
        class="iconfont iconxuanzeshujuji-"
        @click="handleCreate"
        style="margin-left:10px;margin-left:0.1rem;"
        :title="$t('leftTool.create')||''"
      ></i>
      <i
        v-show="false"
        class="iconfont iconbianjishujujiicon"
        @click="handleEdit"
        style="margin-left:10px;margin-left:0.1rem;"
        :title="$t('leftTool.edit')||''"
      ></i>
      <!-- <i
        class="iconfont iconxuanzeshujuji-"
        @click="handleCombineFeatures"
        style="margin-left:10px;margin-left:0.1rem;"
        title=""
      ></i>-->
    </i-col>
    <i-col span="12" style="text-align: right;">
      <!-- <Tooltip placement="bottom" :content="$t('leftTool.save')||''">
        <i class="iconfont iconbaocun-" @click.stop="handleSave"></i>
      </Tooltip>
      <Tooltip placement="bottom" :content="$t('leftTool.undo')||''">
        <i
          class="iconfont iconchexiao-" style="margin-left:10px;margin-left:0.1rem;"
          :class="[isUndo?'anble-state':'unanble-state']"
          @click.stop="handleUndo"
        ></i>
      </Tooltip>
      <Tooltip placement="bottom" :content="$t('leftTool.redo')||''">
        <i
          class="iconfont iconhuifu-" style="margin-left:10px;margin-left:0.1rem;"
          :class="[isRedo?'anble-state':'unanble-state']"
          @click.stop="handleRedo"
        ></i>
      </Tooltip>-->
      <i class="iconfont iconbaocun-" @click="handleSave" :title="$t('leftTool.save')||''"></i>
      <i
        :title="$t('leftTool.undo')||''"
        class="iconfont iconchexiao-"
        style="margin-left:10px;margin-left:0.1rem;"
        :class="[isUndo?'anble-state':'unanble-state']"
        @click="handleUndo"
      ></i>
      <i
        :title="$t('leftTool.redo')||''"
        class="iconfont iconhuifu-"
        style="margin-left:10px;margin-left:0.1rem;"
        :class="[isRedo?'anble-state':'unanble-state']"
        @click="handleRedo"
      ></i>
    </i-col>
    <AddDataset ref="addDataset"></AddDataset>
    <CreateDataset ref="createDataset"></CreateDataset>
    <EditDataset ref="editDataset"></EditDataset>
    <Dialog :title="title" :visible="visible" @cus_cancel="handleClose" @cus_ok="handleOk">
      <Row style="padding:6px ; font-size:14px;">
        <Col span="24" style="color:red; ">{{msg}}</Col>
      </Row>
    </Dialog>
    <CompareFeature ref="compareFeature" @compare-features="handleCompareFeatures"></CompareFeature>
  </Row>
</template>

<script>
import CreateDataset from "./CreateDataset";
import EditDataset from "./EditDataset";
import CompareFeature from "./CompareFeature";
import AddDataset from "./AddDataset";
import Dialog from "./Dialog";
import { mapState } from "vuex";
import { errorMsg, infoMsg, successMsg } from "../utils/message";
export default {
  name: "LeftTool",
  components: {
    CreateDataset,
    AddDataset,
    EditDataset,
    Dialog,
    CompareFeature
  },
  data() {
    return {
      /**
       * @description 
       */
      isUndo: false,
      /****
       * @description 
       */
      isRedo: false,
      /***
       * @description 
       */
      title: this.$t("dialog.confirm") || "",
      /***
       * @description 
       */
      visible: false,
      /**
       * @description 
       */
      msg: this.$t("dialog.saveFeaturesMsg"),
      editFeatures: null
    };
  },
  // data
  computed: {
    ...mapState(["map"])
  },
  //
  methods: {
    /***
     * @description 
     */
    handleEdit() {
      this.editLayer = this.map.getEditLayer();
      if (this.editLayer && this.editLayer.dataset) {
        this.$refs &&
          this.$refs.editDataset &&
          this.$refs.editDataset.showDialog();
      } else {
        infoMsg(this.$t("message.noEditLayer"));
      }
    },
    /****
     * @description 
     */
    handleCombineFeatures() {
      this.$refs &&
        this.$refs.compareFeature &&
        this.$refs.compareFeature.showDialog();
    },
    /***
     * @description 
     */
    handleSave() {
      this.editFeatures = this.map.getAllDatasetEditFeatures();
      if (this.editFeatures && this.editFeatures.length > 0) {
        this.visible = true;
      } else {
        infoMsg(this.$t("message.noEditData"));
      }
    },
    /***
     * @description 
     */
    handleClose() {
      this.visible = false;
    },
    /***
     * @description 
     */
    handleOk() {
      if (this.editFeatures && this.editFeatures.length > 0) {
        let options = {
          params: this.editFeatures,
          success: result => {
            if (result && result.length > 0) {
              let dts = result;
              let arr = [];
              let remoteList = []; //[]
              let localList = []; //[]
              for (let i = 0; i < dts.length; i++) {
                let dt = this.map.findDatasetById(dts[i].id);
                if (!dt) continue;
                if (dts[i].code !== 0) {
                  arr.push(dt.getName());
                  // let tempFeatures = dt.getEditFeatures();
                  // localList.push(tempFeatures);
                  // if (dts[i].code == 1) {
                  //   //
                  // } else {
                  //   //2
                  //   let tempFeatures2 = dt.getEditFeatures();
                  //   let tempDt = dts[i];
                  //   if (
                  //     tempDt.conflict &&
                  //     tempDt.conflict.length > 0 &&
                  //     tempFeatures2 &&
                  //     tempFeatures2.features &&
                  //     tempFeatures2.features.length > 0
                  //   ) {
                  //     remoteList.push({
                  //       dataset_id: dt.getID(),
                  //       dataset_name: dt.getName(),
                  //       features: tempDt.conflict
                  //     });
                  //     localList.push(tempFeatures2);
                  //   }
                  // }
                } else {
                  //0
                  dt.clearData();
                }
              }
              if (arr.length > 0) {
                let s = "" + arr.join(",") + "";
                errorMsg(s + this.$t("message.fail"));
              }
              this.map.fire("edit-state", { isUndo: false, isRedo: false });
              if (arr && arr.length == 0) {
                this.map.fire("edit-finish");
              }
              this.map.clearSelection();
              this.map.refresh();
              // if (
              //   remoteList &&
              //   remoteList.length > 0 &&
              //   localList &&
              //   localList.length > 0
              // ) {
              //   this.$refs.compareFeature &&
              //     this.$refs.compareFeature.showDialog(remoteList, localList);
              // }
              this.visible = false;
            } else {
              errorMsg(this.$t("message.fail"));
              this.visible = false;
            }
          }
        };
        this.$http.saveAllDatasetFeatures(options);
      } else {
        infoMsg(this.$t("message.noEditData"));
        this.visible = false;
      }
    },
    /****
     * @description 
     */
    handleCompareFeatures(features) {
      if (features && features.length > 0) {
        let options = {
          params: { dataset_list: features },
          success: result => {
            if (
              result &&
              result.dataset_list &&
              result.dataset_list.length > 0
            ) {
              let dts = result.dataset_list;
              let arr = [];
              let remoteList = []; //[]
              let localList = []; //[]
              for (let i = 0; i < dts.length; i++) {
                let dt = this.map.findDatasetById(dts[i].dataset_id);
                if (!dt) continue;
                if (dts[i].code !== 0) {
                  if (dts[i].code == 1) {
                    //
                    arr.push(dt.getName());
                    let tempFeatures = dt.getEditFeatures();
                    localList.push(tempFeatures);
                  } else {
                    //2
                    let tempFeatures2 = dt.getEditFeatures();
                    let tempDt = dts[i];
                    if (
                      tempDt.conflict &&
                      tempDt.conflict.length > 0 &&
                      tempFeatures2 &&
                      tempFeatures2.features &&
                      tempFeatures2.features.length > 0
                    ) {
                      remoteList.push({
                        dataset_id: dt.getID(),
                        dataset_name: dt.getName(),
                        features: tempDt.conflict
                      });
                      localList.push(tempFeatures2);
                    }
                  }
                } else {
                  //0
                  dt.clearData();
                }
              }
              if (arr.length > 0) {
                let s = "" + arr.join(",") + "";
                errorMsg(s + this.$t("message.fail"));
              }
              this.map.fire("edit-state", { isUndo: false, isRedo: false });
              if (arr && arr.length == 0) {
                this.map.fire("edit-finish");
              }
              this.map.clearSelection();
              this.map.refresh();
              if (
                remoteList &&
                remoteList.length > 0 &&
                localList &&
                localList.length > 0
              ) {
                this.$refs.compareFeature &&
                  this.$refs.compareFeature.showDialog(remoteList, localList);
              }
              this.visible = false;
            } else {
              errorMsg(this.$t("message.fail"));
              this.visible = false;
            }
          }
        };
        this.$http.saveAllDatasetFeatures(options);
      } else {
        //,
        this.map && this.map.clearAllDatasetFeatures();
        this.map.fire("edit-state", { isUndo: false, isRedo: false });
        this.map.fire("edit-finish");
        this.map.refresh();
      }
    },
    /***
     * 
     */
    handleAddDataset() {
      this.$refs && this.$refs.addDataset && this.$refs.addDataset.showDialog();
    },
    /***
     * 
     */
    handleCreate() {
      this.$refs &&
        this.$refs.createDataset &&
        this.$refs.createDataset.showDialog();
    },
    /***
     * @description 
     */
    handleEditState(e) {
      this.isUndo = e.isUndo;
      this.isRedo = e.isRedo;
    },
    /***
     * @description 
     */
    handleUndo() {
      this.map && this.map.undo();
    },
    /**
     * @description 
     */
    handleRedo() {
      this.map && this.map.redo();
    }
  },
  watch: {
    map() {
      if (this.map) {
        this.map.off("edit-state", this.handleEditState);
        this.map.on("edit-state", this.handleEditState);
      }
    }
  }
};
</script>
<style lang='less' scoped>
//@import url(); css
.i-split2 {
  text-align: right;
  margin-right: 20px;
  margin-right: 0.2rem;
}
div > i {
  cursor: pointer;
}
.pannel {
  background: #3d5455;
  height: 50px;
  height: 0.5rem;
  padding: 0 10px;
  padding: 0 0.1rem;
  color: white;
}
.unanble-state {
  color: rgba(211, 220, 235, 0.35) !important;
  cursor: not-allowed;
}
.anble-state {
  cursor: pointer;
}
</style>


