<!---->
<template>
  <div class="cus-vec-edit">
    <Row
      class="item-default item-text-default"
      type="flex"
      align="middle"
      :class="[selfLayerInfo.editable?'item-select':'item-unselect']"
    >
      <i-col span="15" style="text-align: left; display:flex;align-items: center;">
        <i
          class="iconfont lyr-i"
          :class="[selfLayerInfo.visible?'iconkejian':'iconbukejian2']"
          style="vertical-align: text-bottom;"
          @click.stop="handleVisibleClick"
        ></i>
        <canvas :id="selfLayerInfo.id" width="12" height="12" style="margin-left:4px;"></canvas>
        <span class="span-lyr" :title="getLayerTitle()">{{selfLayerInfo.name}}</span>
      </i-col>
      <i-col span="9" style="text-align: right;">
        <div style="text-align:right;">
          <!---->
          <i class="iconfont iconbianji- lyr-i" @click="handleEditClick" title=""></i>
          <!---->
          <i class="iconfont iconchaxunyaosu- lyr-i" @click="handleQueryClick" title=""></i>
          <!---->
          <i class="iconfont iconguanli- lyr-i" @click="handleSettingClick" title=""></i>
          <!---->
          <i class="iconfont icondingwei- lyr-i" @click="handleLayerLocationClick" title=""></i>
          <!---->
          <i class="iconfont iconyichu- yc lyr-i" @click="handleDeleteLayerClick" title=""></i>
        </div>
      </i-col>
    </Row>
    <!---->
    <div style="background-color:#37404a;" v-if="selfLayerInfo.opAction==1">
      <Row class="sub-item cus-vec-edit" type="flex" align="middle" justify="space-between">
        <!-- <div class="v-split-line"></div> -->
        <i-col span="16" style="text-align:left;" class="sub-item-title">
          <div class="v-split-line" style="display:inline-block;margin-right:6px;"></div>
          {{$t("layerItem.labelSetting.title")||""}}
        </i-col>
        <i-col span="8" style="text-align:right;">
          <i-switch size="small" v-model="selfLayerInfo.label.enable" />
        </i-col>
      </Row>
      <!-- <div class="h-split-line"></div> -->
      <Row class="sub-item2" type="flex" align="middle" justify="space-between">
        <Col
          span="6"
          style="text-align:left;"
          class="sub-item-title2"
        >{{$t("layerItem.labelSetting.field")||""}}</Col>
        <Col span="18" style="text-align:left;" class="cus-vec-edit">
          <Select v-model="selfLayerInfo.label.field">
            <Option
              v-for="item in selfLayerInfo.fields"
              :value="item.name"
              :key="item.name"
            >{{ item.name }}</Option>
          </Select>
        </Col>
      </Row>
      <!-- <Row class="sub-item2" type="flex" align="middle" justify="space-between">
        <Col
          span="6"
          style="text-align:left;"
          class="sub-item-title2"
        >{{$t("layerItem.labelSetting.size")||""}}</Col>
        <Col span="6">
          <InputNumber v-model="selfLayerInfo.label.size" :min="10"></InputNumber>
        </Col>
        <Col
          span="6"
          style="text-alig:center;"
          class="sub-item-title2"
        >{{$t("layerItem.labelSetting.color")||""}}</Col>
        <Col span="6">
          <ColorPicker v-model="selfLayerInfo.label.color"/>
        </Col>
      </Row>-->
      <Row class="sub-item2" type="flex" align="middle" justify="space-between">
        <Col
          span="6"
          style="text-align:left;"
          class="sub-item-title2"
        >{{$t("layerItem.labelSetting.size")||""}}</Col>
        <Col span="18">
          <InputNumber
            v-model="selfLayerInfo.label.size"
            :min="10"
            :max="100"
            :active-change="false"
            style="width:100%;"
            :placeholder="$t('common.placeholder.numberRangeType')"
          ></InputNumber>
        </Col>
      </Row>
      <Row class="sub-item2" type="flex" align="middle" justify="space-between">
        <Col
          span="6"
          style="text-align:left;"
          class="sub-item-title2"
        >{{$t("layerItem.labelSetting.color")||""}}</Col>
        <Col span="18" style="text-align:left;">
          <!-- <ColorPicker v-model="selfLayerInfo.label.color"/> -->
          <CusColorPicker v-model="selfLayerInfo.label.color" />
        </Col>
      </Row>
      <!---->
      <Row class="sub-item" type="flex" align="middle" justify="space-between">
        <!-- <div class="v-split-line"></div> -->
        <i-col span="8" style="text-align:left;" class="sub-item-title">
          <div class="v-split-line" style="display:inline-block;margin-right:4px;"></div>
          {{$t("layerItem.filterSetting.title")||""}}
        </i-col>
        <i-col span="14" class="sub-item-title" style="text-align:left;">
          <Select v-model="selfLayerInfo.filter.op">
            <Option
              v-for="item in logicalOperatorList"
              :value="item.value"
              :key="item.value"
            >{{ item.label }}</Option>
          </Select>
        </i-col>
      </Row>
      <!--item-->
      <Row
        class="sub-item2"
        type="flex"
        align="middle"
        justify="start"
        v-for="(item,index) in selfLayerInfo.filter.list"
        :key="index"
      >
        <i-col span="3">
          <i class="iconfont iconyichu- i-delete" @click="handleDelete(index)"></i>
        </i-col>
        <i-col span="6">
          <div style="text-align:left;">
            <Select v-model="item.name" :title="item.name" @on-change="changeFilterName(index)">
              <Option
                v-for="item in selfLayerInfo.fields"
                :value="item.name"
                :key="item.name"
              >{{ item.name }}</Option>
            </Select>
          </div>
        </i-col>
        <!---->
        <i-col span="5">
          <div style="margin-left: 6px;margin-left:0.06rem;text-align:left;">
            <Select
              :title="item.operator"
              v-model="item.operator"
              @on-change="changeFilterType(index)"
            >
              <Option
                v-for="item in getOpList(item.name)"
                :value="item.value"
                :key="item.value"
              >{{ item.label }}</Option>
            </Select>
          </div>
        </i-col>
        <!---->
        <i-col span="7">
          <div style="margin-left: 6px;margin-left:0.06rem;text-align:left;">
            <Select
              @on-chnage="handleChangeValue(item)"
              v-if="isBoolType(item.name) && item.operator!='IsNull'"
              v-model="item.value"
              :title="item.value"
              class="exp-f-item"
              style="text-align:left;"
              :placeholder="getMsg(item)"
            >
              <Option v-for="t in bools" :value="t.value" :key="t.value">{{ t.label }}</Option>
            </Select>
            <Input
              v-else
              :title="item.value"
              v-model="item.value"
              :placeholder="getMsg(item)"
              :disabled="item.operator=='IsNull'"
              @on-change="valueChange(item)"
            />
          </div>
        </i-col>
        <i-col span="3" style="text-align:right;">
          <i
            v-if="item.operator=='IsNull'"
            style="padding-left:10px;cursor: not-allowed; color:#ccc;"
            class="iconfont icontubiaon- i-tubiaon"
          ></i>
          <Poptip :transfer="true" placement="right" width="320" v-model="item.visible" v-else>
            <i
              class="iconfont icontubiaon- i-tubiaon"
              style="cursor:pointer;"
              @click="chooseValue(item,index)"
            ></i>
            <div slot="content" style="height: 410px; overflow: hidden">
              <div
                style="padding: 10px;display: flex;flex-direction: row;justify-content: space-between;align-items: center;height: 50px"
              >
                <div
                  style="display: flex;flex-direction: row;align-items: center;overflow: auto;width: 260px"
                >
                  <div v-for="(item,index) in checkedList" :key="index" class="tag-layout">
                    <span>{{item.value}}</span>
                    <i class="iconfont iconguanbi- i-close" @click="deleteCheckedVal(index)"></i>
                  </div>
                </div>
                <p
                  style="color: #44AAF6;margin-left: 5px;width: 30px;cursor: pointer;"
                  @click="confirmValue"
                ></p>
              </div>
              <Input
                style="margin: 0 10px;width: 280px "
                :placeholder="$t('common.button.search')"
                v-model="key"
                prefix="ios-search"
                :search="true"
                @on-search="handleSearch"
              />
              <div
                style="margin: 10px;width: 280px;border: 1px solid #D3DCEB;height: 300px;overflow: hidden"
              >
                <div style="height: 250px;overflow-y: auto;overflow-x: hidden">
                  <div
                    class="sub-val-layout"
                    v-for="(item,index) in valueList"
                    :key="index"
                    :class="item.isChecked?'sub-val-checked':''"
                    @click="chooseItemVal(item)"
                  >
                    <span>{{item.value}}</span>
                    <i v-if="item.isChecked" class="iconfont iconduihao- i-choose"></i>
                  </div>
                </div>
                <Page
                  style="margin-top: 10px;text-align: center;"
                  simple
                  :total="total"
                  :current.sync="current"
                  class-name="bottom-page"
                  @on-change="handlePageChange"
                />
              </div>
            </div>
          </Poptip>
        </i-col>
      </Row>
      <div class="button-add">
        <i class="iconfont icontianjia- i-add" @click="handleAdd"></i>
        <span
          style="margin-left:4px;font-size: 14px;font-size: 0.14rem;color:#43AAF5;"
          @click="handleAdd"
        ></span>
      </div>
      <Row class="sub-item2" type="flex" align="middle" justify="space-between">
        <i-col span="6" style="text-align:left;"></i-col>
        <i-col span="18" style="text-align:right;">
          <div style="margin-left:8px;margin-left:0.08rem;">
            <Button
              @click.stop="handleSettingCancle"
              type="primary"
              style="background:#D3DCEB;font-size:12px;font-size:0.12rem;color:#4A6581;border-color: #D3DCEB;"
            >{{$t("common.button.cancel")||""}}</Button>
            <Button
              @click.stop="handleSettingOK"
              type="primary"
              style="background:#44AAF6;font-size:12px;font-size:0.12rem;margin-right:8px;margin-left:0.08rem;"
            >{{$t("common.button.confirm")||""}}</Button>
          </div>
        </i-col>
      </Row>
    </div>
    <QueryAttributes
      v-else-if="selfLayerInfo.opAction==2"
      :layer-info="layerInfo"
      @query-filter-setting="handleQueryFilterSetting"
      @query-filter-json="handleQueryFilterJSON"
    />
  </div>
</template>
<script>
import {
  opList,
  logicalOperatorList,
  datasetType,
  dataType,
  boolList
} from "../const/enum";
import { errorMsg } from "../utils/message";
import CusColorPicker from "./CusColorPicker";
import util from "../utils/util";
import { mapState } from "vuex";
import QueryAttributes from "./QueryAttributes";
const props = {
  layerInfo: {
    type: Object,
    default: () => {}
  }
};
export default {
  name: "LayerSortableItem",
  props: props,
  components: { CusColorPicker, QueryAttributes },
  data() {
    //
    let temp = this.layerInfo ? util.deepCopy(this.layerInfo) : {};
    return {
      bools: [...boolList],
      opList,
      opList2: [opList[0], opList[1], opList[8]], //Bool
      selfLayerInfo: temp,
      labelField: "",
      logicalOperatorList,
      data_id: null, // id
      current: 1, // 
      key: "", // 
      total: 0, // 
      valueList: [], // 
      checkedList: [], // value
      curFilter: null // filter
    };
  },
  // data
  computed: {
    ...mapState(["map"])
  },
  //data
  watch: {
    layerInfo: {
      handler(newVal, oldVal) {
        let temp = newVal ? util.deepCopy(newVal) : {};
        this.selfLayerInfo = temp;
      },
      deep: true
    }
  },
  //
  methods: {
    /***
     * @description Bool
     */
    isBoolType(fieldName) {
      let field = this.findFieldType(fieldName);
      if (field && field.type == dataType.BOOL) {
        return true;
      }
      return false;
    },
    /****
     * @description 
     */
    getOpList(fieldName) {
      let bRet = this.isBoolType(fieldName);
      if (bRet) {
        return this.opList2;
      } else {
        return this.opList;
      }
    },
    getLayerTitle() {
      if (this.selfLayerInfo) {
        return this.selfLayerInfo.name + "" + this.selfLayerInfo.num + "";
      }
      return "";
    },

    /**
     * @description 
     */
    queryKeyVal(filter) {
      // console.log(this.id);
      let list1 = [];
      list1.push(filter.name);
      // let params = {
      //   page: this.current,
      //   page_size: 10,
      //   key: this.key,
      //   field_list: list1
      // };
      let params = {
        id: this.data_id,
        query: {
          aggregation: "distinct",
          filter: this.key ? ["==", filter.name, this.key] : null,
          fields: list1
        },
        page: this.current,
        page_size: 10
      };
      let options = {
        params,
        success: this.handleKeyVal
      };
      this.$http.queryKeyVal(options);
    },

    /**
     * @description 
     */
    handleKeyVal(data) {
      // console.log(data);
      if (data && data.rows) {
        let that = this;
        that.valueList.length = 0;
        Array.prototype.forEach.call(data.rows, item => {
          let enable = false;
          let value = "";
          if (Object.values(item).length > 0) {
            value = Object.values(item)[0];
            if (
              Array.prototype.some.call(this.checkedList, f => {
                return f.value === value;
              })
            ) {
              enable = true;
            }

            if (value !== null && value !== "") {
              that.valueList.push({
                value: value,
                isChecked: enable
              });
            }
          }
        });
        this.total = data.total;
      }
    },

    /**
     * @description 
     */
    chooseValue(filter, index) {
      // console.log(filter.checkedList)
      this.curFilter = filter;
      this.checkedList = [];
      this.curFilter.checkedList.forEach(item => {
        this.checkedList.push(item);
      });
      this.queryKeyVal(filter);
    },
    handleChangeValue(item) {
      // if(item.operator=="IsNUll"){
      //   item.value="",
      //   item.checkedList=[];
      // }
      // else{
      // }
    },
    /**
     * @description 
     */
    valueChange(item) {
      // console.log(item.value);
      let value = item.value;
      if (!item.checkedList) {
        item.checkedList = [];
      }
      if (value && value.trim && value.trim()) {
        if (item.operator === "in" || item.operator === "!in") {
          let arr = value.split(",");
          if (arr) {
            item.checkedList.length = 0;
            Array.prototype.forEach.call(arr, ele => {
              if (ele.trim() !== "") {
                item.checkedList.push({
                  value: ele.trim()
                });
              }
            });
          }
        } else {
          item.checkedList[0] = {
            value: value.trim()
          };
        }

        // valueList
        // this.changeChecked();
      } else {
        item.checkedList.length = 0;
      }
      item.visible = false;
    },

    /**
     * @description 
     */
    confirmValue() {
      this.curFilter.visible = false;
      this.curFilter.checkedList = this.checkedList;
      this.curFilter.value = this.calcval();
    },

    calcval() {
      let val = "";
      for (let i = 0; i < this.curFilter.checkedList.length; i++) {
        let item = this.curFilter.checkedList[i];
        if (i === this.curFilter.checkedList.length - 1) {
          val = val + item.value;
        } else {
          val = val + item.value + ",";
        }
      }
      return val;
    },

    handleSearch() {
      this.queryKeyVal(this.curFilter);
    },

    chooseItemVal(item) {
      let op = this.curFilter.operator;
      if (op === "in" || op === "!in") {
        item.isChecked = !item.isChecked;
        if (item.isChecked) {
          // 
          this.checkedList.push(item);
        } else {
          // 
          let index = -1;
          for (let i = 0; i < this.checkedList.length; i++) {
            let h = this.checkedList[i];
            if (item.value === h.value) {
              index = i;
              break;
            }
          }

          if (index >= 0) {
            this.checkedList.splice(index, 1);
          }
        }
      } else {
        // 
        this.resetChecked();
        item.isChecked = !item.isChecked;
        this.checkedList.length = 0;
        if (item.isChecked) {
          this.checkedList.push(item);
        }
      }
    },

    /**
     * @description 
     */
    resetChecked() {
      Array.prototype.forEach.call(this.valueList, item => {
        item.isChecked = false;
      });
    },

    handlePageChange(size) {
      this.current = size;
      this.queryKeyVal(this.curFilter);
    },

    /**
     * @description value
     */
    deleteCheckedVal(index) {
      this.checkedList.splice(index, 1);
      this.resetChecked();
      // 
      Array.prototype.forEach.call(this.checkedList, item => {
        Array.prototype.some.call(this.valueList, ele => {
          let enable = false;
          if (item.value === ele.value) {
            ele.isChecked = true;
            enable = true;
          }
          return enable;
        });
      });
    },

    /**
     * @description 
     */
    changeFilterName(index) {
      let item = this.selfLayerInfo.filter.list[index];
      this.handleFieldType(item);
    },

    /**
     * @description 
     */
    changeFilterType(index) {
      // console.log(index)
      let item = this.selfLayerInfo.filter.list[index];
      if (!item) return false;
      // item.value= "";
      // item.checkedList=[];
      let value = item.value;
      let op = item.operator;
      item.checkedList.length = 0;
      if (item.operator == "IsNull") {
        item.value = "";
      } else {
        if (value && value.trim && value.trim() !== "") {
          if (op === "in" || op === "!in") {
            let arr = value.split(",");
            if (arr) {
              Array.prototype.forEach.call(arr, ele => {
                if (ele.trim && ele.trim() !== "") {
                  item.checkedList.push({
                    value: ele.trim()
                  });
                }
              });
            }
          } else {
            item.checkedList.push({
              value: value.trim()
            });
          }
        } else {
          item.value = "";
        }
      }
      let field = this.findFieldType(item.name);
      if (field && field.type == dataType.BOOL && item.operator != "IsNull") {
        item.value = "false";
        item.checkedList = [{ value: "false" }];
      }
      item.visible = false;
    },

    /**
     * @description name
     */
    handleFieldType(filter) {
      if (filter) {
        let that = this;
        let fields = this.selfLayerInfo.fields ? this.selfLayerInfo.fields : [];
        // console.log(fields)
        if (fields) {
          fields.some(item => {
            let enable = false;
            let value = item.type == dataType.BOOL ? "false" : "";
            if (item.name === filter.name) {
              filter.type = item.type;
              filter.value = value;
              filter.operator = "==";
              filter.visible = false;
              filter.checkedList = value ? [{ value }] : [];
              enable = true;
            }
            return enable;
          });
        }
      }
    },

    /***
     * @description
     */
    handleNumberBlue() {
      if (
        this.selfLayerInfo.label.size < 10 ||
        this.selfLayerInfo.label.size > 100
      ) {
        this.selfLayerInfo.label.size = 10;
      }
    },
    /***
     * @description 
     */
    findFieldType(name) {
      let fields = this.selfLayerInfo ? this.selfLayerInfo.fields : null;
      let temp = fields && fields.find(item => item.name == name);
      return temp;
    },
    getMsg(item) {
      let field = this.findFieldType(item.name);
      let msg = "";
      if (field) {
        switch (field.type) {
          case L.QGIS.DataType.DB_STRING:
          case dataType.STRING: {
            msg = this.$t("common.placeholder.stringType") || "";
            break;
          }
          case L.QGIS.DataType.DB_BIGINT:
          case L.QGIS.DataType.DB_INTEGER:
          case dataType.INT: {
            msg = this.$t("common.placeholder.intType") || "";
            break;
          }
          case dataType.FLOAT:
          case L.QGIS.DataType.DB_DOUBLE:
          case dataType.DOUBLE: {
            msg = this.$t("common.placeholder.numberType") || "";
            break;
          }
          case L.QGIS.DataType.DB_DATETIME:
          case dataType.DATETIME:
          case L.QGIS.DataType.DB_DATE:
          case dataType.DATE:
          case L.QGIS.DataType.DB_TIME:
          case dataType.TIME: {
            msg = this.$t("common.placeholder.dateType") || "";
            break;
          }
          case dataType.BOOL: {
            msg = this.$t("common.placeholder.boolType") || "";
            break;
          }
          default: {
            msg = this.$t("common.placeholder.unknowType") || "";
            break;
          }
        }
      } else {
        msg = this.$t("common.placeholder.unknowType") || "";
      }
      return msg;
    },
    /****
     * @description 
     */
    handleVisibleClick() {
      let item = this.selfLayerInfo;
      if (!item) {
        return false;
      }
      this.$emit("layer-visible", item.id);
    },
    /****
     * @description 
     */
    handleEditClick() {
      let item = this.selfLayerInfo;
      if (!item) {
        return false;
      }
      this.$emit("layer-edit", item.id);
    },
    /****
     * @description 
     *
     */
    handleQueryClick() {
      let item = this.selfLayerInfo;
      if (!item) {
        return false;
      }
      this.$emit("layer-query", item.id);
    },

    /****
     * @description 
     *
     */
    handleDeleteLayerClick() {
      let item = this.selfLayerInfo;
      if (!item) {
        return false;
      }
      this.$emit("layer-remove", item.id);
    },
    /****
     * @description 
     */
    handleSettingClick() {
      let item = this.selfLayerInfo;
      if (!item) {
        return false;
      }
      this.$emit("layer-setting", item.id);
    },
    /****
     * @description 
     */
    handleLayerLocationClick() {
      let item = this.selfLayerInfo;
      if (!item) {
        return false;
      }
      this.$emit("layer-location", item.id);
    },
    handleSettingCancle() {
      this.handleSettingClick();
    },
    /****
     * @description 
     */
    handleSettingOK() {
      let item = this.selfLayerInfo;
      if (!item) {
        return false;
      }
      if (item.label.enable) {
        if (!item.label.field) {
          errorMsg(this.$t("message.labelfieldNullError"));
          return false;
        }
      }
      let filter = item.filter;
      let fields = item.fields;
      let newList = null;
      let bRet = false;
      // console.log(filter)
      if (filter && fields && fields.length > 0) {
        let list = filter.list;
        let len = list ? list.length : 0;
        let count = fields.length;
        for (let i = 0; i < len; i++) {
          let field = null;
          for (let j = 0; j < count; j++) {
            if (fields[j].name === list[i].name) {
              field = fields[j];
              break;
            }
          }
          if (!field) continue;
          if (list[i].operator == "IsNull") {
            if (!newList) {
              newList = [];
            }
            newList.push({
              name: list[i].name,
              operator: "IsNull",
              checkedList: [],
              value: null
            });
          } else {
            if (
              field.type != L.QGIS.DataType.DB_DATETIME &&
              field.type != L.QGIS.DataType.DB_DATE &&
              field.type != L.QGIS.DataType.DB_TIME &&
              field.type != L.QGIS.DataType.DB_STRING
            ) {
              if (
                list[i].checkedList == null ||
                list[i].checkedList.length <= 0
              ) {
                errorMsg(
                  this.$t("message.filterNotValidError", {
                    filter: list[i].name + list[i].operator + list[i].value
                  })
                );
                bRet = true;
                break;
              }
            }
            let value = null;
            let bValid = false;
            let checkedList = list[i].checkedList;
            switch (field.type) {
              case L.QGIS.DataType.DB_INTEGER:
              case L.QGIS.DataType.DB_INT:
              case L.QGIS.DataType.DB_BIGINT: {
                // bValid = util.isValidInteger(list[i].value);
                bValid = checkedList.every(ele => {
                  return util.isValidInteger(ele.value);
                });
                if (bValid) {
                  // value = util.parseNum(list[i].value);
                  value = list[i].value;
                }
                break;
              }
              case L.QGIS.DataType.DB_FLOAT:
              case L.QGIS.DataType.DB_DOUBLE: {
                // bValid = util.isValidDecimal(list[i].value);
                bValid = checkedList.every(ele => {
                  return util.isValidDecimal(ele.value);
                });
                if (bValid) {
                  // value = util.parseNum(list[i].value);
                  value = list[i].value;
                }
                break;
              }
              case L.QGIS.DataType.DB_DATETIME: {
                if (list[i].value == null || list[i].value.trim() == "") {
                  value = "";
                  bValid = true;
                } else {
                  // bValid = util.isValidDate(list[i].value);
                  bValid = checkedList.every(ele => {
                    return util.isValidDate(ele.value);
                  });
                  if (bValid) {
                    value = list[i].value;
                  }
                }
                break;
              }
              case L.QGIS.DataType.DB_DATE: {
                if (list[i].value == null || list[i].value.trim() == "") {
                  value = "";
                  bValid = true;
                } else {
                  // bValid = util.isDatePart(list[i].value);
                  bValid = checkedList.every(ele => {
                    return util.isDatePart(ele.value);
                  });
                  if (bValid) {
                    value = list[i].value;
                  }
                }
                break;
              }
              case L.QGIS.DataType.DB_TIME: {
                if (list[i].value == null || list[i].value.trim() == "") {
                  value = "";
                  bValid = true;
                } else {
                  // bValid = util.isTimePart(list[i].value);
                  bValid = checkedList.every(ele => {
                    return util.isTimePart(ele.value);
                  });
                  if (bValid) {
                    value = list[i].value;
                  }
                }
                break;
              }
              case L.QGIS.DataType.DB_STRING: {
                bValid = true;
                value = list[i].value;
                if (value == null) {
                  value = "";
                }
                break;
              }
              case dataType.BOOL: {
                //bool
                value = list[i].value;
                if (value && value == "false") {
                  value = "false";
                  bValid = true;
                } else if (value && value == "true") {
                  value = "true";
                  bValid = true;
                } else {
                  bValid = false;
                  value = null;
                }
                break;
              }
            }
            if (!bValid) {
              errorMsg(
                this.$t("message.filterNotValidError", {
                  filter: list[i].name + list[i].operator + list[i].value
                })
              );
              bRet = true;
              break;
            } else {
              if (!newList) {
                newList = [];
              }
              newList.push({
                name: list[i].name,
                operator:
                  list[i].operator == "IsNull" ? "==" : list[i].operator,
                value: list[i].operator == "IsNull" ? null : value,
                checkedList: list[i].checkedList,
                type: field.type
              });
            }
          }
        }
      }
      if (bRet) {
        return false;
      } else {
        if (newList) {
          item.filter.list = newList;
        }
      }
      this.$emit("layer-setting-ok", item);
    },
    /****
     * @description 
     */
    handleAdd() {
      let fields = this.selfLayerInfo.fields ? this.selfLayerInfo.fields : [];
      //name:,operator:,value:
      let name = fields && fields.length > 0 ? fields[0].name : "";
      let field = this.findFieldType(name);
      let value = "";
      if (field.type == dataType.BOOL) {
        value = "false";
      }
      let temp = {
        name: fields && fields.length > 0 ? fields[0].name : "",
        operator: "==",
        value: "",
        visible: false,
        checkedList: []
      };
      if (!this.selfLayerInfo.filter) {
        this.selfLayerInfo.filter = {
          op: "all",
          list: []
        };
      }
      this.selfLayerInfo.filter.list.push(temp);
    },
    /****
     * @description 
     * @param{Number} index
     */
    handleDelete(index) {
      if (
        this.selfLayerInfo.filter &&
        this.selfLayerInfo.filter.list &&
        index >= 0 &&
        index < this.selfLayerInfo.filter.list.length
      ) {
        this.selfLayerInfo.filter.list.splice(index, 1);
      }
    },
    /***
     * @description 
     */
    drawSymbol(item) {
      if (item && item.symbol) {
        let canvas = document.getElementById(item.id);
        if (!canvas) return false;
        let ctx = canvas.getContext("2d");
        ctx.clearRect(0, 0, 12, 12);
        switch (item.type) {
          case datasetType.POINT: {
            //
            const r = 4;
            ctx.beginPath();
            ctx.arc(6, 6, r, 0, Math.PI * 2);
            ctx.fillStyle =
              item.symbol && item.symbol.color ? item.symbol.color : "black";
            ctx.fill();
            break;
          }
          case datasetType.LINE: {
            //
            let points = [
              { x: 0, y: 0 },
              { x: 4, y: 12 },
              { x: 8, y: 0 },
              { x: 12, y: 12 }
            ];
            if (!points || points.length === 0) return;
            ctx.beginPath();
            const len = points.length;
            for (let i = 0; i < len; i++) {
              const p = points[i];
              if (i === 0) {
                ctx.moveTo(p.x, p.y);
              } else {
                ctx.lineTo(p.x, p.y);
              }
            }
            ctx.strokeStyle =
              item.symbol && item.symbol.color ? item.symbol.color : "black";
            ctx.stroke();
            break;
          }
          case datasetType.POLYGON: {
            //
            ctx.beginPath();
            let color =
              item.symbol && item.symbol.color ? item.symbol.color : "black";
            ctx.fillStyle = color;
            ctx.fillRect(0, 0, 12, 12);
            if (item.symbol.lineColor) {
              ctx.strokeStyle = item.symbol.lineColor;
              ctx.lineWidth = 2;
              ctx.strokeRect(0, 0, 12, 12);
            }
            break;
          }
        }
      }
    },
    handleQueryFilterSetting(data) {
      if (!data) {
        return false;
      }
      this.$emit("query-filter", {
        layerId: this.selfLayerInfo.id,
        dtId: this.selfLayerInfo.dataset_id,
        filter: data,
        fields: this.selfLayerInfo.fields
      });
    },
    handleQueryFilterJSON(data) {
      if (!data) {
        return false;
      }
      this.$emit("query-filter", {
        layerId: this.selfLayerInfo.id,
        dtId: this.selfLayerInfo.dataset_id,
        filter: data,
        mode: "JSON",
        fields: this.selfLayerInfo.fields
      });
    }
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    if (this.layerInfo) {
      this.data_id = this.layerInfo.dataset_id;
    }
    this.$nextTick(() => {
      this.drawSymbol(this.selfLayerInfo);
    });
    // console.log(this.layerInfo);
  }
};
</script>
<style lang='less' scoped>
//@import url(); css
