}
.item-text-default {
  font-size: 14px;
  font-size: 0.14rem;
  font-family: SourceHanSansCN-Normal;
  font-weight: 400;
}
.item-unselect {
  background-color: #171d24 !important;
  color: rgba(211, 220, 235, 1) !important;
}
.item-select {
  background-color: #283637 !important;
  color: rgba(211, 220, 235, 1) !important;
}
i:hover {
  color: rgba(67, 170, 245, 1);
}
i.yc:hover {
  color: #fb5252;
}

.i-delete {
  font-size: 16px;
  cursor: pointer;
  // font-size: 0.16rem;
}

.i-delete:hover {
