<template>
  <Poptip placement="right" :width="width" :transfer="true" >
    <div :style="{background:color}" class="div-bg">
      <!-- <div style="width: 100%;height: 100%;" :style="{background:color}"></div> -->
    </div>
    <sketch-picker slot="content" :value="color" @input="updateColor"/>
  </Poptip>
</template>

<script>
import { Sketch } from "vue-color";
export default {
  name: "CusColorPicker",
  props: ["value"],
  components: {
    "sketch-picker": Sketch
  },
  data() {
    return {
      color: this.value,
      width: 238
    };
  },
  methods: {
    updateColor(val) {
      const rgba = val.rgba;
      this.color = `rgba(${rgba.r},${rgba.g},${rgba.b},${rgba.a})`;
      this.$emit("input", this.color);
    }
  }
};
</script>

<style  lang='less'>
.div-bg {
  display: inline-block;
  width: 35px;
  height: 25px;
  border: 1px solid #fff;
  border-radius: 4px;
  padding: 0px;
  vertical-align: middle;
  cursor: pointer;
}
.div-bg:hover {
  border-color: #57a3f3;
  outline: 0;
  box-shadow: 0 0 0 2px rgba(45, 140, 240, 0.2);
}
.ivu-poptip{
  .ivu-poptip-rel{
    display: block !important;
  }
  .ivu-poptip-arrow{
    display: none !important;
  }
}
.ivu-poptip-body{
  padding: 0 !important;
}

.ivu-poptip-inner{
  white-space: normal !important;
}

.ivu-poptip-popper[x-placement=right] .ivu-poptip-arrow{
  left: -2px;
  border-width: 14px 14px 14px 0;
  margin-top: -14px;
  &:after{
    left: 2px;
    bottom: -14px;
    border-width: 14px 14px 14px 0;
  }
}
</style>



