<!--  -->
<template>
  <div>
    <vue-scroll>
      <div class="pannel">
        <Row class="item-default pannel" type="flex" align="middle">
          <i-col span="20" style="text-align: left;">
            <span class="item-text">{{$t('layersList.layers')}}({{layerCount}})</span>
          </i-col>
          <i-col span="4" style="text-align: right;">
            <Poptip trigger="hover" placement="bottom" word-wrap :transfer="true">
              <i class="iconfont iconbangzhu header-ico"></i>
              <div slot="content" style="padding:10px;">
                <p v-html="cusText" style="line-height:1.5;font-size:12px;"></p>
              </div>
            </Poptip>
          </i-col>
        </Row>
        <LayerSortableItem
          v-for="(item) in layerInfos"
          :key="item.id"
          :layerInfo="item"
          @layer-visible="handleLayerVisible"
          @layer-edit="handleLayerEdit"
          @layer-query="handleLayerQuery"
          @layer-remove="handleLayerRemove"
          @layer-setting="handleLayerSetting"
          @layer-setting-ok="handleLayerSettingOK"
          @layer-location="handleLayerLocation"
          @query-filter="handleQueryFilter"
        ></LayerSortableItem>
        <Row
          style="border-top: 1px solid #1d2835;"
          class="item-default item-text-default2"
          type="flex"
          align="middle"
          :class="[isPbZK?'pb-item-select':'pb-item-unselect']"
        >
          <Col span="20" style="text-align: left; display:flex;align-items: center;">
            <i
              class="iconfont"
              :class="[isShowTile?'iconkejian':'iconbukejian2']"
              style="vertical-align: text-bottom;cursor:pointer;"
              @click="handleVisibleClick"
            ></i>
            <img src="@/assets/img/earth.svg" class="earth" />
            <span class="span-lyr">{{curPbMap}}</span>
          </Col>
          <Col span="4" style="text-align: right; ">
            <i class="iconfont iconxingzhuang i-attr" @click="handleGray" title=""></i>
            <i class="iconfont iconxingzhuang1 i-attr" @click="handleZK" title=""></i>
          </Col>
        </Row>
        <PublicMap v-show="isPbZK" @change-map="handleChangeMap" />
      </div>
    </vue-scroll>
    <Dialog :title="title" :visible="visible" @cus_cancel="handleClose" @cus_ok="handleOk">
      <Row style="padding:6px ; font-size:14px;">
        <!-- <Col span="6">
          <Checkbox v-model="bNotClearData">{{$t("label.retainData")||""}}</Checkbox>
        </Col>-->
        <Col span="24" style="color:red; ">{{msg}}</Col>
      </Row>
    </Dialog>
  </div>
</template>

<script>
import LayerSortableItem from "./LayerSortableItem";
import Dialog from "./Dialog";
import { LK_EVENT, dataType } from "../const/enum";
import util from "../utils/util";
import tinycolor from "tinycolor2";
import { successMsg, errorMsg, infoMsg } from "../utils/message";
import PublicMap from "./base/PublicMap";
import { mapState } from "vuex";
export default {
  name: "LayerSortableList",
  components: {
    LayerSortableItem,
    Dialog,
    PublicMap
  },
  data() {
    //
    return {
      cusText: "3",
      isShowTile: true,
      layerInfos: [],
      title: this.$t("dialog.confirm") || "",
      msg: this.$t("dialog.deleteMsg") || "",
      visible: false,
      curLayerId: null,
      bNotClearData: false,
      // pbMap: "(4326)",
      isPbZK: false,
      bGray: false //[0],true[1]
    };
  },
  // data
  computed: {
    layerCount() {
      return this.layerInfos ? this.layerInfos.length : 0;
    },
    ...mapState(["map", "curPbMap"])
  },
  //data
  watch: {
    map() {
      if (this.map) {
        this.map.off("edit-finish", this.handleEditFinish);
        this.map.on("edit-finish", this.handleEditFinish);
      }
    }
  },
  //
  methods: {
    handleChangeMap(data) {
      if (data) {
        let gray = this.bGray ? 1 : 0;
        this.$store.commit("setGray", gray);
        this.$store.commit("setCurPbMap", data.name);
      }
    },
    /****
     * @description 
     */
    handleVisibleClick() {
      this.isShowTile = !this.isShowTile;
      this.$emit("layer-tile-visible", this.isShowTile);
    },
    handleZK() {
      this.isPbZK = !this.isPbZK;
    },
    /****
     * @description 
     */
    handleEditFinish() {
      this.layerInfos &&
        this.layerInfos.forEach(item => {
          item.editable = false;
        });
    },
    /***
     * @description 
     */
    handleClose() {
      this.bNotClearData = false;
      this.visible = false;
    },
    /***
     * @description 
     */
    handleOk() {
      if (!this.map || !this.map.seManager || !this.curLayerId) {
        return false;
      }
      let index = this.findLayerIndex(this.curLayerId);
      if (index >= 0) {
        this.layerInfos.splice(index, 1);
        let seManager = this.map.seManager;
        let layer = seManager.getLayerById(this.curLayerId);
        this.map.fire("delete-layer", { layerId: this.curLayerId });
        if (layer) {
          let editlayer = this.map.getEditLayer();
          this.map.removeVectorLayer(this.curLayerId, !this.bNotClearData);
          if (editlayer && editlayer.getID() === layer.getID()) {
            this.map.fire("edit-state", { isUndo: false, isRedo: false });
            this.map.fire("edit-finish");
          }
          seManager.refresh();
          let renderStyle = layer.renderStyle ? layer.renderStyle : null;
          let renderItem = renderStyle && renderStyle.getDefaultRenderItem();
          let symbol = renderItem && renderItem.getSymbol();
          if (symbol && symbol.getColor()) {
            let hexColor = symbol.getColor().hexValue();
            let index = this.$symbolColors.indexOf(hexColor.toUpperCase());
            if (index >= 0) {
              this.$symbolColors.splice(index, 1);
            }
          }
        }
      }
      this.curLayerId = null;
      this.bNotClearData = false;
      this.visible = false;
    },
    /****
     * @description 
     * @param{Number} layerIdID
     *
     */
    handleLayerVisible(layerId) {
      if (!this.map || !this.map.seManager) {
        return false;
      }
      let index = this.findLayerIndex(layerId);
      if (index >= 0) {
        let seManager = this.map.seManager;
        let layer = seManager.getLayerById(layerId);
        if (layer) {
          layer.visible = !layer.visible;
          seManager.refresh();
          this.layerInfos &&
            this.layerInfos.forEach(item => {
              if (item.id === layerId) {
                item.visible = !item.visible;
              }
            });
        }
      }
    },
    /****
     * @description 
     */
    handleLayerEdit(layerId) {
      if (!this.map || !this.map.seManager) {
        return false;
      }
      let index = this.findLayerIndex(layerId);
      if (index >= 0) {
        let seManager = this.map.seManager;
        let layer = seManager.getLayerById(layerId);
        if (layer) {
          seManager.setEditLayer(layerId);
          seManager.refresh();
          this.layerInfos &&
            this.layerInfos.forEach(item => {
              if (item.id === layerId) {
                item.editable = true;
                this.map.fire("start_edit", { data: item });
              } else {
                item.editable = false;
              }
            });
        }
      }
    },
    /****
     * @description 
     */
    handleLayerQuery(layerId) {
      let index = this.findLayerIndex(layerId);
      if (index >= 0) {
        this.layerInfos &&
          this.layerInfos.forEach(item => {
            if (item.id === layerId) {
              //item.isSetting = !item.isSetting;
              item.opAction = item.opAction == 2 ? 0 : 2;
            } else {
              //item.isSetting = false;
              item.opAction = 0;
            }
          });
      }
      if (index >= 0 && this.layerInfos[index].opAction == 2) {
        this.$emit("dataset-query", {
          layerId: layerId,
          dtId: this.layerInfos[index].dataset_id,
          filter: [],
          fields: this.layerInfos[index].fields
        });
      }
    },
    /****
     * @description 
     */
    handleLayerRemove(layerId) {
      this.curLayerId = null;
      if (!this.map || !this.map.seManager) {
        return false;
      }
      let index = this.findLayerIndex(layerId);
      if (index >= 0) {
        this.curLayerId = layerId;
        this.visible = true;
      }
    },
    /****
     * @description 
     * @param{Number} layerIdID
     */
    handleLayerSetting(layerId) {
      let index = this.findLayerIndex(layerId);
      if (index >= 0) {
        this.layerInfos &&
          this.layerInfos.forEach(item => {
            if (item.id === layerId) {
              //item.isSetting = !item.isSetting;
              item.opAction = item.opAction == 1 ? 0 : 1;
            } else {
              //item.isSetting = false;
              item.opAction = 0;
            }
          });
      }
    },
    /****
     * @description 
     * @param{Number} layerIdID
     */
    handleLayerLocation(layerId) {
      let layerInfo = this.findLayer(layerId);
      if (layerInfo && this.map) {
        let dtid = layerInfo.dataset_id;
        let dt = this.map.findDatasetById(dtid);
        if (dt) {
          const dtBounds = dt.getBounds();
          if (dtBounds && dtBounds.isValid()) {
            const latlngBounds = dtBounds.toLatLngBounds(dt.getProjInfo());
            if (latlngBounds) {
              this.map.fitBounds(latlngBounds);
            }
          }
        }
      }
    },
    /***
     * @description 
     */
    handleLayerSettingOK(layerInfo) {
      let layerId = layerInfo ? layerInfo.id : undefined;
      let index = this.findLayerIndex(layerId);
      if (index >= 0) {
        this.layerInfos.splice(index, 1, layerInfo);
        let layer = this.map.getLayerById(layerId);
        if (layer) {
          let renderTextStyle = layer.renderTextStyle
            ? layer.renderTextStyle
            : null;
          if (!renderTextStyle) {
            renderTextStyle = L.QGIS.RenderTextStyle.createDefault(
              layer.dataset.getType()
            );
          }
          renderTextStyle.setEnable(layerInfo.label.enable);
          let textRenderItem =
            renderTextStyle && renderTextStyle.getRenderItem()
              ? renderTextStyle.getRenderItem()
              : null;
          if (textRenderItem) {
            textRenderItem.setFieldList([layerInfo.label.field]);
          }
          let textSymbol =
            textRenderItem && textRenderItem
              ? textRenderItem.getTextSymbol()
              : null;
          if (textSymbol) {
            textSymbol.setFontSize(layerInfo.label.size);
            let rgb = tinycolor(layerInfo.label.color).toRgb();
            let color = new L.SymbolLib.RGBAColor(
              rgb.r,
              rgb.g,
              rgb.b,
              255 * rgb.a
            );
            textSymbol.setColor(color);
          }
          layer.setRenderTextStyle(renderTextStyle);
          let filter = layerInfo.filter;
          let exp = null;
          if (filter) {
            if (filter.list && filter.list.length > 0) {
              if (!exp) {
                exp = [];
              }
              exp.push(filter.op);
              filter.list.forEach(item => {
                if (item.operator == "IsNull") {
                  exp.push(["==", item.name, null]);
                } else {
                  let checkedList = item.checkedList;
                  let type = item.type;
                  let arr = [];
                  if (checkedList && Array.isArray(checkedList)) {
                    Array.prototype.forEach.call(checkedList, ele => {
                      if (
                        type === L.QGIS.DataType.DB_INTEGER ||
                        type === L.QGIS.DataType.DB_INT ||
                        type === L.QGIS.DataType.DB_BIGINT ||
                        type === L.QGIS.DataType.DB_FLOAT ||
                        type === L.QGIS.DataType.DB_DOUBLE
                      ) {
                        arr.push(util.parseNum(ele.value));
                      } else {
                        arr.push(ele.value);
                      }
                    });
                  }
                  if (!arr || arr.length == 0) {
                    if (
                      type === L.QGIS.DataType.DB_INTEGER ||
                      type === L.QGIS.DataType.DB_INT ||
                      type === L.QGIS.DataType.DB_BIGINT ||
                      type === L.QGIS.DataType.DB_FLOAT ||
                      type === L.QGIS.DataType.DB_DOUBLE
                    ) {
                      exp.push([item.operator, item.name, null]);
                    } else {
                      exp.push([item.operator, item.name, ""]);
                    }
                  } else {
                    exp.push([item.operator, item.name, ...arr]);
                  }
                }
              });
            }
          }
          layer.setRenderTextStyle(renderTextStyle);
          layer.setFilter(exp);
          this.map.clearAllDatasetFeatures(true); //
          this.map.refresh();
        }
      }
    },
    handleQueryFilter(data) {
      if (!data || !data.filter || !data.layerId || !data.dtId) {
        return false;
      }
      if (data.mode != undefined && data.mode == "JSON") {
        this.$emit("dataset-query", {
          layerId: data.layerId,
          dtId: data.dtId,
          filter: data.filter,
          fields: data.fields
        });
        return false;
      }
      let filter = data.filter;
      let exp = null;
      if (filter) {
        if (filter.list && filter.list.length > 0) {
          if (!exp) {
            exp = [];
          }
          exp.push(filter.op);
          filter.list.forEach(item => {
            if (item.operator == "IsNull") {
              exp.push(["==", item.name, null]);
            } else {
              let checkedList = item.checkedList;
              let type = item.type;
              let arr = [];
              if (checkedList && Array.isArray(checkedList)) {
                Array.prototype.forEach.call(checkedList, ele => {
                  if (
                    type === L.QGIS.DataType.DB_INTEGER ||
                    type === L.QGIS.DataType.DB_INT ||
                    type === L.QGIS.DataType.DB_BIGINT ||
                    type === L.QGIS.DataType.DB_FLOAT ||
                    type === L.QGIS.DataType.DB_DOUBLE
                  ) {
                    arr.push(util.parseNum(ele.value));
                  } else if (type === dataType.BOOL) {
                    if (ele.value && ele.value == "true") {
                      arr.push("true");
                    } else {
                      arr.push("false");
                    }
                  } else {
                    arr.push(ele.value);
                  }
                });
              }
              if (!arr || arr.length == 0) {
                if (
                  type === L.QGIS.DataType.DB_INTEGER ||
                  type === L.QGIS.DataType.DB_INT ||
                  type === L.QGIS.DataType.DB_BIGINT ||
                  type === L.QGIS.DataType.DB_FLOAT ||
                  type === L.QGIS.DataType.DB_DOUBLE
                ) {
                  exp.push([item.operator, item.name, null]);
                } else {
                  exp.push([item.operator, item.name, ""]);
                }
              } else {
                exp.push([item.operator, item.name, ...arr]);
              }
            }
          });
        }
      }
      this.$emit("dataset-query", {
        layerId: data.layerId,
        dtId: data.dtId,
        filter: exp,
        fields: data.fields
      });
    },
    /***
     * @description ID
     * @param{Number} layerIdID
     * @returns{Number} 
     */
    findLayerIndex(layerId) {
      if (layerId != undefined) {
        let len = this.layerInfos ? this.layerInfos.length : 0;
        for (let i = 0; i < len; i++) {
          if (this.layerInfos[i].id === layerId) {
            return i;
          }
        }
      }
      return -1;
    },
    /***
     * @description ID
     * @param{Number} layerIdID
     * @returns{Object} 
     */
    findLayer(layerId) {
      if (layerId != undefined) {
        let len = this.layerInfos ? this.layerInfos.length : 0;
        for (let i = 0; i < len; i++) {
          if (this.layerInfos[i].id === layerId) {
            return this.layerInfos[i];
          }
        }
      }
      return null;
    },
    handleGray() {
      this.bGray = !this.bGray;
      this.$store.commit("setGray", this.bGray ? 1 : 0);
      this.$emit("tilelayer-gray", { gray: this.bGray ? 1 : 0 });
    }
  },
  beforeDestroy() {
    this.$bus.off(LK_EVENT.ADD_DATASETS);
    this.$bus.off(LK_EVENT.VIEW_STATICS);
    this.$bus.off(LK_EVENT.EDIT_DATASET);
  },
  created() {
    this.$bus.on(LK_EVENT.ADD_DATASETS, result => {
      let temp = this.layerInfos;
      let layers = this.map && this.map.getVectorLayers();
      let count = layers ? layers.length : 0;
      let len = temp ? temp.length : 0;
      this.layerInfos = [];
      for (let i = 0; i < count; i++) {
        let layInfo = null;
        for (let j = 0; j < len; j++) {
          if (layers[i].getID() === temp[j].id) {
            layInfo = temp[j];
            break;
          }
        }
        let editLayer = this.map.getEditLayer();
        let bEdit = false;
        if (editLayer && editLayer.getID() === layers[i].getID()) {
          bEdit = true;
        }
        if (layInfo) {
          layInfo.editable = bEdit;
          this.layerInfos.push(layInfo);
        } else {
          let obj = util.createLayerInfo(layers[i], bEdit);
          if (obj) {
            this.layerInfos.push(obj);
          }
        }
      }
    });
    this.$bus.on(LK_EVENT.VIEW_STATICS, result => {
      if (result) {
        let max_count = 30000;
        let count = result ? result.length : 0;
        let tempList = [];
        this.layerInfos &&
          this.layerInfos.forEach(item => {
            // let bExist=false;
            let num = 0;
            for (let i = 0; i < count; i++) {
              if (item.dataset_id == result[i].dtId) {
                //bExist=true;
                num = result[i].count;
                break;
              }
            }
            item.num = num;
            if (max_count < num) {
              tempList.push(item.name);
            }
          });
        if (tempList.length > 0) {
          let msg = tempList.join(",") + ",";
          infoMsg(msg);
        }
      }
    });
    //
    this.$bus.on(LK_EVENT.EDIT_DATASET, result => {
      let { id, fields } = result;
      let len = this.layerInfos ? this.layerInfos.length : 0;
      for (let i = 0; i < len; i++) {
        if (id == this.layerInfos[i].dataset_id) {
          let tempLayerInfo = { ...this.layerInfos[i] };
          tempLayerInfo.fields = fields;
          this.$set(this.layerInfos, i, tempLayerInfo);
        }
      }
      console.log(this.layerInfos);
      // this.layerInfos &&
      //   this.layerInfos.forEach(item => {
      //     if (item.dataset_id == id) {
      //       if (!item.fields) {
      //         item.fields = [];
      //       }
      //       item.fields.splice(0, item.fields.length, fields);
      //     }
      //   });
    });
  }
};
</script>
<style lang='less' scoped>
//@import url(); css
.pannel {
  background-color: #171d24;
}
span {
  padding: 0px;
}
.layer-content {
  //height:calc(100% - 1.7rem);
  height: 200px;
}
.item-text-default2 {
  font-size: 14px;
  font-size: 0.14rem;
  font-family: SourceHanSansCN-Normal;
  font-weight: 400;
  background-color: rgba(51, 80, 110, 1) !important;
  color: rgba(211, 220, 235, 1) !important;
}
img.earth {
  width: 15px;
  height: 15px;
  margin: 0 4px;
}
.span-lyr {
  //position: absolute;
  display: inline-block;
  margin-left: 3px;
  white-space: nowrap;
  // width: 100px;
  // width: 1rem;
  text-overflow: ellipsis;
  overflow: hidden;
}
i:hover {
  // color: white;
  color: rgba(67, 170, 245, 1);
  // cursor: pointer;
}
.header-ico {
  font-size: 14px;
  font-size: 0.14rem;
  color: rgba(211, 220, 235, 1);
  font-weight: 400;
  cursor: pointer !important;
}
.header-ico:hover {
  color: #44aaf6;
  cursor: pointer !important;
}
.lyr-zk-img :hover {
  cursor: pointer;
  color: red;
}
svg.svg-cur {
  cursor: pointer;
}
svg:hover feFlood {
  flood-color: rgb(65, 165, 238);
}
.pb-item-unselect {
  background-color: #171d24 !important;
  color: rgba(211, 220, 235, 1) !important;
}
.pb-item-select {
  background-color: #283637 !important;
  color: rgba(211, 220, 235, 1) !important;
}
.i-attr:hover {
  cursor: pointer;
  color: #44aaf6;
}
</style>


