<!---->
<template>
  <div v-show="visible">
    <div class="search-containter">
      <!--header-->
      <Row type="flex" class="row-item search-header" align="middle">
        <i-col span="12" style="text-align:left;">
          <span>{{name}}</span>
        </i-col>
        <i-col span="12" style="text-align:right;cursor: pointer;">
          <i class="iconfont iconguanbi-" @click="closeDialog"></i>
        </i-col>
      </Row>
      <!--content-->
      <div class="search-body">
        <!-- <Row type="flex" align="middle" justify="space-between" class="cus-vec-edit-btn">
          <Col span="24" style="text-align:left;">
            {{$t('common.text.condition')||''}}
            <Select style="width:120px;margin-left:10px;" v-model="curField">
              <Option v-for="item in fields" :value="item.name" :key="item.name">{{ item.name }}</Option>
            </Select>
            <Select
              style="width:120px;margin-left:10px;"
              v-model="curOp"
              v-if="!isBoolType(curField)"
            >
              <Option v-for="item in opList" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
            <Select style="width:120px;margin-left:10px;" v-model="curOp" v-else>
              <Option v-for="item in opList2" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
            <div v-if="!(curOp=='IsNull')" style="display:inline-block;">
              <Input
                v-if="!isBoolType(curField)"
                v-model="curValue"
                :placeholder="getMsg(curField)"
                style="width: 200px;margin-left: 10px;"
              />
              <Select
                v-model="curValue"
                style="width: 200px;margin-left: 10px;"
                v-else
                :placeholder="getMsg(curField)"
              >
                <Option v-for="t in boolList" :value="t.value" :key="t.value">{{ t.label }}</Option>
              </Select>
            </div>

            <Checkbox
              v-model="bQueryLocal"
              style="margin-left: 10px;display:none;"
            >{{$t('label.cache')||''}}</Checkbox>
            <Button
              type="primary"
              icon="ios-search"
              style="margin-left: 10px;"
              class="modal-dialog-footer-ok"
              @click="handleSearch"
            >{{$t("common.button.query")||""}}</Button>
          </Col>
        </Row>-->
        <div style="border: 1px solid rgba(211,220,235,1);" class="cus-edit-table-header">
          <vue-scroll :ops="ops">
            <Table
              height="240"
              ref="selection"
              @on-row-click="handleRowClick"
              :row-class-name="rowClassName"
              :columns="columns"
              :data="dtList"
            ></Table>
          </vue-scroll>
        </div>
        <Row type="flex" align="middle" justify="space-between" style="margin-top:16px;">
          <Col span="8" style="text-align:left;">{{$t('common.text.total',{total:total})}}</Col>
          <Col span="16" style="text-align:right;">
            <Page
              :total="total"
              :page-size="pageSize"
              :current.sync="current"
              show-elevator
              @on-change="handleChange"
            />
          </Col>
        </Row>
      </div>
    </div>
  </div>
</template>
<script>
import { errorMsg } from "../utils/message";
import { opList, dataType, LK_EVENT, boolList } from "../const/enum";
import util from "../utils/util";
import { mapState } from "vuex";
export default {
  name: "SearchFeature",
  components: {},
  data() {
    //
    return {
      name: "",
      opList2: [opList[0], opList[1], opList[8]],
      boolList,
      ops: {
        scrollPanel: {
          scrollingX: true,
          scrollingY: true
        },
        bar: {
          background: "#ccc",
          keepShow: true,
          size: "4px"
        },
        rail: {
          background: "#f1f1f1",
          opacity: 1,
          size: "4px"
        }
      },
      layerId: null, //ID
      curField: "", //
      curOp: "==", //
      curValue: "", //
      dataset: {}, //
      opList, //
      pageSize: 5, //
      total: 0,
      current: 1, //
      visible: false,
      bQueryLocal: false, //,true
      /***
       *
       */
      dtList: [],
      columns: [],
      fields: [], //
      filter: [] //
    };
  },
  mounted() {},
  // data
  computed: {
    ...mapState(["map"])
  },

  watch: {
    map() {
      this.map && this.map.off("delete-layer", this.handleDeleteLayer);
      this.map && this.map.on("delete-layer", this.handleDeleteLayer);
    },
    curField(newVal, oldVal) {
      let field = this.findFieldByName(newVal);
      this.curOp = "==";
      if (field && field.type == dataType.BOOL) {
        this.curValue = "false";
      } else {
        this.curValue = "";
      }
    }
  },
  //
  methods: {
    /***
     * @description Bool
     */
    isBoolType(fieldName) {
      let field = this.findFieldByName(fieldName);
      if (field && field.type == dataType.BOOL) {
        return true;
      }
      return false;
    },
    /****
     * @description 
     */
    handleDeleteLayer(e) {
      if (this.visible && this.layerId !== null && this.layerId === e.layerId) {
        this.clear();
        this.visible = false;
        this.$bus.emit(LK_EVENT.CLOSE_SEARCHFEATURE);
      }
    },
    /****
     * @description 
     * @param{Object} {layerId:ID,dtId:ID}
     */
    showDialog(data) {
      this.clear();
      this.visible = true;
      this.layerId = data.layerId;
      this.filter = data.filter;
      this.dataset = this.map.findDatasetById(data.dtId);
      this.name = this.dataset.getName();
      this.fields = data.fields ? [...data.fields] : [];
      //ID
      if (this.fields && this.fields.length == 0) {
        this.fields.push({ name: "sid" });
      }
      this.fields &&
        this.fields.forEach((item, index) => {
          if (index == 0) {
            this.curField = item.name;
          }
          this.columns.push({
            title: item.name,
            key: item.name,
            ellipsis: true,
            tooltip: true,
            minWidth: 150
          });
        });
      this.handleSearch();
      //this.getDatasetMeta(data.dtId);
      this.$bus.emit(LK_EVENT.OPEN_SEARCHFEATURE);
    },
    /***
     *@description 
     */
    clear() {
      this.name = "";
      this.layerId = null;
      this.curField = "";
      this.curOp = "==";
      this.curValue = "";
      this.dataset = {};
      this.pageSize = 5;
      this.total = 0;
      this.current = 1;
      this.dtList = [];
      this.columns = [];
      this.fields = [];
      this.filter = [];
    },
    /***
     * @description 
     */
    closeDialog() {
      this.visible = false;
      this.$bus.emit(LK_EVENT.CLOSE_SEARCHFEATURE);
    },
    /***
     * 
     */
    rowClassName(row, index) {
      if (index % 2 === 0) {
        return "table-even";
      } else {
        return "table-odd";
      }
    },
    /***
     *
     * @deprecated
     * @description
     */
    handleCurrentChange(row) {
      console.log(row);
    },
    /****
     * @description 
     */
    handleRowClick(item, index) {
      if (
        this.dataset &&
        index >= 0 &&
        this.dtList &&
        index < this.dtList.length
      ) {
        let seFeature = L.QGIS.SeFeature.createGeoJSONFeatureFromGeoJSON({
          type: "Feature",
          geometry: this.dtList[index].geometry
        });
        if (seFeature) {
          let dtid = this.dataset.getID();
          let dt = this.map.findDatasetById(dtid);
          if (dt) {
            let tempFeature = dt.getFeatureById(item.id);
            let projInfo = dt.getProjInfo();
            let proj = projInfo && projInfo.createProj();
            if (
              tempFeature &&
              tempFeature.getEditAction() != L.QGIS.EditAction.DELETE
            ) {
              seFeature = tempFeature;
            }
            seFeature.setProjInfo(projInfo);
            seFeature.setProj(proj);
            seFeature.setID(item.id);
            let bounds = seFeature.getBounds();
            if (bounds) {
              if (proj) {
                let p1 = L.QGIS.UtilTool.projTransform(
                  L.point(bounds.getLeft(), bounds.getBottom()),
                  proj,
                  L.CRS.EPSG4326
                );
                let corner1 = L.latLng(p1.y, p1.x);
                let p2 = L.QGIS.UtilTool.projTransform(
                  L.point(bounds.getRight(), bounds.getTop()),
                  proj,
                  L.CRS.EPSG4326
                );
                let corner2 = L.latLng(p2.y, p2.x);
                let locBounds = L.latLngBounds(corner1, corner2);
                this.map.fitBounds(locBounds, { maxZoom: 17 });
              } else {
                let corner1 = L.latLng(bounds.getBottom(), bounds.getLeft());
                let corner2 = L.latLng(bounds.getTop(), bounds.getRight());
                let locBounds = L.latLngBounds(corner1, corner2);
                this.map.fitBounds(locBounds, { maxZoom: 17 });
              }
            }
            this.map.showIdentifyFeatures([seFeature]);
          }
        }
      }
    },
    /****
     * @description 
     * @param{String} idID
     */
    getDatasetMeta(id) {
      let options = {
        id,
        success: result => {
          this.dataset = result;
          this.fields =
            this.dataset && this.dataset.field_list
              ? this.dataset.field_list
              : [];
          this.columns = [];
          // this.columns.push({ title: "id", key: "id" });
          this.fields &&
            this.fields.forEach((item, index) => {
              if (index == 0) {
                this.curField = item.name;
              }
              this.columns.push({
                title: item.name,
                key: item.name,
                ellipsis: true,
                tooltip: true,
                minWidth: 150
              });
            });
          this.handleSearch();
        }
      };
      this.$http.getDatasetMeta(options);
    },
    /***
     * @description 
     */
    handleSearch() {
      this.current = 1;
      // this.total = 0;
      // this.dtList = [];
      this.queryData();
    },
    /***
     * @description 
     */
    handleChange() {
      // this.total = 0;
      // this.dtList = [];
      this.queryData();
    },
    /***
     * @description 
     * @returns{Array} 
     */
    getFieldNameList() {
      let arr = [];
      this.fields &&
        this.fields.forEach(item => {
          if (item.name != "sid") {
            arr.push(item.name);
          }
        });
      return arr;
    },
    /***
     * @description 
     * @returns{Object} 
     */
    findFieldByName(val) {
      if (util.isEmpty(val)) {
        return null;
      }
      let len = this.fields ? this.fields.length : 0;
      for (let i = 0; i < len; i++) {
        if (this.fields[i].name.toLowerCase() == val.toLowerCase()) {
          return this.fields[i];
        }
      }
      return null;
    },
    getMsg(name) {
      let field = this.findFieldByName(name);
      let msg = "";
      if (field) {
        switch (field.type) {
          case L.QGIS.DataType.DB_STRING:
          case dataType.STRING: {
            msg = this.$t("common.placeholder.stringType") || "";
            break;
          }
          case L.QGIS.DataType.DB_BIGINT:
          case L.QGIS.DataType.DB_INTEGER:
          case dataType.INT: {
            msg = this.$t("common.placeholder.intType") || "";
            break;
          }
          case dataType.FLOAT:
          case L.QGIS.DataType.DB_DOUBLE:
          case dataType.DOUBLE: {
            msg = this.$t("common.placeholder.numberType") || "";
            break;
          }
          case L.QGIS.DataType.DB_DATETIME:
          case dataType.DATETIME:
          case L.QGIS.DataType.DB_DATE:
          case dataType.DATE:
          case L.QGIS.DataType.DB_TIME:
          case dataType.TIME: {
            msg = this.$t("common.placeholder.dateType") || "";
            break;
          }
          case dataType.BOOL: {
            msg = this.$t("common.placeholder.boolType") || "";
            break;
          }
          default: {
            msg = this.$t("common.placeholder.unknowType") || "";
            break;
          }
        }
      } else {
        msg = this.$t("common.placeholder.unknowType") || "";
      }
      return msg;
    },
    /***
     * @description 
     */
    queryData() {
      // let filter = null;
      // if (this.curOp == "IsNull") {
      //   filter = ["all", ["==", this.curField, null]];
      // } else {
      //   let field = this.findFieldByName(this.curField);
      //   if (!field) {
      //     errorMsg(this.$t("message.fieldNullError"));
      //     return false;
      //   }

      //   if (
      //     field.type == dataType.FLOAT ||
      //     field.type == dataType.DOUBLE ||
      //     field.type == dataType.INT ||
      //     field.type == dataType.INTEGER ||
      //     field.type == dataType.BIGINT
      //   ) {
      //     if (util.isEmpty(this.curValue)) {
      //       errorMsg(this.$t("message.keyNullError"));
      //       return false;
      //     }

      //     if (this.curOp === "in" || this.curOp === "!in") {
      //       // ""","
      //       let arr1 = this.curValue.split(/,|/);
      //       // 
      //       filter = ["all"];
      //       let array = [this.curOp, this.curField];
      //       let enabled = true;
      //       arr1.forEach(v => {
      //         if (v.trim() !== "") {
      //           let v1 = util.parseNum(v.trim());
      //           if (v1 === null) {
      //             enabled = false;
      //             // break;
      //           }
      //           array.push(v1);
      //         }
      //       });
      //       if (enabled) {
      //         filter.push(array);
      //       } else {
      //         errorMsg(this.$t("message.numberNullError"));
      //         return false;
      //       }
      //     } else {
      //       let val = util.parseNum(this.curValue);
      //       if (val === null) {
      //         errorMsg(this.$t("message.numberNullError"));
      //         return false;
      //       } else {
      //         filter = ["all", [this.curOp, this.curField, val]];
      //       }
      //     }
      //   } else {
      //     if (this.curOp === "in" || this.curOp === "!in") {
      //       // ""","
      //       let arr1 = this.curValue.split(/,|/);
      //       // 
      //       filter = ["all"];
      //       let array = [this.curOp, this.curField];
      //       arr1.forEach(v => {
      //         if (v.trim() !== "") {
      //           array.push(v.trim());
      //         }
      //       });
      //       filter.push(array);
      //     } else {
      //       filter = ["all", [this.curOp, this.curField, this.curValue]];
      //     }
      //   }
      // }
      let filter = this.filter ? this.filter : [];
      if (!filter) {
        errorMsg(this.$t("message.filterNullError"));
        return false;
      }
      let id = this.dataset ? this.dataset.getID() : undefined;
      if (id == undefined) {
        errorMsg(this.$t("message.datasetNullError"));
        return false;
      }
      let fieldList = this.getFieldNameList();
      let queryDefine = {
        page_count: this.pageSize,
        page_num: this.current
      };
      let query = {
        dataset_id: id,
        field_list: fieldList,
        filters: filter
      };
      let query2 = {
        id: id,
        query: {
          fields: fieldList,
          filter: filter,
          option: 3
        },
        page: this.current,
        page_size: this.pageSize
      };
      if (this.bQueryLocal) {
        //
        let dataset = this.map.findDatasetById(id);
        if (dataset) {
          let result = dataset.queryFeaturesByFilter({
            filter: filter,
            page_count: this.pageSize,
            page_num: this.current
          });
          this.total = result ? result.total : 0;
          let features = result ? result.features : [];
          this.dtList = [];
          features &&
            features.forEach(item => {
              let newObj = { geometry: item.getGeometry() };
              Object.assign(newObj, { id: item.getID() }, item.getProperties());
              let obj1 = {};
              for (let key in newObj) {
                if (key == "id") {
                  obj1["sid"] = newObj[key];
                }
                if (newObj[key] == null || newObj[key] == undefined) {
                  obj1[key] = "NULL";
                } else {
                  obj1[key] = newObj[key];
                }
              }
              this.dtList.push(obj1);
            });
        }
      } else {
        //[]
        let dataset = this.map.findDatasetById(id);
        let existIds = [];
        let localFeatures = [];
        if (dataset) {
          existIds = dataset.queryFeatureIdsByNotFilterForCombine();
          localFeatures = dataset.queryFeaturesByFilterNew(filter);
        }
        query2.exist_ids = existIds;
        let options = {
          params: query2,
          success: result => {
            this.dtList = [];
            let dataset = result;
            let features = dataset && dataset.rows ? dataset.rows : [];
            this.total = dataset ? dataset.total : 0;
            this.total += localFeatures ? localFeatures.length : 0;
            if (features && features.length < this.pageSize) {
              if (localFeatures && localFeatures.length > 0) {
                let t1 = (this.current - 1) * this.pageSize + features.length;
                let startIndex = 0;
                let endIndex = 0;
                if (t1 >= dataset.total) {
                  startIndex = t1 - dataset.total;
                  endIndex = startIndex + this.pageSize;
                } else {
                  startIndex = 0;
                  let temp = dataset.total - t1;
                  endIndex = this.pageSize - temp;
                }
                let tempList = localFeatures.slice(startIndex, endIndex);
                if (tempList) {
                  tempList.forEach(item => {
                    features.push({
                      ...item.properties,
                      gid: item.id,
                      geom: JSON.stringify(item.geometry)
                    });
                  });
                }

                // let sPageCont = Math.ceil(this.total / this.pageSize);
                // let sYS = dataset.total % this.pageSize;//
                // if (this.current >= sPageCont) {
                //   let tmp = this.current - sPageCont;
                //   let startIndex = (tmp - 1) * this.pageSize;
                //   if (startIndex <= 0) {
                //     startIndex = 0;
                //   }
                //   else{
                //     startIndex+=(this.pageSize-sYS);
                //   }
                //   let temp2 = this.pageSize - features.length;
                //   let endIndex = startIndex + temp2;
                //   let tempList = localFeatures.slice(startIndex, endIndex);
                //   // if (tempList) {
                //   //   features = features.concat(tempList);
                //   // }
                //   if (tempList) {
                //   tempList.forEach(item => {
                //     features.push({
                //       ...item.properties,
                //       gid: item.id,
                //       geom: JSON.stringify(item.geometry)
                //     });
                //   });
                // }
                // }
              }
            }
            features &&
              features.forEach(item => {
                let newObj = {
                  geometry: item.geom ? JSON.parse(item.geom) : null
                };
                let properties = this.dataset.syncProperties(item);
                Object.assign(newObj, { id: item.gid }, properties);
                let obj1 = {};
                for (let key in newObj) {
                  if (key == "id") {
                    obj1["sid"] = newObj[key];
                  }
                  if (newObj[key] == null || newObj[key] == undefined) {
                    obj1[key] = "NULL";
                  } else {
                    obj1[key] = newObj[key];
                  }
                }
                this.dtList.push(obj1);
              });
          }
        };
        this.$http.queryDatasetBySQL(options);
      }
    }
  }
};
</script>
<style lang='less' scoped>
//@import url(); css
.search-containter {
  width: 100%;
  height: 100%;
  background: rgba(242, 246, 249, 1);
  box-shadow: 0px 0px 10px 0px rgba(51, 80, 110, 0.2);
}
.row-item {
  padding: 0 16px;
}
.search-header {
  height: 36px;
  font-size: 14px;
  font-family: SourceHanSansCN-Normal;
  font-weight: 400;
  color: #10243c;
  border-bottom: 1px solid #d3dceb;
}
.search-body {
  padding: 12px 16px;
}
div.temp {
  width: 500px;
  overflow: auto;
}
</style>


