<!--  -->
<template>
  <div>
    <!---->
    <div class="cus-vec-edit">
      <Input
        v-model="key"
        search
        enter-button
        size="large"
        :placeholder="$t('common.placeholder.key')||''"
        @on-search="handleSearch"
        @on-change="handleKeyChange"
      />
    </div>
    <transition name="fade">
      <!---->
      <div class="poi-container" v-show="bShow" :style="heightStyle">
        <vue-scroll ref="vs">
          <div style="background-color: #fff;">
            <div v-for="item in dataList" :key="item.id" @click="handleLocation(item)">
              <div class="poi-item">
                <div class="left">
                  <i class="iconfont icondingweiweizhizuobiaoxianxing"></i>
                </div>
                <div class="right">
                  <p class="first">{{item.name}}</p>
                  <p class="second">{{item.address}}</p>
                </div>
              </div>
            </div>
            <v-page
              v-show="total>pageSize"
              style="padding:10px 0;"
              :page-size-menu="false"
              v-model="currentPage"
              :info="false"
              :total-row="total"
              :first="false"
              :last="false"
              align="center"
              @page-change="handleChange"
            ></v-page>
          </div>
        </vue-scroll>
      </div>
    </transition>
  </div>
</template>
<script>
import { errorMsg } from "../utils/message";
import util from "../utils/util";
import { mapState } from "vuex";
import markerDefault from "../assets/img/marker_default.png";
import markerSelect from "../assets/img/marker_select.png";
import { LK_EVENT, GCJ02_PB_MAP, BD09_PB_MAP } from "../const/enum";
import { coordtransform } from "../utils/coordtransform";
const props = {
  height: {
    type: Number,
    default: 0
  }
};
export default {
  name: "PoiSearch",
  props,
  data() {
    //
    return {
      bShow: false,
      key: "", //
      pageSize: 20, //
      currentPage: 1, //
      total: 0, //
      dataList: [], //
      //height: 0,
      tempHeight: 0
    };
  },
  mounted() {
    // this.height = `${document.documentElement.clientHeight}`;
    // window.onresize = () => {
    //   this.height = `${document.documentElement.clientHeight}`;
    // };
    this.$bus.on(LK_EVENT.OPEN_SEARCHFEATURE, () => {
      this.tempHeight = 360;
    });
    this.$bus.on(LK_EVENT.CLOSE_SEARCHFEATURE, () => {
      this.tempHeight = 0;
    });
  },
  // data
  computed: {
    heightStyle() {
      return {
        height: this.height - 130 - this.tempHeight + "px"
      };
    },
    ...mapState(["city", "map", "curPbMap"])
  },
  //data
  watch: {
    curPbMap() {
      if (this.dataList && this.dataList.length > 0) {
        this.queryPoiData();
      }
    }
  },
  //
  methods: {
    /****
     * @description 
     */
    handleSearch() {
      this.currentPage = 1;
      this.queryPoiData();
    },
    /***
     * @description 
     */
    clear() {
      this.bShow = false;
      this.dataList = [];
      this.total = 0;
      this.currentPage = 1;
      this.clearMarkers();
    },
    /****
     * @description 
     */
    handleChange(pInfo) {
      this.queryPoiData();
    },
    handleKeyChange() {
      if (util.isEmpty(this.key)) {
        this.clear();
      }
    },
    /***
     * @description 
     */
    queryPoiData() {
      if (util.isEmpty(this.key)) {
        errorMsg(this.$t("message.keyNullError"));
        return false;
      }
      this.$refs.vs && this.$refs.vs.scrollTo({ y: 0 });
      let city = this.city && this.city.city ? this.city.city : "";
      let options = {
        //city: city,
        pageSize: this.pageSize,
        pageIndex: this.currentPage,
        keyword: this.key,
        success: result => {
          if (result && result.poiList) {
            this.bShow = true;
            let poiList = result.poiList;
            this.total = poiList.count;
            let list = poiList.pois ? poiList.pois : [];
            // if(BD09_PB_MAP.indexOf(this.curPbMap)>=0){
            //   //
            //   this.bdMapConvertor(list);
            //   return false;
            // }
            list.forEach(item => {
              var lnglat = item.location;
              let crs = this.map.options.crs;
              let lnglat2 = null;
              if (GCJ02_PB_MAP.indexOf(this.curPbMap) >= 0) {
                lnglat2 = [lnglat.lng, lnglat.lat];
              } 
              // else if (BD09_PB_MAP.indexOf(this.curPbMap) >= 0) {
              //   lnglat2 = coordtransform.gcj02tobd09(lnglat.lng, lnglat.lat);
              // }
               else {
                lnglat2 = coordtransform.gcj02towgs84(lnglat.lng, lnglat.lat);
              }
              item.location.lng = lnglat2[0];
              item.location.lat = lnglat2[1];
            });
            this.dataList = list;
          } else {
            this.bShow = false;
            this.dataList = [];
            this.total = 0;
          }
          this.addMarkers(this.dataList);
        }
      };
      this.$http.searchPoiByGaoDe(options);
    },
    bdMapConvertor(list) {
      let points = [];
      if (list && list.length) {
        list.forEach(item => {
          points.push(new BMap.Point(item.location.lng, item.location.lat));
        });
      }
      if (points && points.length > 0) {
        var convertor = new BMap.Convertor();
        convertor.translate(points, 3, 5, data => {
          let array=data.points;
          array && array.forEach((ele, index) => {
              list[index].location.lng = 120.7457571011//116.3850791893;
              list[index].location.lat = 31.2706751991//39.8713583747;
            });
            this.dataList = list;
            this.addMarkers(this.dataList);
        });
      }
    },
    /****
     * @description Markers
     */
    addMarkers(list) {
      let bSetView = false;
      let minX, minY, maxX, maxY;
      this.clearMarkers();
      let myIcon = L.icon({
        iconUrl: markerDefault,
        iconSize: [20, 30],
        iconAnchor: [12, 30],
        popupAnchor: [0, -32]
      });

      list &&
        list.forEach((item, index) => {
          let marker = L.marker([item.location.lat, item.location.lng], {
            icon: myIcon,
            title: item.name
          });
          marker.bindPopup(item.name, { closeButton: false });
          marker.on("click", () => {
            this.clearSelectMarker();
            this.addSelectMarker(item);
          });
          this.map && this.map.addLayer(marker);
          this.map && this.map.tempMarkers.push(marker);
          if (!bSetView) {
            bSetView = true;
          }
          if (index == 0) {
            maxX = minX = item.location.lng;
            minY = maxY = item.location.lat;
          } else {
            minX = minX > item.location.lng ? item.location.lng : minX;
            maxX = maxX > item.location.lng ? maxX : item.location.lng;
            minY = minY > item.location.lat ? item.location.lat : minY;
            maxY = maxY > item.location.lat ? maxY : item.location.lat;
          }
        });
      if (bSetView) {
        let sw = L.latLng(minY, minX);
        let ne = L.latLng(maxY, maxX);
        let bounds = L.latLngBounds(sw, ne);
        this.map && this.map.flyToBounds(bounds, { maxZoom: 17 });
      }
    },
    /****
     * @description Markers
     */
    clearMarkers() {
      let markers =
        this.map && this.map.tempMarkers ? this.map.tempMarkers : [];
      markers &&
        markers.forEach(element => {
          this.map.removeLayer(element);
        });
      this.map.tempMarkers = [];
      this.clearSelectMarker();
    },
    /****
     * @description marker
     */
    addSelectMarker(item) {
      if (item && this.map) {
        let selIcon = L.icon({
          iconUrl: markerSelect,
          iconSize: [20, 30],
          iconAnchor: [12, 30],
          popupAnchor: [0, -32]
        });
        let marker = L.marker([item.location.lat, item.location.lng], {
          icon: selIcon,
          title: item.name
        });
        this.map.selectMarker = marker;
        this.map.addLayer(marker);
        marker.bindPopup(item.name, { closeButton: false }).openPopup();
      }
    },
    /****
     * @description marker
     */
    clearSelectMarker() {
      this.map &&
        this.map.selectMarker &&
        this.map.removeLayer(this.map.selectMarker);
      this.map.selectMarker = null;
    },
    /****
     * @description Marker
     */
    handleLocation(item) {
      this.clearSelectMarker();
      this.addSelectMarker(item);
    }
  }
};
</script>
<style lang='less' scope>
.poi-container {
  margin-top: 10px;
  // height: 800px;
}
.poi-item {
  display: flex;
  align-items: center;
  padding: 10px 20px 10px 10px;
}
.poi-item:hover {
  background-color: #f6f6f6;
  cursor: pointer;
}
.poi-item .left {
  // height: 67px;
  // line-height: 67px;
  vertical-align: middle;
  padding: 0px 10px;
}
.poi-item i {
  font-size: 20px;
}
.poi-item:hover i {
  color: #44aaf6;
}
.poi-item .right {
  text-align: left;
  font-size: 14px;
}
.right .first {
  color: #44aaf6;
}
.right .second {
  color: #5e6b8e;
}
</style>


