<!--HEADER-->
<template>
  <div @click="handleClick" style="cursor:pointer;">
    <img src="../assets/img/logo.png" >
    <span>{{$t("header.title")||""}}</span>
    </img>
  </div>
</template>
<script>
import { dashboard_url } from "@/const/enum";
export default {
  name: "Header",
  components: {},
  data() {
    //
    return {};
  },
  methods: {
    handleClick() {
      location.href = dashboard_url;
    }
  }
};
</script>
<style lang="less"  scoped>
div {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 70px;
  height: 0.7rem;
  background: #3a4349 !important;
  box-shadow: 0px 0px 10px 0px rgba(51, 80, 110, 0.2);
}
div > img {
  margin-left: 10px;
  margin-left: 0.1rem;
  cursor: pointer;
  height: 30px;
  height: 0.3rem;
  display: none;
}
span {
  font-size: 25px;
  font-size: 0.25rem;
  padding-left: 10px;
  padding-left: 0.1rem;
  cursor: pointer;
  color: white;
  // font-style: italic;
}
</style>


