<!--  -->
<template>
  <div class="q-attr-containter">
    <div class="q-attr-header">
      <div class="left">
        <div
          @click="handleClick(true)"
          class="q-attr-button btn-setting"
          :class="[isSetting ? 'btn-active':'btn-unactive']"
        ></div>
        <div
          @click="handleClick(false)"
          class="q-attr-button btn-json"
          :class="[!isSetting ? 'btn-active':'btn-unactive']"
        >JSON</div>
      </div>
      <div class="q-attr-button btn-ok" @click="handleOK"></div>
    </div>
    <!--setting-->
    <ExpFilter
      v-show="isSetting"
      :fields="selfLayerInfo.fields"
      :dtid="selfLayerInfo.dataset_id"
      @filter-setting="handleFilterSetting"
    ></ExpFilter>
    <!--JSON-->
    <div style="padding: 10px;padding:0.1rem;width:300px;text-align:left;">
      <codemirror
        ref="myCm"
        v-if="!isSetting"
        v-model="itemcode.content"
        :options="cmOptions"
        @changes="hanndleChanges"
      ></codemirror>
    </div>
  </div>
</template>

<script>
import ExpFilter from "./base/ExpFilter";
import util from "../utils/util";
import { dataType } from "../const/enum";
import { codemirror } from "vue-codemirror";
import "codemirror/addon/scroll/simplescrollbars.js";
import "codemirror/addon/scroll/simplescrollbars.css";
import "codemirror/mode/javascript/javascript.js";
import "codemirror/theme/monokai.css";
import { errorMsg } from "../utils/message";
const props = {
  layerInfo: {
    type: Object,
    default: () => {
      return {};
    }
  }
};
export default {
  props,
  components: {
    ExpFilter,
    codemirror
  },
  data() {
    //
    let temp = this.layerInfo ? util.deepCopy(this.layerInfo) : {};
    return {
      isSetting: true, //true;falseJSON
      selfLayerInfo: temp,
      filters: [], //
      itemcode: {
        content: ""
      },
      cmOptions: {
        value: "",
        tabSize: 4,
        mode: "application/json",
        //theme: 'monokai',
        scrollbarStyle: "simple",
        lineNumbers: true,
        lineWrapping: true,
        // lineWrapping: true,
        // extraKeys: {"Ctrl": "autocomplete"},
        // lineWiseCopyCut: true,
        // showCursorWhenSelecting: true,
        // matchBrackets: true,
        line: true,
        autofocus: true,
        styleActiveLine: true
      }
    };
  },
  // data
  computed: {},
  //data
  watch: {
    layerInfo: {
      handler(newVal, oldVal) {
        let temp = newVal ? util.deepCopy(newVal) : {};
        this.selfLayerInfo = temp;
      },
      deep: true
    }
  },
  //
  methods: {
    hanndleChanges() {
      console.log(this.itemcode.content);
    },
    handleClick(val) {
      this.isSetting = val;
    },
    handleFilterSetting(data) {
      this.filters = data;
    },
    /**
     * @description JSON
     */
    isJSON(jsonStr) {
      if (jsonStr && typeof jsonStr == "string") {
        try {
          JSON.parse(jsonStr);
          return true;
        } catch (e) {
          return false;
        }
      }
      return false;
    },

    /**
     * @description 
     */
    isValidFilter(json) {
      let val = JSON.parse(json);
      let enable = util.isExpressionFilter(val);
      return !enable;
    },
    /****
     * @description 
     */
    handleOK() {
      let item = this.selfLayerInfo;
      if (!item) {
        return false;
      }
      //JSON
      if (!this.isSetting) {
        if (!util.isEmpty(this.itemcode.content)) {
          let v2 = this.itemcode.content
            .replace(/ +/g, "")
            .replace(/[\r\n]/g, ""); // 
          if (v2 === "") {
            let params = [];
            this.$emit("query-filter-json", params);
          } else {
            // JSON
            if (this.isJSON(v2) && this.isValidFilter(v2)) {
              let params = JSON.parse(v2);
              this.$emit("query-filter-json", params);
            } else {
              errorMsg("JSON");
            }
          }
        } else {
          this.$emit("query-filter-json", []);
        }
        return true;
      }
      //
      let filter = this.filters;
      let fields = item.fields;
      let newList = null;
      let bRet = false;
      // console.log(filter)
      if (!filter || !fields || fields.length == 0) {
        return false;
      }
      if (filter && fields && fields.length > 0) {
        let list = filter.list;
        let len = list ? list.length : 0;
        let count = fields.length;
        for (let i = 0; i < len; i++) {
          let field = null;
          for (let j = 0; j < count; j++) {
            if (fields[j].name === list[i].name) {
              field = fields[j];
              break;
            }
          }
          if (!field) continue;
          if (list[i].operator == "IsNull") {
            if (!newList) {
              newList = [];
            }
            newList.push({
              name: list[i].name,
              operator: "IsNull",
              checkedList: [],
              value: null
            });
          } else {
            if (
              field.type != L.QGIS.DataType.DB_DATETIME &&
              field.type != L.QGIS.DataType.DB_DATE &&
              field.type != L.QGIS.DataType.DB_TIME &&
              field.type != L.QGIS.DataType.DB_STRING
            ) {
              if (
                list[i].checkedList == null ||
                list[i].checkedList.length <= 0
              ) {
                errorMsg(
                  this.$t("message.filterNotValidError", {
                    filter: list[i].name + list[i].operator + list[i].value
                  })
                );
                bRet = true;
                break;
              }
            }
            let value = null;
            let bValid = false;
            let checkedList = list[i].checkedList;
            switch (field.type) {
              case L.QGIS.DataType.DB_INTEGER:
              case L.QGIS.DataType.DB_INT:
              case L.QGIS.DataType.DB_BIGINT: {
                // bValid = util.isValidInteger(list[i].value);
                bValid = checkedList.every(ele => {
                  return util.isValidInteger(ele.value);
                });
                if (bValid) {
                  // value = util.parseNum(list[i].value);
                  value = list[i].value;
                }
                break;
              }
              case L.QGIS.DataType.DB_FLOAT:
              case L.QGIS.DataType.DB_DOUBLE: {
                // bValid = util.isValidDecimal(list[i].value);
                bValid = checkedList.every(ele => {
                  return util.isValidDecimal(ele.value);
                });
                if (bValid) {
                  // value = util.parseNum(list[i].value);
                  value = list[i].value;
                }
                break;
              }
              case L.QGIS.DataType.DB_DATETIME: {
                if (list[i].value == null || list[i].value.trim() == "") {
                  value = "";
                  bValid = true;
                } else {
                  // bValid = util.isValidDate(list[i].value);
                  bValid = checkedList.every(ele => {
                    return util.isValidDate(ele.value);
                  });
                  if (bValid) {
                    value = list[i].value;
                  }
                }
                break;
              }
              case L.QGIS.DataType.DB_DATE: {
                if (list[i].value == null || list[i].value.trim() == "") {
                  value = "";
                  bValid = true;
                } else {
                  // bValid = util.isDatePart(list[i].value);
                  bValid = checkedList.every(ele => {
                    return util.isDatePart(ele.value);
                  });
                  if (bValid) {
                    value = list[i].value;
                  }
                }
                break;
              }
              case L.QGIS.DataType.DB_TIME: {
                if (list[i].value == null || list[i].value.trim() == "") {
                  value = "";
                  bValid = true;
                } else {
                  // bValid = util.isTimePart(list[i].value);
                  bValid = checkedList.every(ele => {
                    return util.isTimePart(ele.value);
                  });
                  if (bValid) {
                    value = list[i].value;
                  }
                }
                break;
              }
              case L.QGIS.DataType.DB_STRING: {
                bValid = true;
                value = list[i].value;
                if (value == null) {
                  value = "";
                }
                break;
              }
              case dataType.BOOL: {
                //bool
                value = list[i].value;
                if (value && value == "false") {
                  value = "false";
                  bValid = true;
                } else if (value && value == "true") {
                  value = "true";
                  bValid = true;
                } else {
                  bValid = false;
                  value = null;
                }
                break;
              }
            }
            if (!bValid) {
              errorMsg(
                this.$t("message.filterNotValidError", {
                  filter: list[i].name + list[i].operator + list[i].value
                })
              );
              bRet = true;
              break;
            } else {
              if (!newList) {
                newList = [];
              }
              newList.push({
                name: list[i].name,
                operator:
                  list[i].operator == "IsNull" ? "==" : list[i].operator,
                value: list[i].operator == "IsNull" ? null : value,
                checkedList: list[i].checkedList,
                type: field.type
              });
            }
          }
        }
      }
      if (bRet) {
        return false;
      }
      this.$emit("query-filter-setting", { op: filter.op, list: newList });
    }
  }
};
</script>
<style lang='less' scoped>
//@import url(); css
.q-attr-containter {
  background: #37404a;
  color: #fff;
}
.q-attr-header {
  height: 50px;
  height: 0.5rem;
  border-bottom: solid 1px #33506e;
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  padding: 0 0.1rem;
  align-items: center;
}
.q-attr-header .left {
  display: flex;
}
.q-attr-button {
  cursor: pointer;
  border-radius: 12px;
  font-size: 14px;
  font-size: 0.14rem;
  font-family: Source Han Sans SC;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
}
.btn-setting {
  //width: 66px;
  height: 24px;
}
.btn-json {
  //width: 88px;
  height: 24px;
  margin-left: -12px;
}
.btn-active {
  width: 66px;
  background: rgba(68, 170, 246, 1);
  color: #fff;
  z-index: 100;
}
.btn-unactive {
  width: 88px;
  background: #172b3d;
  color: #d3dceb;
  z-index: 10;
}
.btn-ok {
  width: 48px;
  height: 24px;
  background: rgba(68, 170, 246, 1);
  color: rgba(255, 255, 255, 1);
}
</style>


