<!--  -->
<template>
  <div class="pb-containter">
    <div v-for="(item,index) in PB_MAP" :key="index" @click="handleChangeMap(item)">
      <div :style="{backgroundImage:'url(' + item.imgUrl + ')'}"></div>
      <p>{{item.name}}</p>
    </div>
  </div>
</template>

<script>
import { PB_MAP } from "../../const/enum";
export default {
  //import
  components: {},
  data() {
    //
    return {
      PB_MAP
    };
  },
  // data
  computed: {},
  //data
  watch: {},
  //
  methods: {
    handleChangeMap(item) {
      this.$emit("change-map", item);
    }
  },
  // - this
  created() {},
  // - DOM
  mounted() {},
  beforeCreate() {}, // - 
  beforeMount() {}, // - 
  beforeUpdate() {}, // - 
  updated() {}, // - 
  beforeDestroy() {}, // - 
  destroyed() {}, // - 
  activated() {} //keep-alive
};
</script>
<style lang='less' scoped>
//@import url(); css
.pb-containter {
  color: #fff;
  background: #171d24;
  padding: 16px 8px;
}
.pb-containter > div {
  //background: #213a52;
  display: inline-block;
  margin: 0 8px;
}
.pb-containter > div > div {
  border-radius: 4px;
  cursor: pointer;
  background-size: 126px 90px;
  width: 126px;
  height: 90px;
}
.pb-containter > div > div:hover {
  border: 2px solid rgba(68, 170, 246, 1);
  transition: border 0.5s;
}
.pb-containter > div > p {
  font-size: 12px;
  font-family: Source Han Sans CN;
  font-weight: 400;
  color: rgba(211, 220, 235, 1);
  line-height: 30px;
  height: 30px;
  vertical-align: middle;
  text-align: center;
}
</style>


