<!--  -->
<template>
  <div class="m-i-containter">
    <span>{{zoom}}</span>
    <span class="v-line"></span>
    <span>{{location}}</span>
  </div>
</template>

<script>
import { mapState } from "vuex";
import util from "../utils/util";
export default {
  components: {},
  data() {
    return {
      zoom: 0,
      latLng: null
    };
  },
  // data
  computed: {
    ...mapState(["map"]),
    location() {
      if (this.latLng) {
        return (
          util.formatNum(this.latLng.lng) +
          "," +
          util.formatNum(this.latLng.lat)
        );
      } else {
        return "0,0";
      }
    }
  },
  //data
  watch: {
    map() {
      this.map && this.map.off("mousemove", this.getMapInfo);
      this.map && this.map.on("mousemove", this.getMapInfo);
    }
  },
  //
  methods: {
    getMapInfo(e) {
      if (this.map) {
        this.zoom = this.map.getZoom().toFixed(0);
        this.latLng = e.latlng;
      }
    }
  }
};
</script>
<style lang='less' scoped>
.m-i-containter {
  height: 36px;
  background: rgba(255, 255, 255, 1);
  border: 1px solid rgba(211, 220, 235, 1);
  box-shadow: 0px 0px 10px 0px rgba(51, 80, 110, 0.2);
  border-radius: 4px;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.m-i-containter span {
  font-size: 14px;
  font-family: Source Han Sans CN;
  font-weight: 400;
  color: rgba(16, 36, 60, 1);
}
.v-line {
  display: inline-block;
  height: 20px;
  width: 1px;
  margin: 0 10px;
  border: 1px solid rgba(211, 220, 235, 1);
}
</style>


