<!---->
<template>
  <Modal
    :styles="styles"
    :width="width"
    v-model="isShowModal"
    class-name="vertical-center-modal"
    :closable="false"
    :mask-closable="false"
  >
    <Row slot="header" v-show="isShowHeader" type="flex" class="modal-dialog-item modal-dialog-header" align="middle">
      <i-col span="16" style="text-align:left;">
        <span>{{title}}</span>
      </i-col>
      <i-col span="8" style="text-align:right;">
        <i
          class="iconfont iconguanbi-"
          style="cursor: pointer;font-size:14px;font-size:0.14rem;"
          @click="closeDialog"
        ></i>
      </i-col>
    </Row>
    <slot></slot>
    <Row slot="footer" type="flex" align="middle" v-show="isShowBottomBtn">
      <Col span="24" style="text-align:right;">
        <Button
          class="modal-dialog-footer modal-dialog-footer-cancle"
          @click="closeDialog"
        >{{$t('common.button.cancel')||''}}</Button>
        <Button
          type="primary"
          class="modal-dialog-footer modal-dialog-footer-ok"
          :disabled="okBtn"
          @click="handleOk"
        >{{$t('common.button.confirm')||''}}</Button>
      </Col>
    </Row>
  </Modal>
</template>

<script>
const props = {
  width: {
    type: [String, Number],
    default: 520
  },
  title: {
    type: String,
    default: ""
  },
  visible: {
    type: Boolean,
    default: false
  },
  styles: {
    type: Object,
    default: () => {}
  },
  isShowBottomBtn: {
    type: Boolean,
    default: () => true
  },
  isShowHeader: {
    type: Boolean,
    default: () => true
  },
  okBtn: {
    type: Boolean,
    default: () => false
  }
};
export default {
  name:"DialogModal",
  props,
  data() {
    //
    return {
      selfVisible: this.visible
    };
  },
  computed: {
    isShowModal: {
      get() {
        return this.selfVisible;
      },
      set(val) {
        this.selfVisible = val;
      }
    }
  },
  //
  methods: {
    closeDialog() {
      this.$emit("cus_cancel");
    },
    handleOk() {
      this.$emit("cus_ok");
    }
  },
  watch: {
    visible(newVal) {
      this.selfVisible = newVal;
    }
  }
};
</script>
<style lang='less' scoped>
//@import url(); css
</style>



