<!---->
<template>
  <div class="attr-c-default-lable cus-vec-edit">
    <!--header-->
    <Row type="flex" class="attr-c-row-item attr-c-header" align="middle">
      <Col
        span="12"
        style="text-align:left;border-right: 2px solid #D3DCEB;height: 100%;display: flex;align-items: center;"
      >
        <span></span>
      </Col>
      <Col span="12">
        <Row type="flex" align="middle">
          <Col span="12" style="text-align:left;">
            <span style="padding-left:20px;"></span>
          </Col>
          <Col span="12" style="text-align:right;">
            <i
              class="iconfont iconguanbi-"
              style="font-size:12px;cursor:pointer;"
              @click="closeDialog"
            ></i>
          </Col>
        </Row>
      </Col>
    </Row>
    <!--body-->
    <div style="height:302px;">
      <vue-scroll :ops="ops">
        <!---->
        <Row type="flex" align="middle" class="attr-c-item">
          <Col span="12" class="attr-c-body-item attr-c-split-line attr-c-item attr-c-item-left">
            <div class="label-ellipsis" :title="datasetName">{{datasetName}}</div>
          </Col>
          <Col span="12" class="attr-c-body-item attr-c-item attr-c-item-left">
            <div class="label-ellipsis" :title="datasetName">{{datasetName}}</div>
          </Col>
        </Row>
        <!---->
        <div class="attr-c-content-pannel">
          <!--left--->
          <div class="attr-c-content-item attr-c-split-line">
            <Row
              type="flex"
              align="middle"
              class="attr-c-item"
              v-for="(item,index) in attris"
              :key="index"
            >
              <Col
                span="24"
                class="attr-c-body-item attr-c-item attr-c-item-left"
                style="background:rgba(242,246,249,1);"
              >
                <div class="attr-c-att-pannel">
                  <div class="label-ellipsis attr-left" :title="item.name">{{item.name}}</div>
                  <p
                    class="label-ellipsis"
                    :class="[item.isSame?'attr-right':'attr-right-not-same']"
                    :title="item.remoteValue"
                  >{{item.remoteValue}}</p>
                </div>
              </Col>
            </Row>
          </div>
          <!--right--->
          <div class="attr-c-content-item">
            <Row
              type="flex"
              align="middle"
              class="attr-c-item"
              v-for="(item,index) in attris"
              :key="index"
            >
              <Col
                span="24"
                class="attr-c-body-item attr-c-item attr-c-item-left"
                style="background:rgba(242,246,249,1);"
              >
                <div class="attr-c-att-pannel">
                  <div class="label-ellipsis attr-left" :title="item.name">{{item.name}}</div>
                  <p
                    class="label-ellipsis"
                    :class="[item.isSame?'attr-right':'attr-right-not-same']"
                    :title="item.localValue"
                  >{{item.localValue}}</p>
                </div>
              </Col>
            </Row>
          </div>
        </div>
      </vue-scroll>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
import { dataType, boolList, NULL } from "../../const/enum";
import util from "../../utils/util";
import { errorMsg, successMsg } from "../../utils/message";
const props = {
  datasetName: {
    type: String,
    default: ""
  },
  attris: {
    type: Array,
    default: () => []
  }
};
export default {
  props,
  data() {
    return {
      ops: {
        scrollPanel: {
          scrollingX: false,
          scrollingY: true
        },
        bar: {
          background: "#ccc",
          keepShow: true,
          size: "4px"
        },
        rail: {
          background: "#f1f1f1",
          opacity: 1,
          size: "4px"
        }
      }
      // /****
      //  * @param{String} datasetName 
      //  */
      // datasetName: "",
      // attris: [
      //   {
      //     name: "", //
      //     localValue: "", //
      //     remoteValue:"", //
      //     isSame: true //,true
      //   }
      // ] //
    };
  },
  computed: {},
  watch: {},
  methods: {
    /****
     * @description 
     */
    showDialog() {
      this.visible = true;
    },
    /****
     * @description 
     */
    closeDialog() {
      this.$emit("attri-close", false);
    }
  },
  mounted() {}
};
</script>
<style lang="less" scope>
.attr-c-row-item {
  padding: 0 20px;
}
.attr-c-header {
  height: 40px;
  font-size: 14px;
  font-family: SourceHanSansCN-Normal;
  font-weight: 400;
  color: #10243c;
  border-bottom: 1px solid #d3dceb;
}
.attr-c-body-item {
  padding: 0px 10px 0px 20px;
  background: rgba(255, 255, 255, 1);
}
.attr-c-item {
  height: 40px;
}
.attr-c-split-line {
  border-right: 2px solid #d3dceb;
}
.attr-c-default-lable {
  font-size: 14px;
  font-family: Source Han Sans CN;
  font-weight: 400;
  color: rgba(16, 36, 60, 1);
}
.attr-c-item-left {
  display: flex;
  align-items: center;
}
.attr-c-att-pannel {
  display: flex;
  align-items: center;
  padding-top: 5px;
  height: 100%;
  width: 100%;
}
.attr-c-att-pannel .attr-left {
  flex: 1;
  padding-right: 10px;
}
.attr-c-att-pannel .attr-right {
  width: 140px;
  background-color: #d7e7f2;
  height: 100%;
  line-height: 35px;
  padding-left: 10px;
}
.attr-c-att-pannel .attr-right-not-same {
  width: 140px;
  background-color: #f2d7d7;
  height: 100%;
  line-height: 35px;
  padding-left: 10px;
}
.attr-c-content-pannel {
  display: flex;
}
.attr-c-content-pannel .attr-c-content-item {
  width: 50%;
  min-height: 262px;
}
.attr-c-content-item > .attr-c-item:last-child {
  height: 45px;
  padding-bottom: 5px;
}
</style>



