<template>
  <div id="contont">
    <transition name="fade">
      <div class="left-pannel" v-show="isExpand">
        <div ref="temp">
          <Header></Header>
          <LeftTool></LeftTool>
        </div>
        <LayerSortableList
          @layer-tile-visible="handleLayerTileVisible"
          @dataset-query="handleDatasetQuery"
          @tilelayer-gray="handleTilelayerGray"
          :style="layerListHeight"
        ></LayerSortableList>
      </div>
    </transition>
    <!-- <div  class="center-pannel" @click="handleExpand" :style="centerPannelStyle"></div> -->
    <!-- <CusPage class="center-pannel-page" :style="centerPagePannelStyle"></CusPage> -->
    <Map class="right-pannel" :style="rightPannelStyle" ref="map"></Map>
    <transition name="fade">
      <SearchFeature ref="searchFeature" class="search-feature-pannel" :style="rightPannelStyle"></SearchFeature>
    </transition>
    <!---->
    <div class="right-top-pannel">
      <PoiSearch :height="totalHeight"></PoiSearch>
    </div>
    <!---->
    <div class="left-top-pannel" v-show="isExpand">
      <MapTool></MapTool>
    </div>
    <MapInfo class="right-bottom-pannel"></MapInfo>
    <Dialog :title="title" :visible="visible" @cus_cancel="handleClose" @cus_ok="handleOk">
      <Row style="padding:6px ; font-size:14px;">
        <Col span="24" style="color:red; ">{{msg}}</Col>
      </Row>
    </Dialog>
  </div>
</template>
<script>
import Header from "../components/Header";
import LeftTool from "../components/LeftTool";
import LayerSortableList from "../components/LayerSortableList";
import Map from "../components/Map";
import SearchFeature from "../components/SearchFeature";
import PoiSearch from "../components/PoiSearch";
import MapTool from "../components/MapTool";
import { mapState } from "vuex";
import Dialog from "../components/Dialog";
import { errorMsg, infoMsg, successMsg } from "../utils/message";
import MapInfo from "../components/MapInfo";
// import CusPage from "../components/CusPage";
export default {
  name: "Edit",
  data() {
    return {
      /***
       * @description 
       * @param{Boolean} isExpand true,false
       */
      isExpand: true,
      /****
       * @description Header+Tool
       */
      heigh1: 0,
      /****
       * @description 
       */
      totalHeight: 0,
      /***
       * @description 
       */
      title: this.$t("dialog.confirm") || "",
      /***
       * @description 
       */
      visible: false,
      /**
       * @description 
       */
      msg: this.$t("dialog.saveFeaturesMsg")
    };
  },
  components: {
    Header,
    LeftTool,
    LayerSortableList,
    Map,
    SearchFeature,
    PoiSearch,
    MapTool,
    Dialog,
    MapInfo
  },
  methods: {
    /***
     * @description 
     */
    handleLayerTileVisible(bVisible) {
      this.$refs.map && this.$refs.map.changeTileMap(bVisible);
    },
    handleTilelayerGray(data) {
      let { gray } = data;
      this.$refs.map && this.$refs.map.changeTileLayers(gray);
    },
    /****
     * @description Header
     */
    handleHeaderSize(result) {
      console.log(result);
    },
    /***
     * 
     */
    handleTest() {
      this.$refs.searchFeature.showDialog(1);
    },
    /****
     * @description 
     * @param{String} dtIdID
     */
    handleDatasetQuery(data) {
      this.$refs.searchFeature.showDialog(data);
    },
    /***
     * @description 
     */
    handleExpand() {
      this.isExpand = !this.isExpand;
      this.$refs.map.refresh();
    },
    /***
     * @description 
     */
    handleClose() {
      this.visible = false;
    },
    /***
     * @description 
     */
    handleOk() {
      if (this.editFeatures && this.editFeatures.length > 0) {
        let options = {
          params: { dataset_list: this.editFeatures },
          success: result => {
            if (
              result &&
              result.dataset_list &&
              result.dataset_list.length > 0
            ) {
              let dts = result.dataset_list;
              let arr = [];
              for (let i = 0; i < dts.length; i++) {
                let dt = this.map.findDatasetById(dts[i].dataset_id);
                if (!dt) continue;
                if (dts[i].code !== 0) {
                  arr.push(dts[i].getName());
                } else {
                  dt.clearData();
                }
              }
              if (arr.length > 0) {
                let s = "" + arr.join(",") + "";
                errorMsg(s + this.$t("message.fail"));
              }
              this.map.fire("edit-state", { isUndo: false, isRedo: false });
              this.map.fire("edit-finish");
              this.map.refresh();
              this.visible = false;
            } else {
              errorMsg(this.$t("message.fail"));
              this.visible = false;
            }
          }
        };
        this.$http.saveAllDatasetFeatures(options);
      } else {
        infoMsg(this.$t("message.noEditData"));
        this.visible = false;
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.heigh1 = this.$refs.temp.clientHeight;
      this.totalHeight = `${document.documentElement.clientHeight}`;
      this.totalHeight *= 1;
      window.onresize = () => {
        this.totalHeight = `${document.documentElement.clientHeight}`;
        this.totalHeight *= 1;
        this.heigh1 = this.$refs.temp.clientHeight;
      };
      document.title = this.$t("appTitle");
      window.onbeforeunload = () => {
        return "Are you sure to leave?";
      };
      // window.addEventListener("beforeunload", e => {
      //   let editFeatures = this.map && this.map.getAllDatasetEditFeatures();
      //   if (editFeatures && editFeatures.length > 0) {
      //     e = e || window.event;
      //     // IE8Firefox 4
      //     if (e) {
      //       e.returnValue = "";
      //     }
      //     // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
      //     return "";
      //   }
      // });
    });
  },
  // data
  computed: {
    layerListHeight() {
      return {
        height: this.totalHeight - this.heigh1 + "px"
      };
    },
    rightPannelStyle() {
      if (this.isExpand) {
        return {
          left: "300px",
          width: "calc(100% - 300px)"
        };
      } else {
        return {
          left: "0px",
          width: "100%"
        };
      }
    },
    centerPannelStyle() {
      if (this.isExpand) {
        return {
          left: "286px"
        };
      } else {
        return {
          left: "10px"
        };
      }
    },
    centerPagePannelStyle() {
      if (this.isExpand) {
        return {
          left: "350px"
        };
      } else {
        return {
          left: "10px"
        };
      }
    },
    ...mapState(["map"])
  }
};
</script>
<style scoped lang="less">
#contont {
  width: 100%;
  height: 100%;
  background-color: burlywood;
  position: relative;
}
.left-pannel {
  position: absolute;
  display: inline-block;
  left: 0px;
  top: 0px;
  width: 300px;
  //width: 3rem;
  height: 100%;
  z-index: 1000;
  background: #171d24;
  box-shadow: 0px 0px 10px 0px rgba(51, 80, 110, 0.2);
}
.right-pannel {
  position: absolute;
  display: inline-block;
  height: 100%;
}
.center-pannel {
  position: absolute;
  top: 20px;
  top: 0.2rem;
  background-image: url(../assets/img/zk.png);
  background-size: 0.28rem auto;
  width: 28px;
  width: 0.28rem;
  height: 32px;
  height: 0.32rem;
  z-index: 1000;
  cursor: pointer;
}
.center-pannel-page {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 350px;
  //left: 3.5rem;
  z-index: 1000;
}
.search-feature-pannel {
  position: absolute;
  display: inline-block;
  bottom: 0px;
  z-index: 1000;
}
.right-top-pannel {
  position: absolute;
  display: inline-block;
  top: 20px;
  top: 0.2rem;
  right: 20px;
  width: 360px;
  width: 3.6rem;
  z-index: 1000;
}
.right-bottom-pannel {
  position: absolute;
  display: inline-block;
  bottom: 10px;
  bottom: 0.1rem;
  right: 20px;
  // width: 360px;
  // width: 3.6rem;
  z-index: 999;
}
.left-top-pannel {
  position: absolute;
  display: inline-block;
  left: 350px;
  // left: 3.5rem;
  top: 20px;
  top: 0.2rem;
  z-index: 1000;
}
</style>


